import manager from '@/store/manager.js'

class Homepage {
  constructor (data) {
    this._id = data._id
    this.domain = data.domain || null
    this.logo = data.logo || null
    this.icon = data.icon || null
    this.theme = data.theme || 'theme01'
    this.contactMail = data.contactMail || null
    this.profile = {}
    if (!data.profile) data.profile = {}
    this.profile.name = data.profile.name || null
    this.profile.president = data.profile.president || null
    this.profile.create = data.profile.create || null
    this.profile.capital = data.profile.capital || null
    this.profile.license = data.profile.license || null
    this.profile.address = data.profile.address || null
    this.profile.access = data.profile.access || null
    this.profile.phone = data.profile.phone || null
    this.profile.fax = data.profile.fax || null
    this.profile.email = data.profile.email || null
    this.profile.business = data.profile.business || null
    this.profile.holiday = data.profile.holiday || null
    this.profile.latitude = data.profile.latitude || null
    this.profile.longitude = data.profile.longitude || null
    this.shops = data.shops || null
    this.vision = {}
    if (!data.vision) data.vision = {}
    this.vision.title = data.vision.title || null
    this.vision.description = data.vision.description || null
    this.services = data.services || null
    this.pages = data.pages || null
  }
  get isDuplicate () {
    return manager.homepageService.homepages.some(one => {
      return one._id !== this._id && one.domain === this.domain
    })
  }
  recommend () {
    if (!this.logo) this.logo = manager.user.logo
    if (!this.icon) this.icon = manager.user.icon
    if (!this.contactMail) this.contactMail = manager.user.email
    if (!this.profile.name) this.profile.name = manager.user.company
    if (!this.profile.license) this.profile.license = manager.user.license
    if (!this.profile.address) this.profile.address = manager.user.address
    if (!this.profile.phone) this.profile.phone = manager.user.phone
    if (!this.profile.fax) this.profile.fax = manager.user.fax
    if (!this.profile.email) this.profile.email = manager.user.email
    if (!this.vision.title) this.vision.title = '住みたい家がある'
    if (!this.vision.description) this.vision.description = '我々は、あなたの快適な暮らしをサポートいたします。'
    if (!this.services) {
      this.services = []
      this.services.push(manager.const.service['buy'])
      this.services.push(manager.const.service['rent'])
    }
  }
  toJSON () {
    return {
      _id: this._id,
      domain: this.domain,
      logo: this.logo,
      icon: this.icon,
      theme: this.theme,
      contactMail: this.contactMail,
      profile: this.profile,
      shops: this.shops,
      vision: this.vision,
      services: this.services,
      pages: this.pages
    }
  }
}

export default Homepage



