import utils from '@/tool/utils.js'
import BorrowRequest from '@/store/models/borrowRequest.js'

class MyBorrowRequestService {
  constructor () {
    this._borrowRequests = []
    this._editBorrowRequest = null
    this._count = 0
    this.page = 1
  }
  get count () {
    return this._count
  }
  set count (data) {
    this._count = parseInt(data)
  }
  get borrowRequests () {
    return this._borrowRequests
  }
  get editBorrowRequest () {
    return this._editBorrowRequest
  }
  set editBorrowRequest (request) {
    this._editBorrowRequest = new BorrowRequest(request)
  }

  init () {
    this._borrowRequests.splice(0, this._borrowRequests.length)
    this._count = 0
    this.page = 1
  }

  loadMyBorrow = (to, from, next) => {
    this.init()
    this.findBorrowRequest({}, 1).then(
      () => {
        next()
      }
    )
  }

  async findBorrowRequest (params, page) {
    let that = this
    if (page && page >= 1) page--
    await utils.restGet('/private/findBorrowRequests', {filter: params, page: page}).then(response => {
      let datas = response.datas
      that.count = response.count
      that._borrowRequests.splice(0, that._borrowRequests.length)
      if (datas) {
        datas.forEach(one => {
          that._borrowRequests.push(new BorrowRequest(one))
        })
      }
    })
  }

  loadMyBorrowRequest = (to, from, next) => {
    let that = this

    if (to.params.id === 'new') {
      that.editBorrowRequest = new BorrowRequest({})
      next()
    } else {
      utils.restGet('/private/findBorrowRequests', {'_id': to.params.id}).then(
        response => {
          if (response) {
            that.editBorrowRequest = new BorrowRequest(response.datas[0])
            next()
          }
        }
      )
    }
  }

  insertBorrowRequest (request, next) {
    utils.restPut('/private/insertBorrowRequest', request).then(savedPiece => {
      if (savedPiece) {
        next()
      }
    })
  }

  async publishBorrowRequest (request) {
    let that = this
    let isSuccess = true
    await utils.restPost('/private/publishBorrowRequest', {_id: request._id, isPublishing: true}).then(savedRequest => {
      if (!savedRequest) {
        isSuccess = false
      }
    })
    if (!isSuccess) {
      return {err: true}
    }
    that.init()
    await that.findBorrowRequest({}, 1)
  }

  async unPublishBorrowRequest (request) {
    let that = this
    let isSuccess = true
    await utils.restPost('/private/unPublishBorrowRequest', {_id: request._id, isPublishing: false}).then(savedRequest => {
      if (!savedRequest) {
        isSuccess = false
      }
    })

    if (!isSuccess) {
      return {err: true}
    }
    that.init()
    await that.findBorrowRequest({}, 1)
  }

  updateBorrowRequest (piece, next) {
    utils.restPost('/private/updateBorrowRequest', piece).then(savedPiece => {
      if (savedPiece) {
        next()
      }
    })
  }

  deleteBorrowRequest (piece, next) {
    let that = this
    utils.restPost('/private/deleteBorrowRequest', {_id: piece._id}).then(savedPiece => {
      if (savedPiece) {
        this.init()
        that.findBorrowRequest({}, 1)
        next()
      }
    })
  }
}

export default new MyBorrowRequestService()



