<template>
<div class="page-container">
  <router-view :manager="manager"/>
  <lockScreen :manager="manager"/>
  <messageDialog :manager="manager"/>
  <snackBar :manager="manager"/>
</div>
</template>

<script>
import lockScreen from '@/components/common/lockScreen'
import messageDialog from '@/components/common/messageDialog'
import snackBar from '@/components/common/snackBar'
export default {
  props: ['manager'],
  components: {
    lockScreen,
    messageDialog,
    snackBar
  }
}
</script>

<style lang="scss">
@import "~vue-material/dist/theme/engine";
@include md-register-theme("default", (
  primary: #7065a3,
  accent: #c97586
));
.primary-gradient {
  background: -webkit-linear-gradient(top, #7e71ab, #6145b6);
  background: linear-gradient(to bottom right, #7e71ab, #6145b6);
  background: -ms-linear-gradient(top, #7e71ab, #6145b6) !important;
  background: -moz-linear-gradient(center top, #7e71ab 0%, #6145b6 100%) !important;
  background: -o-linear-gradient(#7e71ab, #6145b6) !important;
}
.primary-active:before, .primary-active:after {
  border-top: 4px solid #563e9e;
}
@include md-register-theme("warm", (
  primary: #4ea07c,
  accent: #c97586
));
@include md-register-theme("light", (
  primary: #ebe48b,
  accent: #c97586
));

@include md-register-theme("theme01", (
  primary: #c1e4e9,
  accent: #007b43
));
.theme01-primary-gradient {
  background: -webkit-linear-gradient(top, #cff0f5, #90cbd3);
  background: linear-gradient(to bottom right, #cff0f5, #90cbd3);
  background: -ms-linear-gradient(top, #cff0f5, #90cbd3) !important;
  background: -moz-linear-gradient(center top, #cff0f5 0%, #90cbd3 100%) !important;
  background: -o-linear-gradient(#cff0f5, #90cbd3) !important;
}
.theme01-accent-gradient {
  background: -webkit-linear-gradient(top, #0db86b, #00723f);
  background: linear-gradient(to bottom right, #0db86b, #00723f);
  background: -ms-linear-gradient(top, #0db86b, #00723f) !important;
  background: -moz-linear-gradient(center top, #0db86b 0%, #00723f 100%) !important;
  background: -o-linear-gradient(#0db86b, #00723f) !important;
}
.theme01-accent-ribbon:before, .theme01-accent-ribbon:after {
  border-top: 4px solid #016136;
}
.theme01-accent-ribbon2:before {
  border-bottom: solid 6px #016136;
}
.theme01-accent-ribbon2:after {
  border-top: 10px solid #007b43;
}

@include md-register-theme("theme02", (
  primary: #d6ddf0,
  accent: #834e62
));
.theme02-primary-gradient {
  background: -webkit-linear-gradient(top, #d9deec, #b1bddf);
  background: linear-gradient(to bottom right, #d9deec, #b1bddf);
  background: -ms-linear-gradient(top, #d9deec, #b1bddf) !important;
  background: -moz-linear-gradient(center top, #d9deec 0%, #b1bddf 100%) !important;
  background: -o-linear-gradient(#d9deec, #b1bddf) !important;
}
.theme02-accent-gradient {
  background: -webkit-linear-gradient(top, #9c7181, #774457);
  background: linear-gradient(to bottom right, #9c7181, #774457);
  background: -ms-linear-gradient(top, #9c7181, #774457) !important;
  background: -moz-linear-gradient(center top, #9c7181 0%, #774457 100%) !important;
  background: -o-linear-gradient(#9c7181, #774457) !important;
}
.theme02-accent-ribbon:before, .theme02-accent-ribbon:after {
  border-top: 4px solid #5e2c3d;
}
.theme02-accent-ribbon2:before {
  border-bottom: solid 6px #5e2c3d;
}
.theme02-accent-ribbon2:after {
  border-top: 10px solid #834e62;
}

@include md-register-theme("theme03", (
  primary: #d6d680,
  accent: #2c4b79
));
.theme03-primary-gradient {
  background: -webkit-linear-gradient(top, #eeeeb3, #bebe5d);
  background: linear-gradient(to bottom right, #eeeeb3, #bebe5d);
  background: -ms-linear-gradient(top, #eeeeb3, #bebe5d) !important;
  background: -moz-linear-gradient(center top, #eeeeb3 0%, #bebe5d 100%) !important;
  background: -o-linear-gradient(#eeeeb3, #bebe5d) !important;
}
.theme03-accent-gradient {
  background: -webkit-linear-gradient(top, #4e6a94, #213e69);
  background: linear-gradient(to bottom right, #4e6a94, #213e69);
  background: -ms-linear-gradient(top, #4e6a94, #213e69) !important;
  background: -moz-linear-gradient(center top, #4e6a94 0%, #213e69 100%) !important;
  background: -o-linear-gradient(#4e6a94, #213e69) !important;
}
.theme03-accent-ribbon:before, .theme03-accent-ribbon:after {
  border-top: 4px solid #132c52;
}
