@import "~vue-material/dist/theme/all";
@import "~vue-material/dist/base/theme";

// @import url('https://fonts.googleapis.com/earlyaccess/notosansjapanese.css');
@import url('https://fonts.googleapis.com/earlyaccess/roundedmplus1c.css');

// common
html, body {
  // font-family: 'Noto Sans Japanese';
  font-family: 'Rounded Mplus 1c';
}
.bd-xsmall-hide, .bd-small-hide {
  .md-tabs-navigation {
    background: rgba(0, 0, 0, 0) !important;
    .md-tab-label {
      color: #fff !important;
    }
  }
}
.page-container {
  width: 100vw;
  height: 100vh;
  position: relative;
}
.md-app-content {
  padding: 0px;
  border: none;
}
.app-content-auto-height {
  width: 100%;
  height: auto;
  padding: 16px;
  overflow: hidden;
}
.app-content-fix-height {
  width: 100%;
  height: 100%;
  overflow: scroll;
}
.component-wrapper {
  padding: 16px;
}

// icon and text in one row
.icon-text {
  display: flex;
  justify-content: center;
  align-items: center;
  .md-icon {
    margin-right: 5px;
  }
}

// hover cursor
.md-with-hover {
  cursor: default !important;
}

// dialog selection
.md-select-menu {
  z-index: 10;
}

// fix tab height
.md-tabs-content {
  height: auto !important;
}

// radio click range
.md-radio-label {
  width: 100% !important;
  height: 100% !important;
  display: flex;
  align-items: center;
}

// chat
// @media (max-width: 600px) {
//   .md-app-container {
//     padding-left: 0px !important;
//   }
// }

// responsive
.bd-xsmall-show {
  display: none !important;
}
@media (max-width: 600px) {
  .bd-xsmall-hide {
    display: none !important;
  }
  .bd-xsmall-show {
    display: block !important;
  }
}
.bd-small-show {
  display: none !important;
}
@media (max-width: 944px) {
  .bd-small-hide {
    display: none !important;
  }
  .bd-small-show {
    display: block !important;
  }
}
.bd-medium-show {
  display: none !important;
}
@media (max-width: 1264px) {
  .bd-medium-hide {
    display: none !important;
  }
  .bd-medium-show {
    display: block !important;
  }
}

// google map
.pac-item {
  cursor: pointer;
}
@media (max-width: 600px) {
  .gmap {
    height: 300px !important;
  }
}
</style>



