<template>
<div>
  <vue-headful title="ブドウさん - 私の売買物件"/>
  <md-button class="md-icon-button md-fab md-raised md-accent add-piece" to="/my/sellPiece/new"><md-icon>add</md-icon></md-button>
  <div class="info-title">
    <md-content class="title-bar md-primary"></md-content><p class="md-title">私の売買物件 ({{manager.mySellPieceService.sellPieces.length}} / {{manager.mySellPieceService.count}})</p>
    <md-radio class="bd-xsmall-hide option" v-model="manager.mySellPieceService.filter.type" value="1">マンション</md-radio>
    <md-radio class="bd-xsmall-hide option" v-model="manager.mySellPieceService.filter.type" value="2">一戸建</md-radio>
  </div>
  <div class="bd-xsmall-show">
    <md-radio class="option" v-model="manager.mySellPieceService.filter.type" value="1">マンション</md-radio>
    <md-radio class="option" v-model="manager.mySellPieceService.filter.type" value="2">一戸建</md-radio>
  </div>
  <div class="md-layout md-gutter">
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100" v-for="piece in manager.mySellPieceService.sellPieces" :key="piece._id">
      <mySellPieceCard :manager="manager" :object="piece"/>
    </div>
  </div>
</div>
</template>

<script>
import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import mySellPieceCard from '@/components/shared/mySellPieceCard'
export default {
  props: ['manager'],
  components: {
    mySellPieceCard
  },
  data: () => ({
    isProcessing: false
  }),
  mounted () {
    const that = this
    that.isProcessing = false
    utils.event.$on('SCROLL_ON_BOTTOM', () => {
      if (manager.mySellPieceService.sellPieces.length < manager.mySellPieceService.count && !that.isProcessing) {
        that.isProcessing = true
        that.manager.mySellPieceService.moreSellPieces(() => {
          that.$nextTick(() => {
            that.isProcessing = false
          })
        })
      }
    })
    utils.event.$emit('SCROLL_TO_TOP')
  },
  beforeDestroy () {
    utils.event.$off('SCROLL_ON_BOTTOM')
  },
  watch: {
    'manager.mySellPieceService.filter.type' () {
      manager.mySellPieceService.loadSellPieces(null, null, null)
    }
  }
}
</script>

<style scoped lang="scss">
.add-piece {
  position: absolute;
  top: 85px;
  right: 10px;
}
.info-title {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.option {
  margin-left: 20px;
}
.title-bar {
  width: 5px;
  height: 30px;
  border-radius: 1px;
  margin-right: 10px;
}
.md-title {
  width: 240px;
  margin: 10px 10px 10px 0px;
}
.md-layout {
  margin-top: 10px;
}
.md-layout-item {
  margin-bottom: 15px;
}
</style>



