import utils from '@/tool/utils.js'

class ResourceService {
  constructor () {
    this.userResources = []
    this._count = 0
    this._page = 0
  }

  init () {
    this.userResources.splice(0, this.userResources.length)
    this.count = 0
    this.page = 0
  }

  get count () {
    return this._count
  }
  set count (data) {
    this._count = parseInt(data)
  }
  get page () {
    return this._page
  }
  set page (data) {
    this._page = parseInt(data)
  }

  preloadUserResources = (to, from, next) => {
    console.log('preloadUserResources start')
    this.init()
    this.findUserResources({}, 0).then(
      () => {
        next && next()
      }
    )
  }

  async findUserResources (params, page) {
    let that = this
    await utils.restGet('/private/findUserResources', {filter: params, page: page}).then(response => {
      console.log(response)
      if (response) {
        that.init()
        that.count = response.count
        that.userResources = response.datas
        response.datas.forEach(oneResource => {
          if (oneResource.thumbnail == null) {
            oneResource.thumbnail = utils.getIcon(oneResource.type)
          }
        })
      }
    })
  }

  insertUserResources (userResources, next) {
    let that = this
    console.log('insertUserResources')
    utils.restPut('/private/insertUserResources', userResources).then(savedData => {
      if (savedData) {
        that.preloadUserResources(null, null, next)
      }
    })
  }

  deleteUserResources (userResource, next) {
    let that = this
    utils.restPost('/private/deleteUserResources', {_id: userResource._id}).then(savedData => {
      if (savedData) {
        utils.event.$emit('SHOW_MESSAGE', {code: 'I007'}, () => {
          that.preloadUserResources(null, null, next)
        })
      }
    })
  }
}

export default new ResourceService()



