<template>
<div>
  <md-divider style="margin: 0px 10px 1px 10px;"></md-divider>
  <div class="detail-title">
    <md-content class="estate-type md-primary">{{piece.typeText}}</md-content>
    <p class="md-title bd-xsmall-hide">{{piece.name}}</p>
    <p class="md-title bd-xsmall-show"></p>
    <md-content class="estate-publish md-primary" v-if="piece.isPublishing">公開中</md-content>
    <md-content class="estate-publish md-primary" v-else>未公開</md-content>
  </div>
  <md-divider style="margin: 1px 10px 20px 10px;"></md-divider>
  <p class="md-title estate-name bd-xsmall-show">{{piece.name}}</p>

  <!-- 基本情報 -->
  <div class="info-title">
    <md-content class="title-bar md-primary"></md-content><p class="md-title">基本情報</p>
  </div>
  <div class="md-layout">
    <div class="md-layout-item md-size-100">
      <div class="detail-row">
        <md-content class="detail-label"><md-content class="detail-label-ui md-primary">アドレス</md-content></md-content>
        <p class="detail-value">{{piece.address}}</p>
      </div>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100">
      <div class="detail-row">
        <md-content class="detail-label"><md-content class="detail-label-ui md-primary">賃料</md-content></md-content>
        <p class="detail-value">{{piece.rentText}}</p>
      </div>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100">
      <div class="detail-row">
        <md-content class="detail-label"><md-content class="detail-label-ui md-primary">敷金</md-content></md-content>
        <p class="detail-value">{{piece.depositText}}</p>
      </div>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100">
      <div class="detail-row">
        <md-content class="detail-label"><md-content class="detail-label-ui md-primary">礼金</md-content></md-content>
        <p class="detail-value">{{piece.keyMoneyText}}</p>
      </div>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100">
      <div class="detail-row">
        <md-content class="detail-label"><md-content class="detail-label-ui md-primary">更新料</md-content></md-content>
        <p class="detail-value">{{piece.updateMoneyText}}</p>
      </div>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100">
      <div class="detail-row">
        <md-content class="detail-label"><md-content class="detail-label-ui md-primary">保険など</md-content></md-content>
        <p class="detail-value">{{piece.insurance}}</p>
      </div>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100">
      <div class="detail-row">
        <md-content class="detail-label"><md-content class="detail-label-ui md-primary">契約期間</md-content></md-content>
        <p class="detail-value">{{piece.contractPeroid}}</p>
      </div>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100" v-if="piece.isApartment">
      <div class="detail-row">
        <md-content class="detail-label"><md-content class="detail-label-ui md-primary">管理費</md-content></md-content>
        <p class="detail-value">{{piece.manageText}}</p>
      </div>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100">
      <div class="detail-row">
        <md-content class="detail-label"><md-content class="detail-label-ui md-primary">間取り</md-content></md-content>
        <p class="detail-value">{{piece.layoutText}}</p>
      </div>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100">
      <div class="detail-row">
        <md-content class="detail-label"><md-content class="detail-label-ui md-primary">専有面積</md-content></md-content>
        <p class="detail-value">{{piece.exclusiveAreaText}}</p>
      </div>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100">
      <div class="detail-row">
        <md-content class="detail-label"><md-content class="detail-label-ui md-primary">階層</md-content></md-content>
        <p class="detail-value">{{piece.floorStoryText}}</p>
      </div>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100">
      <div class="detail-row">
        <md-content class="detail-label"><md-content class="detail-label-ui md-primary">構造</md-content></md-content>
        <p class="detail-value">{{piece.structureText}}</p>
      </div>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100" v-if="piece.isApartment">
      <div class="detail-row">
        <md-content class="detail-label"><md-content class="detail-label-ui md-primary">方位</md-content></md-content>
        <p class="detail-value">{{piece.directionText}}</p>
      </div>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100">
      <div class="detail-row">
        <md-content class="detail-label"><md-content class="detail-label-ui md-primary">築年月(数)</md-content></md-content>
        <p class="detail-value">{{piece.builtText}}</p>
      </div>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100">
      <div class="detail-row">
        <md-content class="detail-label"><md-content class="detail-label-ui md-primary">入居可能日</md-content></md-content>
        <p class="detail-value">{{piece.residentableText}}</p>
      </div>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100">
      <div class="detail-row">
        <md-content class="detail-label"><md-content class="detail-label-ui md-primary">現況</md-content></md-content>
        <p class="detail-value">{{piece.availableText}}</p>
      </div>
    </div>
  </div>
  <!-- 取引情報 -->
  <!-- <div class="info-title">
    <md-content class="title-bar md-primary"></md-content><p class="md-title">取引情報</p>
  </div> -->
  <div class="md-layout">
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100">
      <div class="detail-row">
        <md-content class="detail-label"><md-content class="detail-label-ui md-primary">取引態様</md-content></md-content>
        <p class="detail-value">{{piece.aspectText}}</p>
      </div>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100">
      <div class="detail-row">
        <md-content class="detail-label"><md-content class="detail-label-ui md-primary">仲介手数料</md-content></md-content>
        <p class="detail-value">{{piece.margin || '-'}}</p>
      </div>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100">
      <div class="detail-row">
        <md-content class="detail-label"><md-content class="detail-label-ui md-primary">掲載日</md-content></md-content>
        <p class="detail-value">{{piece.cdateText}}</p>
      </div>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100">
      <div class="detail-row">
        <md-content class="detail-label"><md-content class="detail-label-ui md-primary">更新日</md-content></md-content>
        <p class="detail-value">{{piece.udateText}}</p>
      </div>
    </div>
  </div>

  <!-- 交通情報 -->
  <div class="info-title">
    <md-content class="title-bar md-primary"></md-content><p class="md-title">交通情報</p>
  </div>
  <div class="md-layout md-gutter traffic-row">
    <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100">
      <p class="traffic" v-if="piece.stations != null && piece.stations.length > 0">{{piece.stations[0].station}}<span v-if="piece.stations[0].walking != null"> 徒歩{{piece.stations[0].walking}}分</span></p>
      <p class="traffic" v-else> -</p>
    </div>
    <template  v-if="piece.stations && piece.stations.length > 1">
      <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100" v-for="(station, index) in piece.stations" :key="station._id" v-if="index > 0">
        <p class="traffic">{{station.station}}<span v-if="station.walking != null"> 徒歩{{station.walking}}分</span></p>
      </div>
    </template>
  </div>

  <!-- 設備 -->
  <div class="info-title" v-if="piece.isApartment">
    <md-content class="title-bar md-primary"></md-content><p class="md-title">設備</p>
  </div>
  <div class="md-layout" v-if="piece.isApartment">
    <div class="md-layout-item md-size-20 md-xsmall-size-33 facility">
      <div :class="{'facility-on': true, 'facility-off': !piece.carParking}">駐車場</div>
    </div>
    <div class="md-layout-item md-size-20 md-xsmall-size-33 facility">
      <div :class="{'facility-on': true, 'facility-off': !piece.bikeParking}">駐輪場</div>
    </div>
    <div class="md-layout-item md-size-20 md-xsmall-size-33 facility">
      <div :class="{'facility-on': true, 'facility-off': !piece.cityGas}">都市ガス</div>
    </div>
    <div class="md-layout-item md-size-20 md-xsmall-size-33 facility">
      <div :class="{'facility-on': true, 'facility-off': !piece.pet}">ペット可</div>
    </div>
    <div class="md-layout-item md-size-20 md-xsmall-size-33 facility">
      <div :class="{'facility-on': true, 'facility-off': !piece.balcony}">バルコニー</div>
    </div>
  </div>

  <!-- 備考 -->
  <div class="info-title">
    <md-content class="title-bar md-primary"></md-content><p class="md-title">備考</p>
  </div>
  <div class="md-layout">
    <div class="md-layout-item md-size-100">
      <div class="detail-row">
        <md-content class="detail-label"><md-content class="detail-label-ui md-primary">備考</md-content></md-content>
        <p class="detail-value">{{piece.comment || '-'}}</p>
      </div>
    </div>
  </div>

  <!-- メモ -->
  <div class="info-title">
    <md-content class="title-bar md-primary"></md-content><p class="md-title">メモ</p>
  </div>
  <div class="md-layout">
    <div class="md-layout-item md-size-100">
      <div class="detail-row">
        <md-content class="detail-label"><md-content class="detail-label-ui md-primary">メモ</md-content></md-content>
        <p class="detail-value">{{piece.memo || '-'}}</p>
      </div>
    </div>
  </div>

  <div class="md-layout md-gutter md-alignment-top-center">
    <div class="md-layout-item md-size-50 md-xsmall-size-100">
      <md-content class="block-title">
        物件写真
      </md-content>
      <div class="viewer-contaner">
        <photoViewer :manager="manager" :photos="piece.photos" v-if="piece.photos && piece.photos.length > 0"/>
        <div class="no-image" v-else>
          <img src="@/assets/noimage.png">
        </div>
      </div>
    </div>
    <div class="md-layout-item md-size-50 md-xsmall-size-100">
      <md-content class="block-title">
        間取り図
      </md-content>
      <div class="viewer-contaner">
        <photoViewer :manager="manager" :photos="piece.floorPlans" v-if="piece.floorPlans && piece.floorPlans.length > 0"/>
        <div class="no-image" v-else>
          <img src="@/assets/noimage.png">
        </div>
      </div>
    </div>
  </div>

  <md-content class="block-title" v-if="piece.previews && piece.previews.length > 0">
    360度ビューでお部屋を体験
  </md-content>
  <div class="md-layout md-alignment-center-center preview-container" v-if="piece.previews && piece.previews.length > 0">
    <div class="md-layout-item md-size-20 md-medium-size-25 md-small-size-33 md-xsmall-size-50" v-for="(preview, i) in piece.previewThumbnails" :key="i">
      <img class="preview-photo" :src="preview" @click="previewPiece(i)">
    </div>
  </div>

  <md-content class="block-title" v-if="piece.latitude && piece.longitude">
    位置情報
  </md-content>
  <googleMap class="gmap" :manager="manager" :config="piece" v-if="piece.latitude && piece.longitude"></googleMap>
</div>
</template>

<script>
import utils from '@/tool/utils.js'
import photoViewer from '@/components/common/photoViewer'
import googleMap from '@/components/common/googleMap'
export default {
  props: ['manager', 'piece'],
  components: {
    photoViewer,
    googleMap
  },
  methods: {
    previewPiece (index) {
      utils.event.$emit('OPEN_PIECE_DETAIL', index)
    }
  }
}
</script>

<style scoped lang="scss">
.detail-title {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  .md-title {
    padding: 0 10px;
    margin: 0;
    width: calc(100% - 260px);
  }
}
.estate-type, .estate-publish {
  margin: 0 10px;
  width: 120px;
  padding: 10px;
  border-radius: 3px;
  font-size: 16px;
  font-weight: 500;
  text-align: center;
}
.estate-name {
  width: 100%;
  text-align: center;
  background: #e8e8e8;
  padding: 10px;
}
.info-title {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.title-bar {
  width: 5px;
  height: 30px;
  border-radius: 1px;
  margin: 0 10px;
}
.md-caption {
  height: 20px;
  display: flex;
  align-items: center;
  padding: 0 10px;
  .md-icon {
    font-size: 18px !important;
    margin: 0 10px 0 0;
  }
  margin-bottom: 30px;
}
.viewer-contaner {
  width: 100%;
  border: 1px solid #e8e8e8;
  margin-bottom: 20px;
  .no-image {
    height: 300px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      max-height: 100%;
      max-width: 100%;
    }
  }
}

.detail-container {
  width: 100%;
  margin: 40px 0px;
}
.detail-row {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.detail-label {
  width: 90px;
  margin: 8px;
  display: flex;
  justify-content: flex-end;
}
.detail-label-ui {
  width: fit-content;
  font-size: 12px;
  padding: 0px 10px;
  border-radius: 20px;
  background: #e8e8e8;
}
.detail-value {
  width: calc(100% - 90px);
  border-bottom: 1px solid #e8e8e8;
  margin: 8px 0px;
  padding: 3px 10px;
}

.facility {
  width: 100%;
  display: flex;
  justify-content: center;
  margin: 5px 0;
}
.facility-on {
  width: 100px;
  text-align: center;
  border: 1px solid #888;
  background: #e8e8e8;
  padding: 2px 5px;
}
.facility-off {
  opacity: .4;
}

.traffic-row {
  padding: 0 20px;
}
.traffic {
  width: 100%;
  border-bottom: 1px solid #e8e8e8;
  margin: 8px 0px;
  padding: 3px 10px;
}

.preview-container {
  border: 1px solid #e8e8e8;
}
.preview-photo {
  cursor: pointer;
}
.preview-photo:hover {
  opacity: 0.7;
}
.block-title {
  width: 100%;
  height: 50px;
  background: #e8e8e8;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: 600;
  color: #454545;
  margin-top: 20px;
}
.gmap {
  position: relative;
  width: 100%;
  height: 400px;
}
</style>



