<template>
<div class="component-wrapper">
  <div class="info-title">
    <md-content class="title-bar md-primary"></md-content><p class="md-title">新着売買物件</p>
    <md-button class="md-accent more" @click="manager.homepageService.switchPage('sellPieces')">
      more
    </md-button>
  </div>
  <div class="md-layout md-gutter">
    <div :class="getPieceClass(index)" v-for="(piece, index) in manager.homepageService.dummySellPieces" :key="piece._id" v-if="index < parseInt(config.count)">
      <newSellPieceCard :manager="manager" :object="piece" :design="config.design"/>
    </div>
  </div>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
import newSellPieceCard from '../shared/newSellPieceCard'
export default {
  props: ['manager', 'config'],
  components: {
    newSellPieceCard
  },
  methods: {
    getPieceClass (index) {
      let classes = ''
      switch (parseInt(this.config.count)) {
        case 2:
          classes = 'md-layout-item md-size-50 md-small-size-100'
          break
        case 3:
          classes = 'md-layout-item md-size-33 md-medium-size-50 md-xsmall-size-100'
          break
        case 4:
          classes = 'md-layout-item md-size-25 md-medium-size-50 md-xsmall-size-100'
          break
        default:
          classes = 'md-layout-item md-size-33 md-medium-size-50 md-xsmall-size-100'
          break
      }
      if (parseInt(this.config.count) !== 2 && parseInt(this.config.count) !== 4) {
        if (index === 2) classes += ' bd-medium-hide'
      }

      return classes
    }
  }
}
</script>

<style scoped lang="scss">
.info-title {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  margin-bottom: 10px;
}
.title-bar {
  width: 5px;
  height: 30px;
  border-radius: 1px;
  margin-right: 10px;
}
.more {
  position: absolute;
  right: -8px;
  top: 12px;
}
.md-layout {
  min-height: 368px;
}
.md-layout-item {
  margin-bottom: 20px;
}
</style>



