<template>
<div>
  <googleMapLocator ref="locator" :manager="manager" :lat="manager.mySellPieceService.sellPiece.latitude" :lng="manager.mySellPieceService.sellPiece.longitude"></googleMapLocator>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import googleMapLocator from '@/components/common/googleMapLocator'
export default {
  props: ['manager'],
  components: {
    googleMapLocator
  },
  methods: {
    validate () {
      let result = this.$refs.locator.validate()
      if (result) {
        manager.mySellPieceService.sellPiece.latitude = result.lat
        manager.mySellPieceService.sellPiece.longitude = result.lng
      } else {
        return '入力不正です。'
      }
    }
  }
}
</script>

<style scoped lang="scss">

</style>



