<template>
<md-dialog :md-active.sync="showDialog" :md-fullscreen="fullscreen" v-if="componentName">
  <div class="close-button">
    <md-button class="md-icon-button md-accent" @click="showDialog = false">
      <md-icon>close</md-icon>
    </md-button>
  </div>
  <component :is="componentName" :manager="manager" :object="object"/>
</md-dialog>
</template>

<script>
import contactCard from '@/components/shared/contactCard'
import sellPieceCard from '@/components/shared/sellPieceCard'
import buyRequestCard from '@/components/shared/buyRequestCard'
import myBuyRequestCard from '@/components/shared/myBuyRequestCard'
import customizePageComponentEdit from '@/components/shared/customizePageComponentEdit'
import utils from '@/tool/utils.js'
export default {
  props: ['manager'],
  components: {
    contactCard,
    sellPieceCard,
    buyRequestCard,
    myBuyRequestCard,
    customizePageComponentEdit
  },
  data: () => ({
    showDialog: false,
    componentName: null,
    object: null,
    fullscreen: false
  }),
  mounted () {
    const that = this
    utils.event.$on('SHOW_DETAIL', (componentName, object, fullscreen) => {
      if (!componentName || !object) return
      that.componentName = componentName
      that.object = object
      that.showDialog = true
      that.fullscreen = fullscreen || false
    })
  },
  beforeDestroy () {
    utils.event.$off('SHOW_DETAIL')
  }
}
</script>

<style scoped lang="scss">
.md-dialog {
  min-width: 337px;
}
.close-button {
  position: absolute;
  top: 3px;
  right: -3px;
}
</style>



