<template>
<md-card md-with-hover @click.native="showRequestDetail(object)">
  <md-ripple>
    <md-card-header>
      <md-avatar>
        <img src="/static/user.jpg">
      </md-avatar>
      <div class="md-title">住友不動産株式会社</div>
      <div class="md-subhead">売買担当：</div>
    </md-card-header>

    <md-card-content>
      <div class="md-layout md-alignment-center-left">
        <div class="md-layout-item md-size-100">
          <div>{{object.generation}}0代 {{object.nationality}}人 職業：{{object.profession}}</div>
          <div>建物種別：{{manager.const.type[object.type]}} <span class="isnew_new" v-if="object.isnew=='1'">{{object.isnewText}}</span><span class="isnew_old" v-if="object.isnew=='2'">{{object.isnewText}}</span></div>
          <div>価格:{{object.estatePriceFromText}}~{{object.estatePriceToText}}</div>
          <div>路線:{{object.line}} 駅:{{object.station}} 駅から所要時間:{{object.requiredTime}}</div>
          <div>説明:{{object.comment}}</div>
        </div>
      </div>
    </md-card-content>

    <md-card-actions>
      <md-button class="md-primary md-raised" @click.stop="publishRequest(object)" v-if="!object.isPublishing">公開</md-button>
      <md-button class="md-primary md-raised" @click.stop="unPublishRequest(object)" v-if="object.isPublishing">公開取消</md-button>
      <md-button class="md-primary md-raised" @click.stop="editRequest(object)">変更</md-button>
      <md-button class="md-primary md-raised" @click.stop="deleteRequest(object)">削除</md-button>
    </md-card-actions>
  </md-ripple>
</md-card>
</template>

<script>
import utils from '@/tool/utils.js'
export default {
  props: ['manager', 'object'],
  methods: {
    publishRequest (request) {
      let that = this

      request.isPublishing = 1
      that.manager.myBuyRequestService.publishBuyRequest(request).then((err) => {
        if (err) {
          request.isPublishing = false
        } else {
          utils.event.$emit('SHOW_MESSAGE', {code: 'I002'}, () => {
            that.$router.push({name: 'myBuy'})
          })
        }
      })
    },
    unPublishRequest (request) {
      let that = this

      request.isPublishing = 1
      that.manager.myBuyRequestService.unPublishBuyRequest(request).then((err) => {
        if (err) {
          request.isPublishing = false
        } else {
          utils.event.$emit('SHOW_MESSAGE', {code: 'I002'}, () => {
            that.$router.push({name: 'myBuy'})
          })
        }
      })
    },
    editRequest (request) {
      if (request.id) this.$router.push({path: '/my/buyRequest/' + request.id})
    },
    deleteRequest (request) {
      let that = this
      if (request.id) {
        utils.event.$emit('SHOW_MESSAGE', {code: 'I003'}, () => {
          that.manager.myBuyRequestService.deleteBuyRequest(request, function () {
            utils.event.$emit('SHOW_MESSAGE', {code: 'I002'}, () => {
              that.$router.push({name: 'myBuy'})
            })
          })
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
.md-card {
  margin: 0px;
}
.md-title {
  opacity: 0.7;
  font-size: 12px;
}
.md-subhead {
  font-size: 12px;
  opacity: 1;
  color: #111;
}
.isnew_new {
  color: yellow;
  background: red;
  border: 1px solid rebeccapurple
}
.isnew_old {
  color: white;
  background: green;
  border: 1px solid greenyellow
}
</style>



