<template>
<md-app :md-theme="manager.homepageService.homepage.theme" md-waterfall md-mode="fixed">

  <md-app-toolbar :class="barClass">
    <div class="md-toolbar-row">
      <div class="md-toolbar-section-start">
        <img class="resize-picture" :src="manager.homepageService.homepage.logo" v-if="manager.homepageService.homepage.logo">
        <uploadFile class="logo-uploader" ref="logoUploader" :manager="manager" acceptType="image" type="icon" :subFolder="manager.user._id + '/resources'" :next="uploaded"/>
      </div>
      <div class="md-toolbar-section-end">
        <md-button class="md-icon-button md-primary bd-xsmall-show" @click="menuVisible = !menuVisible">
          <md-icon>menu</md-icon>
        </md-button>
        <md-tabs class="md-accent bd-xsmall-hide" :md-active-tab="manager.homepageService.page.id">
          <md-tab
            v-for="page in manager.homepageService.homepage.pages"
            :key="page.id"
            :id="page.id"
            :md-icon="page.icon"
            :md-label="page.name"
            @click="manager.homepageService.switchPage(page.id)"
          ></md-tab>
        </md-tabs>
      </div>
    </div>
  </md-app-toolbar>

  <md-app-drawer :md-active.sync="menuVisible">
    <md-toolbar class="md-transparent" md-elevation="0">{{manager.homepageService.homepage.profile.name}}</md-toolbar>
    <md-list>
      <md-list-item v-for="page in manager.homepageService.homepage.pages" :key="page.id" @click="manager.homepageService.switchPage(page.id)">
        <md-icon>{{page.icon}}</md-icon>
        <span class="md-list-item-text">{{page.name}}</span>
      </md-list-item>
    </md-list>
  </md-app-drawer>

  <md-app-content>
    <div class="app-content-auto-height" v-if="manager.homepageService.page">
      <component
        v-for="(config, index) in manager.homepageService.page.topComponents"
        :key="'top-' + index"
        :is="config.id"
        :manager="manager"
        :config="config"
      ></component>
      <template v-if="manager.homepageService.page.leftComponents.length > 0 || manager.homepageService.page.rightComponents.length > 0">
        <div class="md-layout md-gutter">
          <div :class="leftClass">
            <component
              v-for="(config, index) in manager.homepageService.page.leftComponents"
              :key="'left-' + index"
              :is="config.id"
              :manager="manager"
              :config="config"
            ></component>
          </div>
          <div :class="rightClass">
            <component
              v-for="(config, index) in manager.homepageService.page.rightComponents"
              :key="'right-' + index"
              :is="config.id"
              :manager="manager"
              :config="config"
            ></component>
          </div>
        </div>
      </template>
      <component
        v-for="(config, index) in manager.homepageService.page.bottomComponents"
        :key="'bottom-' + index"
        :is="config.id"
        :manager="manager"
        :config="config"
      ></component>
    </div>
  </md-app-content>

</md-app>
</template>

<script>
// import utils from '@/tool/utils.js'
// import manager from '@/store/manager.js'
import cover from './component/cover'
import carousel from './component/carousel'
import newSellPieces from './component/newSellPieces'
import newRentPieces from './component/newRentPieces'
import services from './component/services'
import infoCenter from './component/infoCenter'
import infos from './component/infos'
import shopInfo from './component/shopInfo'
import navigation from './component/navigation'
import sellPieces from './component/sellPieces'
import rentPieces from './component/rentPieces'
import companyInfo from './component/companyInfo'
import companyAccess from './component/companyAccess'
import contactUs from './component/contactUs'
import topics from './component/topics'
import uploadFile from '@/components/common/uploadFile'
export default {
  props: ['manager'],
  components: {
    cover,
    carousel,
    newSellPieces,
    newRentPieces,
    services,
    infoCenter,
    infos,
    shopInfo,
    navigation,
    sellPieces,
    rentPieces,
    companyInfo,
    companyAccess,
    contactUs,
    topics,
    uploadFile
  },
  data: () => ({
    menuVisible: false
  }),
  computed: {
    barClass () {
      return 'md-dense ' + this.manager.homepageService.homepage.theme + '-primary-gradient'
    },
    leftClass () {
      return 'md-layout-item md-size-' + this.manager.homepageService.page.left + ' md-small-size-100'
    },
    rightClass () {
      return 'md-layout-item md-size-' + this.manager.homepageService.page.right + ' md-small-size-100'
    }
  },
  methods: {
    uploaded (photo) {
      this.manager.homepageService.homepage.logo = photo.file
      this.manager.homepageService.homepage.icon = photo.thumbnail
    }
  }
}
</script>

<style scoped lang="scss">
.md-app {
  min-height: 400px;
  border: 1px solid #e8e8e8;
}
.md-drawer {
  width: 230px;
  max-width: calc(100vw - 125px);
}
.resize-picture {
  width: auto;
  height: 72px;
  padding: 4px;
}
.app-content-auto-height {
  padding: 0px;
}
</style>



