import moment from 'moment'
import manager from '@/store/manager.js'

class ChatMessage {
  constructor (data) {
    this._id = data._id
    this.user = data.user
    this.avatar = '/static/user.jpg'
    this._chatTime = null
    this.chatTime = data.chatTime
    this._texts = []
    this.texts = data.text
  }
  get chatTime () {
    return this._chatTime
  }
  set chatTime (data) {
    this._chatTime = moment(data).format('YYYY/MM/DD HH:mm:ss')
  }
  get texts () {
    return this._texts
  }
  set texts (data) {
    let datas = data.split('\n')
    for (let index in datas) {
      let html = datas[index]
      html = '<p class="chat-message-p"><span>' + html + '</span></p>'
      let re = /\[.*\]/g
      while (re.test(html)) {
        let beginOffset = html.indexOf('[')
        let endOffset = html.indexOf(']')
        let emotion = html.substr(beginOffset + 1, endOffset - beginOffset - 1)
        if (manager.const.emotion[emotion]) {
          emotion = '</span><img src="' + manager.const.emotion[emotion] + '"><span>'
          html = html.substr(0, beginOffset) + emotion + html.substr(endOffset + 1, html.length - endOffset - 1)
        }
      }

      this._texts += html
    }
  }
}

export default ChatMessage



