import utils from '@/tool/utils.js'
import BorrowRequest from '@/store/models/borrowRequest.js'

class EstateBorrowRequestService {
  constructor () {
    this._borrowRequests = []
    this._count = 0
    this.page = 0

    this.searchCondition = {
      line: '',
      station: '',
      minute: '',
      isNew: '',
      type: '',
      min: '',
      max: '',
      releday: ''
    }
  }
  get borrowRequests () {
    return this._borrowRequests
  }
  set borrowRequests (pieces) {
    pieces.forEach(one => {
      this._borrowRequests.push(new BorrowRequest(one))
    })
  }
  get count () {
    return this._count
  }
  set count (data) {
    this._count = parseInt(data)
  }

  init () {
    this._borrowRequests.splice(0, this._borrowRequests.length)
    this._count = 0
    this.page = 0
  }

  loadBorrowRequest = (to, from, next) => {
    let that = this
    utils.restGet('/public/findBorrowRequests', {}).then(
      response => {
        if (response) {
          that.init()
          that.count = response.count
          that.borrowRequests = response.datas
          next()
        }
      }
    )
  }

  moreBorrowRequest () {
    let that = this
    utils.restGet('/public/findBorrowRequests', {filter: that.searchCondition, page: that.page + 1}).then(
      response => {
        if (response) {
          that.count = response.count
          that.borrowRequests = response.datas
          that.page += 1
        }
      }
    )
  }
}

export default new EstateBorrowRequestService()



