<template>
<div class="md-layout md-gutter">
  <div class="md-layout-item md-size-50 md-xsmall-size-100">
    <template01 :manager="manager"/>
    <md-button class="md-accent" @click="useTemplate('template01')">このテンプレートを使用</md-button>
    <div class="detail">
      <p class="md-body-2">一つの店舗しかお持ちでない、基本的な業務しか行なっていない小規模会社にオススメします。</p>
      <p class="md-body-1">一般客が一番注目される『物件情報』、『店舗情報』をリッチに表現しております。加えて一般客とやりとりを担う『お知らせ』、『お問合せ』も搭載してあります。『サービス』は他の会社と大した差はないので、テンプレートのデフォルト設定でもOKです。</p>
    </div>
  </div>
  <div class="md-layout-item md-size-50 md-xsmall-size-100">
    <template02 :manager="manager"/>
    <md-button class="md-accent" @click="useTemplate('template02')">このテンプレートを使用</md-button>
    <div class="detail">
      <p class="md-body-2">複数店舗をお持ちの中大規模会社にオススメします。</p>
      <p class="md-body-1">『物件情報』、『お知らせ』、『お問合せ』を始め、『査定』機能も付随してあります。複数の店舗情報を一ページに詰めて、お客様がアクセスしやすくしたデザインを採用しております。充実したサービスも一つのページに綺麗に表しました。</p>
    </div>
  </div>
</div>
</template>

<script>
import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import template01 from './template/template01'
import template02 from './template/template02'
export default {
  props: ['manager'],
  components: {
    template01,
    template02
  },
  methods: {
    useTemplate (template) {
      let isDomainValid = true
      if (!manager.homepageService.homepage.domain) isDomainValid = false
      else if (manager.homepageService.homepage.domain.indexOf('http') === 0) isDomainValid = false
      else if (manager.homepageService.homepage.domain.length > 50) isDomainValid = false
      else if (manager.homepageService.homepage.isDuplicate) isDomainValid = false
      if (!isDomainValid) {
        utils.event.$emit('SHOW_MESSAGE', {code: 'B011'})
        return
      }
      if (manager.homepageService.homepage._id) {
        utils.event.$emit('SHOW_MESSAGE', {code: 'I010'}, () => {
          manager.homepageService.homepage.pages = manager.const.template[template]
          manager.homepageService.updateHomepage(() => {
            manager.homepageService.page = manager.homepageService.homepage.pages[0]
            manager.homepageService.showTemplate = false
          })
        })
      } else {
        manager.homepageService.homepage.pages = manager.const.template[template]
        manager.homepageService.insertHomepage(() => {
          manager.homepageService.showTemplate = false
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
.md-button {
  width: 100%;
}
.detail {
  width: 100%;
  padding: 0 16px;
  margin-bottom: 16px;
  p {
    margin: 0;
  }
}
</style>



