import utils from '@/tool/utils.js'
import consts from './consts'
import user from './user.js'

import socketService from './services/socketService.js'
import chatService from './services/chatService.js'

import estateBuyRequestService from './services/estateBuyRequestService.js'
import estateSellPieceService from './services/estateSellPieceService.js'
import estateRentPieceService from './services/estateRentPieceService.js'
import estateBorrowRequestService from './services/estateBorrowRequestService.js'

import mySellPieceService from './services/mySellPieceService.js'
import myBuyRequestService from './services/myBuyRequestService.js'
import myRentPieceService from './services/myRentPieceService.js'
import myBorrowRequestService from './services/myBorrowRequestService.js'

import homepageService from './services/homepageService.js'
import resourceService from './services/resourceService.js'

class Manager {
  constructor () {
    const that = this

    that.const = consts
    that.user = user

    that.socketService = socketService
    that.chatService = chatService

    that.estateBuyRequestService = estateBuyRequestService
    that.estateSellPieceService = estateSellPieceService
    that.estateRentPieceService = estateRentPieceService
    that.estateBorrowRequestService = estateBorrowRequestService

    that.mySellPieceService = mySellPieceService
    that.myBuyRequestService = myBuyRequestService
    that.myRentPieceService = myRentPieceService
    that.myBorrowRequestService = myBorrowRequestService

    that.homepageService = homepageService
    that.resourceService = resourceService

    that.locker = 'unlock'
    utils.event.$on('LOCK_SCREEN', (locker) => {
      that.locker = locker
    })
  }
  login (data, next) {
    const that = this
    that.user.login(data.user)
    // that.socketService.online(next)
    if (next) next()
  }
  anonymousLogin () {
    this.user.login({
      _id: '匿名',
      role: '0'
    })
  }
  logout () {
    const that = this
    that.user.logout()
    // that.socketService.offline()
  }
  checkAuth (route) {
    const that = this
    if (!that.user || !that.user.isLogin) {
      if (route) {
        utils.router.push({name: 'loading', query: {path: route.fullPath}})
        return false
      } else {
        utils.router.push({name: 'loading'})
        return false
      }
    } else {
      if (this.const.authority[this.user.role].indexOf(route.name) < 0) {
        if (this.user.isOrdinary) {
          utils.event.$emit('SHOW_SNACKBAR', 'I001', 4000, () => {
            utils.router.push({name: 'apply'})
          })
          return false
        } else {
          utils.event.$emit('SHOW_SNACKBAR', 'I005', 4000, () => {
            utils.router.push({name: 'login', query: {path: route.fullPath}})
          })
          return false
        }
      }
    }
    return true
  }
}

export default new Manager()



