<template>
<div class="app-content-auto-height" v-if="manager.myRentPieceService.rentPiece">
  <vue-headful title="ブドウさん - 物件詳細"/>
  <md-button class="md-icon-button md-fab md-raised md-accent share" @click="share"><md-icon>share</md-icon></md-button>
  <rentPieceDetailInfo :manager="manager" :piece="manager.myRentPieceService.rentPiece"/>
</div>
</template>

<script>
import manager from '@/store/manager.js'
import utils from '@/tool/utils.js'
import rentPieceDetailInfo from './rentPieceDetailInfo'
export default {
  props: ['manager'],
  components: {
    rentPieceDetailInfo
  },
  mounted () {
    utils.event.$on('OPEN_PIECE_DETAIL', (index) => {
      let url = location.protocol + '//' + location.host + '/my/rentPiecePreview/' + manager.myRentPieceService.rentPiece._id + '/' + index
      utils.openInNewTab(url)
    })
  },
  beforeDestroy () {
    utils.event.$off('OPEN_PIECE_DETAIL')
    manager.myRentPieceService.rentPiece = null
  },
  methods: {
    share () {
      if (manager.myRentPieceService.rentPiece.isPublishing) {
        utils.copyToPasteboard(location.protocol + '//' + location.host + '/rentPieceDetail/' + manager.myRentPieceService.rentPiece._id)
        utils.event.$emit('SHOW_MESSAGE', {code: 'I008'})
      } else {
        utils.event.$emit('SHOW_MESSAGE', {code: 'I009'}, () => {
          manager.myRentPieceService.publishRentPiece(manager.myRentPieceService.rentPiece, () => {
            manager.myRentPieceService.rentPiece.isPublishing = true
            utils.event.$emit('SHOW_MESSAGE', {code: 'I008'}, () => {
              utils.copyToPasteboard(location.protocol + '//' + location.host + '/rentPieceDetail/' + manager.myRentPieceService.rentPiece._id)
            })
          })
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
.share {
  position: absolute;
  top: 146px;
  right: 28px;
}
</style>



