<template>
<md-card class="md-elevation-10">
  <md-card-header>
    <md-avatar>
      <img :src="manager.user.avatar" v-if="object.isExam">
      <img :src="object.avatar" v-else>
    </md-avatar>
    <div class="md-title" v-if="object.isExam">{{manager.user.name}}</div>
    <div class="md-title" v-else>{{object.name}}</div>
    <div class="md-subhead" v-if="object.isExam">{{manager.user.position}}</div>
    <div class="md-subhead" v-else>{{object.position}}</div>
  </md-card-header>

  <md-card-content>
    <div class="contact-detail-logo">
      <img :src="manager.user._logo" v-if="object.isExam && manager.user._logo">
      <img :src="object._logo" v-else-if="!object.isExam && object._logo">
    </div>
    <div class="is-mediator" v-if="object.isExam">審査中</div>
    <div class="is-mediator" v-else-if="object.isMediator">認定業者</div>
    <div class="contact-detail-comment md-elevation-5">
      <p v-if="object.isExam && manager.user.comment">{{manager.user.comment}}</p>
      <p v-else-if="object.isExam && !manager.user.comment">よろしくお願いします。</p>
      <p v-else-if="!object.isExam && object.comment">{{object.comment}}</p>
      <p v-else>よろしくお願いします。</p>
    </div>
    <div class="contact-detail-row">
      <div>
        <md-icon class="md-primary">business</md-icon>
      </div>
      <div class="contact-text" v-if="object.company">
        {{object.company}}
      </div>
      <div class="contact-text" v-else>
        不明
      </div>
    </div>
    <div class="contact-detail-row">
      <div>
        <md-icon class="md-primary">call</md-icon>
      </div>
      <div class="contact-text" v-if="object.phone">
        {{object.phone}}
      </div>
      <div class="contact-text" v-else>
        不明
      </div>
    </div>
    <div class="contact-detail-row" v-if="object.fax">
      <div>
        <md-icon class="md-primary">print</md-icon>
      </div>
      <div class="contact-text">
        {{object.fax}}
      </div>
    </div>
    <div class="contact-detail-row">
      <div>
        <md-icon class="md-primary">email</md-icon>
      </div>
      <div class="contact-text" v-if="object.email">
        {{object.email}}
      </div>
      <div class="contact-text" v-else>
        不明
      </div>
    </div>
    <div class="contact-detail-row" v-if="object.homepage">
      <div>
        <md-icon class="md-primary">home</md-icon>
      </div>
      <div class="contact-text address-text">
        <a class="homepage" @click="openHomepage">{{object.homepage}}</a>
      </div>
    </div>
    <div class="contact-detail-row">
      <div>
        <md-icon class="md-primary">room</md-icon>
      </div>
      <div class="contact-text address-text" v-if="object.address">
        {{object.address}}
      </div>
      <div class="contact-text address-text" v-else>
        不明
      </div>
    </div>
  </md-card-content>

  <md-card-actions>
    <md-content class="md-accent license-badge md-elevation-1" v-if="object.license">
      {{object.license}}
    </md-content>
    <!-- <md-button class="md-icon-button md-accent">
      <md-icon>assignment_ind</md-icon>
    </md-button> -->
    <!-- <md-button class="md-icon-button md-accent" to="/chat" v-if="object._id !== manager.user._id">
      <md-icon>forum</md-icon>
    </md-button> -->
  </md-card-actions>
</md-card>
</template>

<script>
import utils from '@/tool/utils.js'
export default {
  props: ['manager', 'object'],
  methods: {
    openHomepage () {
      if (this.object.homepage) {
        let url = this.object.homepage
        if (url.indexOf('http') !== 0) {
          url = 'http://' + url
        }
        utils.openInNewTab(url)
      }
    }
  }
}
</script>

<style scoped lang="scss">
.md-card {
  min-width: 337px;
  max-width: 420px;
  margin: 0px;
}
.md-card-content {
  position: relative;
  padding-bottom: 0px;
}
.md-subhead {
  font-size: 12px;
  height: 20px;
}
.contact-detail-logo {
  position: absolute;
  top: -10px;
  left: 36px;
  width: 120px;
  height: 120px;
  opacity: 0.3;
  display: flex;
  justify-content: center;
  align-items: center;
  img {
    max-width: 120px;
    max-height: 120px;
  }
}
.is-mediator {
  position: absolute;
  top: 75px;
  left: 36px;
  width: 120px;
  height: 25px;
  background-color: rgba(255, 255, 255, .1);
  display: flex;
  justify-content: center;
  align-items: center;
  color: #dc66a0;
  font-weight: 600;
}
.company-logo {
  width: 100%;
  height: auto;
}
.contact-detail-comment {
  opacity: 0.7;
  position: absolute;
  top: -15px;
  padding: 8px;
  width: calc(100% - 205px);
  background: #e8e8e8;
  border-radius: 2px;
  p {
    margin: 0;
    font-size: 11px;
  }
  &::before {
    position: absolute;
    top: -8px;
    left: 15px;
    content: '';
    width: 0;
    height: 0;
    font-weight: 100;
    border-left: solid 5px transparent;
    border-right: solid 5px transparent;
    border-bottom: solid 8px #e8e8e8;
  }
}
.contact-detail-row {
  width: 100%;
  display: flex;
  align-items: center;
  font-size: 11px;
  margin-top: 8px;
  justify-content: flex-end;
  .md-icon {
    font-size: 16px !important;
  }
}
.contact-text {
  min-width: 140px;
  min-height: 24px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border-bottom: 1px solid #e8e8e8;
}
.homepage {
  cursor: pointer;
}
.address-text {
  width: 100%;
}
.md-card-actions {
  height: 56px;
}
.license-badge {
  position: absolute;
  bottom: 15px;
  left: 20px;
  padding: 5px 10px;
  border-radius: 30px;
}
</style>



