<template>
<div class="app-content-fix-height">
  <vue-headful title="ブドウさん - 物件内覧"/>
  <previewContent :manager="manager" :piece="manager.estateRentPieceService.rentPiece"/>
</div>
</template>

<script>
import manager from '@/store/manager.js'
// import utils from '@/tool/utils.js'
import previewContent from './previewContent'
export default {
  props: ['manager'],
  components: {
    previewContent
  },
  beforeDestroy () {
    manager.estateRentPieceService.rentPiece = null
  }
}
</script>

<style scoped lang="scss">
.app-content-fix-height {
  overflow: hidden;
}
</style>



