<template>
<md-dialog :md-active.sync="showDialog">
  <div class="close-button">
    <md-button class="md-icon-button md-accent" @click="showDialog = false">
      <md-icon>close</md-icon>
    </md-button>
  </div>

  <md-dialog-title>パスワード変更</md-dialog-title>

  <div class="input-area">
    <md-field md-clearable :class="{'md-invalid': !isOldPasswordValid}" :md-toggle-password="false">
      <label>旧パスワード</label>
      <md-input ref="register_password" v-model="oldPassword" type="password"></md-input>
      <span class="md-error">不正なパスワード</span>
    </md-field>
    <md-field md-clearable :class="{'md-invalid': !isNewPasswordValid}" :md-toggle-password="false">
      <label>新パスワード</label>
      <md-input ref="register_password" v-model="newPassword" type="password"></md-input>
      <span class="md-error" v-if="newPassword !== manager.user.password">英数字6〜20文字</span>
      <span class="md-error" v-else>旧パスワードと違うようにしてください。</span>
    </md-field>
    <md-field md-clearable :class="{'md-invalid': !isRetypeValid}" :md-toggle-password="false">
      <label>パスワード再入力</label>
      <md-input ref="retype" v-model="retype" type="password" @keyup.enter.native="register()"></md-input>
      <span class="md-error" v-if="retype !== manager.user.password && isNewPasswordValid && retype === newPassword">英数字6〜20文字</span>
      <span class="md-error" v-else-if="retype === manager.user.password">旧パスワードと違うようにしてください。</span>
      <span class="md-error" v-else-if="retype !== newPassword">新パスワードと同じようにしてください。</span>
    </md-field>
  </div>

  <md-dialog-actions>
    <md-button class="md-primary" @click="showDialog = false">取消</md-button>
    <md-button class="md-primary" @click="save()" :disabled="!oldPassword || !newPassword || !retype || !isOldPasswordValid || !isNewPasswordValid || !isRetypeValid">確定</md-button>
  </md-dialog-actions>
</md-dialog>
</template>

<script>
import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
export default {
  props: ['manager'],
  data: () => ({
    showDialog: false,
    oldPassword: null,
    newPassword: null,
    retype: null,
    isOldPasswordValid: true,
    isNewPasswordValid: true,
    isRetypeValid: true
  }),
  watch: {
    oldPassword () {
      this.check()
    },
    newPassword () {
      this.check()
    },
    retype () {
      this.check()
    }
  },
  methods: {
    show () {
      this.oldPassword = null
      this.newPassword = null
      this.retype = null
      this.isOldPasswordValid = true
      this.isNewPasswordValid = true
      this.isRetypeValid = true
      this.showDialog = true
    },
    check () {
      if (this.oldPassword !== null && (this.oldPassword.length < 6 || this.oldPassword.length > 20)) {
        this.isOldPasswordValid = false
      } else if (this.oldPassword !== null && this.oldPassword.match(/[^A-Za-z0-9]+/)) {
        this.isOldPasswordValid = false
      } else if (this.oldPassword !== manager.user.password) {
        this.isOldPasswordValid = false
      } else {
        this.isOldPasswordValid = true
      }
      if (this.newPassword !== null && (this.newPassword.length < 6 || this.newPassword.length > 20)) {
        this.isNewPasswordValid = false
      } else if (this.newPassword !== null && this.newPassword.match(/[^A-Za-z0-9]+/)) {
        this.isNewPasswordValid = false
      } else if (this.newPassword === manager.user.password) {
        this.isNewPasswordValid = false
      } else {
        this.isNewPasswordValid = true
      }
      if (this.retype !== null && (this.retype.length < 6 || this.retype.length > 20)) {
        this.isRetypeValid = false
      } else if (this.retype !== null && this.retype.match(/[^A-Za-z0-9]+/)) {
        this.isRetypeValid = false
      } else if (this.retype === manager.user.password) {
        this.isRetypeValid = false
      } else if (this.retype && this.newPassword && (this.newPassword !== this.retype)) {
        this.isRetypeValid = false
      } else {
        this.isRetypeValid = true
      }
      return this.isOldPasswordValid && this.isNewPasswordValid && this.isRetypeValid
    },
    save () {
      this.showDialog = false
      let user = {
        _id: manager.user._id,
        password: this.newPassword
      }
      utils.restPost('/private/updateUser', user).then(
        response => {
          if (response) {
            manager.user.login(response)
            utils.event.$emit('SHOW_MESSAGE', {code: 'I002'})
          }
        }
      )
    }
  }
}
</script>

<style scoped lang="scss">
.close-button {
  position: absolute;
  top: 18px;
  right: 0px;
}
.input-area {
  padding: 20px;
}
</style>



