<template>
<div class="map-container">
  <p class="md-display-1">アクセス</p>
  <googleMap class="gmap" :manager="manager" :config="manager.homepageService.homepage.profile" v-if="manager.homepageService.homepage.profile.latitude && manager.homepageService.homepage.profile.longitude"></googleMap>
  <div class="gmap-none" v-else>
    <span>会社の場所が特定できていません。</span>
  </div>
  <md-button md-theme="default" class="md-fab md-mini md-plain locator md-primary" @click="editMap = true">
    <md-icon>room</md-icon>
  </md-button>

  <md-dialog md-theme="default" :md-active.sync="editMap">
    <div class="map-editor">
      <googleMapLocator ref="locator" :manager="manager" :lat="manager.homepageService.homepage.profile.latitude" :lng="manager.homepageService.homepage.profile.longitude" :prevent="false"></googleMapLocator>
    </div>
    <md-dialog-actions>
      <md-button class="md-primary" @click="editMap = false">キャンセル</md-button>
      <md-button class="md-primary" @click="setLocation">確定</md-button>
    </md-dialog-actions>
  </md-dialog>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
import googleMap from '@/components/common/googleMap'
import googleMapLocator from '@/components/common/googleMapLocator'
export default {
  props: ['manager', 'config'],
  components: {
    googleMap,
    googleMapLocator
  },
  data: () => ({
    editMap: false
  }),
  methods: {
    setLocation () {
      let result = this.$refs.locator.validate()
      if (result) {
        this.manager.homepageService.homepage.profile.latitude = result.lat
        this.manager.homepageService.homepage.profile.longitude = result.lng
      }
      this.editMap = false
    }
  }
}
</script>

<style scoped lang="scss">
.map-container {
  position: relative;
}
.md-display-1 {
  text-align: center;
}
.gmap {
  position: relative;
  width: 100%;
  height: 400px;
}
.gmap-none {
  width: 100%;
  height: 200px;
  background: #e8e8e8;
  display: flex;
  align-items: center;
  padding: 16px;
}
.locator {
  position: absolute;
  top: 4px;
  right: 22px;
}
.map-editor {
  padding: 16px;
  height: 100%;
  overflow: auto;
}
</style>



