import moment from 'moment'

class Info {
  constructor (data) {
    this._id = data._id
    this.user = data.user
    this.type = data.type
    this.text = data.text
    this._idate = null
    if (data.idate) this.idate = data.idate
  }
  get idate () {
    return this._idate ? this._idate.valueOf() : null
  }
  get idateText () {
    return this._idate != null ? this._idate.format('YYYY年M月D日') : '-'
  }
  set idate (data) {
    if (data) this._idate = moment(data)
    else this._idate = null
  }
}

export default Info



