<template>
<div class="app-content-auto-height" v-if="manager.mySellPieceService.sellPiece">
  <vue-headful title="ブドウさん - 物件詳細"/>
  <md-button class="md-icon-button md-fab md-raised md-accent share" @click="share"><md-icon>share</md-icon></md-button>
  <sellPieceDetailInfo :manager="manager" :piece="manager.mySellPieceService.sellPiece"/>
</div>
</template>

<script>
import manager from '@/store/manager.js'
import utils from '@/tool/utils.js'
import sellPieceDetailInfo from './sellPieceDetailInfo'
export default {
  props: ['manager'],
  components: {
    sellPieceDetailInfo
  },
  mounted () {
    utils.event.$on('OPEN_PIECE_DETAIL', (index) => {
      let url = location.protocol + '//' + location.host + '/my/sellPiecePreview/' + manager.mySellPieceService.sellPiece._id + '/' + index
      utils.openInNewTab(url)
    })
  },
  beforeDestroy () {
    utils.event.$off('OPEN_PIECE_DETAIL')
    manager.mySellPieceService.sellPiece = null
  },
  methods: {
    share () {
      if (manager.mySellPieceService.sellPiece.isPublishing) {
        utils.copyToPasteboard(location.protocol + '//' + location.host + '/sellPieceDetail/' + manager.mySellPieceService.sellPiece._id)
        utils.event.$emit('SHOW_MESSAGE', {code: 'I008'})
      } else {
        utils.event.$emit('SHOW_MESSAGE', {code: 'I009'}, () => {
          manager.mySellPieceService.publishSellPiece(manager.mySellPieceService.sellPiece, () => {
            manager.mySellPieceService.sellPiece.isPublishing = true
            utils.event.$emit('SHOW_MESSAGE', {code: 'I008'}, () => {
              utils.copyToPasteboard(location.protocol + '//' + location.host + '/sellPieceDetail/' + manager.mySellPieceService.sellPiece._id)
            })
          })
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
.share {
  position: absolute;
  top: 146px;
  right: 28px;
}
</style>



