import utils from '@/tool/utils.js'
import BuyRequest from '@/store/models/buyRequest.js'

class EstateBuyRequestService {
  constructor () {
    this._buyRequests = []
    this._count = 0
    this.page = 0

    this.searchCondition = {
      line: '',
      station: '',
      minute: '',
      isNew: '',
      type: '',
      min: '',
      max: '',
      releday: ''
    }
  }
  get buyRequests () {
    return this._buyRequests
  }
  set buyRequests (pieces) {
    if (pieces) {
      pieces.forEach(one => {
        // 重複を削除
        this._buyRequests.filter(buyRequest => {
          return buyRequest._id === one._id
        })
        // リストに追加
        this._buyRequests.push(new BuyRequest(one))
      })
    }
  }
  get count () {
    return this._count
  }
  set count (data) {
    this._count = parseInt(data)
  }

  init () {
    this._buyRequests.splice(0, this._buyRequests.length)
    this.count = 0
    this.page = 0
  }

  loadBuyRequest = (to, from, next) => {
    let that = this

    utils.restGet('/public/findBuyRequests', {}).then(
      response => {
        if (response) {
          that.init()
          that.count = response.count
          that.buyRequests = response.datas
          next()
        }
      }
    )
  }

  moreBuyRequest () {
    let that = this
    utils.restGet('/public/findBuyRequests', {filter: that.searchCondition, page: that.page + 1}).then(
      response => {
        if (response) {
          that.count = response.count
          that.buyRequests = response.datas
          that.page += 1
        }
      }
    )
  }
}

export default new EstateBuyRequestService()



