<template>
<md-card md-with-hover>
  <md-card-media class="ribbon-box">
    <div class="ribbon-area" v-if="design === '1'">
      <span :class="ribbonClass">New!</span>
    </div>
    <md-content :class="ribbon2Class" v-else-if="design === '2'">
      <span>New!</span>
    </md-content>
    <img class="piece-thumbnail" :src="object.photoThumbnails[0]" v-if="object.photoThumbnails && object.photoThumbnails.length > 0">
  </md-card-media>

  <md-card-header>
    <div class="md-title">{{object.name}}</div>
    <div class="md-subhead" v-if="object.stations && object.stations.length > 0">
      {{object.stations[0].station}} 徒歩{{object.stations[0].walking}}分
    </div>
    <div class="md-subhead" v-else></div>
  </md-card-header>

  <md-card-content>
    <md-content class="badge" @click="tooltipActive1 = !tooltipActive1">
      {{object.typeTextAb}}
      <md-tooltip md-direction="top" :md-active.sync="tooltipActive1">{{object.typeText}}</md-tooltip>
    </md-content>
    <md-content class="badge" @click="tooltipActive2 = !tooltipActive2">
      {{object.rentText}}
      <md-tooltip md-direction="top" :md-active.sync="tooltipActive2">{{object.rentText}}</md-tooltip>
    </md-content>
    <md-content :class="{'badge': true, 'muted': !object.deposit}" @click="tooltipActive3 = !tooltipActive3">
      敷
      <md-tooltip md-direction="top" :md-active.sync="tooltipActive3">{{object.depositText}}</md-tooltip>
    </md-content>
    <md-content :class="{'badge': true, 'muted': !object.keyMoney}" @click="tooltipActive4 = !tooltipActive4">
      礼
      <md-tooltip md-direction="top" :md-active.sync="tooltipActive4">{{object.keyMoneyText}}</md-tooltip>
    </md-content>
    <md-content class="badge" v-if="object.layout" @click="tooltipActive5 = !tooltipActive5">
      {{object.layoutText}}
      <md-tooltip md-direction="top" :md-active.sync="tooltipActive5">{{object.layoutText}}</md-tooltip>
    </md-content>
    <md-content class="badge muted" v-else @click="tooltipActive5 = !tooltipActive5">
      ー
      <md-tooltip md-direction="top" :md-active.sync="tooltipActive5">間取り</md-tooltip>
    </md-content>
    <md-content class="badge" v-if="object.exclusiveArea" @click="tooltipActive6 = !tooltipActive6">
      {{object.exclusiveAreaText}}
      <md-tooltip md-direction="top" :md-active.sync="tooltipActive6">専有：{{object.exclusiveAreaText}}</md-tooltip>
    </md-content>
    <md-content class="badge muted" v-else @click="tooltipActive6 = !tooltipActive6">
      ー
      <md-tooltip md-direction="top" :md-active.sync="tooltipActive6">専有面積</md-tooltip>
    </md-content>
  </md-card-content>
</md-card>
</template>

<script>
// import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
export default {
  props: ['manager', 'object', 'design'],
  data: () => ({
    tooltipActive1: false,
    tooltipActive2: false,
    tooltipActive3: false,
    tooltipActive4: false,
    tooltipActive5: false,
    tooltipActive6: false
  }),
  computed: {
    ribbonClass () {
      return 'ribbon ' + manager.homepageService.homepage.theme + '-accent-gradient ' + manager.homepageService.homepage.theme + '-accent-ribbon'
    },
    ribbon2Class () {
      return 'md-accent ribbon2 ' + manager.homepageService.homepage.theme + '-accent-ribbon2'
    }
  }
}
</script>

<style scoped lang="scss">
.md-card {
  margin: 0px;
  height: 100%;
}

.ribbon-box {
  position: relative;
  box-sizing: border-box;
}
.ribbon-area {
  position: absolute;
  top: -6px;
  right: -6px;
  width: 89px;
  height: 91px;
  overflow: hidden;
  pointer-events: none;
}
.ribbon {
  display: inline-block;
  position: absolute;
  padding: 7px 0;
  left: -23px;
  top: 22px;
  width: 160px;
  text-align: center;
  font-size: 18px;
  font-weight: 600;
  line-height: 16px;
  color: #fff;
  letter-spacing: 0.05em;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}
.ribbon:before, .ribbon:after {
  position: absolute;
  content: "";
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  bottom: -4px;
}
.ribbon:before {
  left: 14px;
}
.ribbon:after {
  right: 18px;
}

.ribbon2 {
  display: inline-block;
  position: absolute;
  top: -6px;
  right: 10px;
  margin: 0;
  padding: 10px 0 7px;
  z-index: 2;
  width: 40px;
  text-align: center;
  color: #fff;
  font-size: 13px;
  border-radius: 2px 0 0 0;
}
.ribbon2:before {
  position: absolute;
  content: '';
  top: 0;
  right: -5px;
  border-right: solid 5px transparent;
}
.ribbon2:after {
  content: '';
  position: absolute;
  left: 0;
  top: 100%;
  height: 0;
  width: 0;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
}

.piece-thumbnail {
  cursor: pointer;
  width: 100%;
  height: 200px;
  object-fit: cover;
}
.md-title {
  height: 32px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.md-subhead {
  height: 20px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.md-card-content {
  display: flex;
  justify-content: flex-start;
}
.badge {
  background: #e8e8e8;
  padding: 8px;
  height: 32px;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 3px;
  white-space: nowrap;
  overflow: hidden;
}
.muted {
  color: #aaa !important;
}
.piece-actions {
  position: absolute;
  bottom: 0px;
  right: 0px;
  padding: 5px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
</style>



