<template>
<div v-if="manager.mySellPieceService.sellPiece">
  <vue-headful title="ブドウさん - 売買物件"/>
  <div class="md-headline" v-if="!manager.mySellPieceService.sellPiece.id">売買物件登録</div>
  <div class="md-headline" v-else>売買物件編集</div>
  <md-steppers :md-active-step.sync="active" md-vertical md-linear>
    <md-step id="step1" md-label="詳細情報入力" :md-error="firstStepError" :md-editable="false" :md-done.sync="step1">
      <md-button class="md-raised md-primary" @click="cancel()">取消</md-button>
      <md-button class="md-raised md-primary" @click="setFirstStepDone()">次へ</md-button>
      <md-button class="md-raised md-accent" @click="save()">保存</md-button>
      <mySellPieceBase ref="baseInfo" :manager="manager"/>
      <md-button class="md-raised md-primary" @click="cancel()">取消</md-button>
      <md-button class="md-raised md-primary" @click="setFirstStepDone()">次へ</md-button>
      <md-button class="md-raised md-accent" @click="save()">保存</md-button>
    </md-step>
    <md-step id="step2" md-label="位置情報入力" :md-error="secondStepError" :md-editable="false" :md-done.sync="step2">
      <md-button class="md-raised md-primary" @click="back('step2', 'step1')">戻る</md-button>
      <md-button class="md-raised md-primary" @click="setSecondStepDone()">次へ</md-button>
      <md-button class="md-raised md-accent" @click="save()">保存</md-button>
      <mySellPieceLocation ref="locationInfo" :manager="manager"/>
      <md-button class="md-raised md-primary" @click="back('step2', 'step1')">戻る</md-button>
      <md-button class="md-raised md-primary" @click="setSecondStepDone()">次へ</md-button>
      <md-button class="md-raised md-accent" @click="save()">保存</md-button>
    </md-step>
    <md-step id="step3" md-label="画像アップロード" :md-editable="false" :md-done.sync="step3">
      <md-button class="md-raised md-primary" @click="back('step3', 'step2')">戻る</md-button>
      <md-button class="md-raised md-accent" @click="save()">保存</md-button>
      <mySellPiecePicture ref="pictures" :manager="manager"/>
      <md-button class="md-raised md-primary" @click="back('step3', 'step2')">戻る</md-button>
      <md-button class="md-raised md-accent" @click="save()">保存</md-button>
    </md-step>
  </md-steppers>
</div>
</template>

<script>
import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import mySellPieceBase from './mySellPieceBase'
import mySellPieceLocation from './mySellPieceLocation'
import mySellPiecePicture from './mySellPiecePicture'
export default {
  props: ['manager'],
  components: {
    mySellPieceBase,
    mySellPieceLocation,
    mySellPiecePicture
  },
  data: () => ({
    active: 'step1',
    step1: false,
    step2: false,
    step3: false,
    firstStepError: null,
    secondStepError: null
  }),
  methods: {
    cancel () {
      this.$router.push({name: 'mySell'})
    },
    setFirstStepDone () {
      this.firstStepError = this.$refs.baseInfo.validate()
      if (!this.firstStepError) {
        this.step1 = true
        this.active = 'step2'
      } else {
        utils.event.$emit('SCROLL_TO_TOP')
      }
    },
    setSecondStepDone () {
      this.secondStepError = this.$refs.locationInfo.validate()
      if (!this.secondStepError) {
        this.step2 = true
        this.active = 'step3'
      } else {
        utils.event.$emit('SCROLL_TO_TOP')
      }
    },
    back (current, pre) {
      this[current] = false
      this[pre] = false
      this.active = pre
    },
    save () {
      const that = this
      this.step1 = false
      this.step2 = false
      this.step3 = false
      this.firstStepError = this.$refs.baseInfo.validate()
      if (this.firstStepError) {
        utils.event.$emit('SCROLL_TO_TOP')
        this.step1 = false
        this.active = 'step1'
        return
      } else {
        this.step1 = true
      }

      this.secondStepError = this.$refs.locationInfo.validate()
      if (this.secondStepError) {
        utils.event.$emit('SCROLL_TO_TOP')
        this.step2 = false
        this.active = 'step2'
        return
      } else {
        this.step2 = true
      }

      that.$refs.pictures.upload(() => {
        if (!manager.mySellPieceService.sellPiece.id) {
          manager.mySellPieceService.insertSellPiece(manager.mySellPieceService.sellPiece, () => {
            that.$router.push({path: '/my/sell'})
          })
        } else {
          manager.mySellPieceService.updateSellPiece(manager.mySellPieceService.sellPiece, () => {
            that.$router.push({path: '/my/sell'})
          })
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
.md-button {
  margin-top: 40px;
  min-width: 70px;
  width: 70px;
}
</style>



