<template>
<div class="map-container">
  <googleMap class="gmap" :manager="manager" :config="shop" v-if="shop.latitude && shop.longitude"></googleMap>
  <div class="gmap-none" v-else>
    <span>店舗の場所が特定できていません。</span>
  </div>
  <md-button md-theme="default" class="md-fab md-mini md-plain locator md-primary" @click="editMap = true">
    <md-icon>room</md-icon>
  </md-button>
  <p class="md-title">
    <span class="name-text">{{shop.name}}</span>
    <md-button md-theme="default" class="md-fab md-mini md-plain md-primary" @click="editName = true">
      <md-icon>edit</md-icon>
    </md-button>
  </p>
  <p class="md-body-1">
    <md-icon class="md-primary">room</md-icon><span>{{shop.address}}</span>
    <md-button md-theme="default" class="md-fab md-mini md-plain md-primary" @click="editAddress = true">
      <md-icon>edit</md-icon>
    </md-button>
  </p>
  <p class="md-body-1">
    <md-icon class="md-primary">directions_railway</md-icon><span>{{shop.access}}</span>
    <md-button md-theme="default" class="md-fab md-mini md-plain md-primary" @click="editAccess = true">
      <md-icon>edit</md-icon>
    </md-button>
  </p>
  <p class="md-body-1">
    <md-icon class="md-primary">phone</md-icon><span>{{shop.phone}}</span>
    <md-button md-theme="default" class="md-fab md-mini md-plain md-primary" @click="editPhone = true">
      <md-icon>edit</md-icon>
    </md-button>
  </p>
  <p class="md-body-1">
    <md-icon class="md-primary">print</md-icon><span>{{shop.fax}}</span>
    <md-button md-theme="default" class="md-fab md-mini md-plain md-primary" @click="editFax = true">
      <md-icon>edit</md-icon>
    </md-button>
  </p>
  <p class="md-body-1">
    <md-icon class="md-primary">email</md-icon><span>{{shop.email}}</span>
    <md-button md-theme="default" class="md-fab md-mini md-plain md-primary" @click="editEmail = true">
      <md-icon>edit</md-icon>
    </md-button>
  </p>

  <md-dialog-prompt
    md-theme="default"
    :md-active.sync="editName"
    v-model="shop.name"
    md-title="店舗名を入力してください。"
    md-input-maxlength="30"
    md-input-placeholder="Type..."
    md-confirm-text="確定"
    md-cancel-text="キャンセル"
  />
  <md-dialog-prompt
    md-theme="default"
    :md-active.sync="editAddress"
    v-model="shop.address"
    md-title="アドレスを入力してください。"
    md-input-maxlength="60"
    md-input-placeholder="Type..."
    md-confirm-text="確定"
    md-cancel-text="キャンセル"
  />
  <md-dialog-prompt
    md-theme="default"
    :md-active.sync="editAccess"
    v-model="shop.access"
    md-title="交通情報を入力してください。"
    md-input-maxlength="60"
    md-input-placeholder="Type..."
    md-confirm-text="確定"
    md-cancel-text="キャンセル"
  />
  <md-dialog-prompt
    md-theme="default"
    :md-active.sync="editPhone"
    v-model="shop.phone"
    md-title="電話番号を入力してください。"
    md-input-maxlength="15"
    md-input-placeholder="Type..."
    md-confirm-text="確定"
    md-cancel-text="キャンセル"
  />
  <md-dialog-prompt
    md-theme="default"
    :md-active.sync="editFax"
    v-model="shop.fax"
    md-title="FAXを入力してください。"
    md-input-maxlength="15"
    md-input-placeholder="Type..."
    md-confirm-text="確定"
    md-cancel-text="キャンセル"
  />
  <md-dialog-prompt
    md-theme="default"
    :md-active.sync="editEmail"
    v-model="shop.email"
    md-title="メールアドレスを入力してください。"
    md-input-maxlength="40"
    md-input-placeholder="Type..."
    md-confirm-text="確定"
    md-cancel-text="キャンセル"
  />
  <md-dialog md-theme="default" :md-active.sync="editMap">
    <div class="map-editor">
      <googleMapLocator ref="locator" :manager="manager" :lat="shop.latitude" :lng="shop.longitude" :prevent="false"></googleMapLocator>
    </div>
    <md-dialog-actions>
      <md-button class="md-primary" @click="editMap = false">キャンセル</md-button>
      <md-button class="md-primary" @click="setLocation">確定</md-button>
    </md-dialog-actions>
  </md-dialog>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
import googleMap from '@/components/common/googleMap'
import googleMapLocator from '@/components/common/googleMapLocator'
export default {
  props: ['manager', 'shop'],
  components: {
    googleMap,
    googleMapLocator
  },
  data: () => ({
    editMap: false,
    editName: false,
    editAddress: false,
    editAccess: false,
    editPhone: false,
    editFax: false,
    editEmail: false
  }),
  methods: {
    setLocation () {
      let result = this.$refs.locator.validate()
      if (result) {
        this.shop.latitude = result.lat
        this.shop.longitude = result.lng
      }
      this.editMap = false
    }
  }
}
</script>

<style scoped lang="scss">
.map-container {
  position: relative;
}
.gmap {
  position: relative;
  width: 100%;
  height: 400px;
}
.gmap-none {
  width: 100%;
  height: 200px;
  background: #e8e8e8;
  display: flex;
  align-items: center;
  padding: 16px;
}
.md-body-1 {
  margin: 3px;
  display: flex;
  align-items: center;
}
p {
  display: flex;
  align-items: center;
  span {
    width: calc(100% - 34px - 56px);
  }
  .name-text {
    width: calc(100% - 3px - 56px);
  }
}
.locator {
  position: absolute;
  top: 4px;
  right: 4px;
}
.map-editor {
  padding: 16px;
  height: 100%;
  overflow: auto;
}
</style>



