<template>
<div class="upload-wrapper">
  <file-upload class="fileupload" ref="uploader" :custom-action="uploadAction" :accept="acceptFileType" :extensions="extensionFileType" :size="limitSize" :multiple="false" :thread="1" @input-filter="inputFilter" v-model="files"/>
  <md-button md-theme="default" class="md-icon-button md-primary md-raised" @click="browse"><md-icon>file_upload</md-icon></md-button>
</div>
</template>

<script>
import utils from '@/tool/utils.js'
import FileUpload from 'vue-upload-component/dist/vue-upload-component.part.js'
export default {
  props: {
    manager: {
      default: 'manager'
    },
    acceptType: {
      type: String,
      default: null
    },
    type: {
      type: String,
      default: 'none'
    },
    subFolder: {
      type: String,
      default: null
    },
    next: {
      type: Function,
      default: null
    }
  },
  components: {
    FileUpload
  },
  data: () => ({
    files: [],
    acceptFileType: 'image/png, image/jpeg, image/tiff, image/x-tiff, image/svg+xml, image/bmp, image/x-bmp, image/x-ms-bmp, image/gif',
    extensionFileType: 'jpg, jpeg, png, tif, tiff, svg, bmp, gif',
    limitSize: 1024 * 1024 * 2
  }),
  created () {
    if (this.acceptType === 'image') {
      this.acceptFileType = 'image/png, image/jpeg, image/tiff, image/x-tiff, image/svg+xml, image/bmp, image/x-bmp, image/x-ms-bmp, image/gif'
      this.extensionFileType = 'jpg, jpeg, png, tif, tiff, svg, bmp, gif'
      this.limitSize = 1024 * 1024 * 20
    } else {
      this.acceptFileType = null
      this.extensionFileType = null
      this.limitSize = 1024 * 1024 * 1024 * 2
    }
  },
  watch: {
    files () {
      if (this.files.length > 0) {
        this.$refs.uploader.active = true
      }
    }
  },
  methods: {
    inputFilter (newFile, oldFile, prevent) {
      if (newFile && !oldFile) {
        if (/(\/|^)(Thumbs\.db|desktop\.ini|\..+)$/.test(newFile.name)) {
          return prevent()
        }
        if (newFile && (!oldFile || newFile.file !== oldFile.file)) {
          if (newFile.size > this.limitSize) {
            utils.event.$emit('SHOW_MESSAGE', {code: 'B005', detail: 'ファイルサイズを' + utils.formatSize(this.limitSize) + '以内にしてください。'})
            return prevent()
          } else {
            newFile.blob = ''
            let URL = window.URL || window.webkitURL
            if (URL && URL.createObjectURL) {
              newFile.blob = URL.createObjectURL(newFile.file)
            }
            newFile.thumbnail = ''
            if (newFile.blob && newFile.type.substr(0, 6) === 'image/' && newFile.type.indexOf('photoshop') < 0 && newFile.type.indexOf('psd') < 0) {
              newFile.thumbnail = newFile.blob
            }
            newFile.progress = 0
          }
        }
      }
    },
    browse () {
      this.$refs.uploader.$el.click()
    },
    clear () {
      this.$refs.uploader.clear()
    },
    uploadAction (file, component) {
      let that = this
      let options = {type: this.type}
      if (this.subFolder) {
        options.subFolder = this.subFolder
      }
      return new Promise(async (resolve, reject) => {
        utils.event.$emit('LOCK_SCREEN', 'lock')
        await utils.uploadFile(file, options).then((response) => {
          if (response && response[0]) {
            if (that.next !== null) {
              that.next(response[0])
            }
          }
          that.clear()
          utils.event.$emit('LOCK_SCREEN', 'unlock')
        })
      })
    }
  }
}
</script>

<style scoped lang="scss">
.fileupload {
  display: none;
}
</style>



