import utils from '@/tool/utils.js'
import SellPiece from '@/store/models/sellPiece.js'

class MySellPieceService {
  constructor () {
    this._sellPieces = []
    this._count = 0
    this._page = 0
    this._sellPiece = null
    this.filter = {
      type: '1'
    }
  }
  get sellPieces () {
    return this._sellPieces
  }
  set sellPieces (pieces) {
    if (pieces) {
      pieces.forEach(one => {
        // 重複を削除
        this._sellPieces.filter(sellPiece => {
          return sellPiece._id === one._id
        })
        // リストに追加
        this._sellPieces.push(new SellPiece(one))
      })
    }
  }
  get count () {
    return this._count
  }
  set count (data) {
    this._count = parseInt(data)
  }
  get page () {
    return this._page
  }
  set page (data) {
    this._page = parseInt(data)
  }
  get sellPiece () {
    return this._sellPiece
  }
  set sellPiece (piece) {
    if (piece) {
      this._sellPiece = new SellPiece(piece)
    } else {
      this._sellPiece = null
    }
  }

  init () {
    this._sellPieces.splice(0, this._sellPieces.length)
    this.count = 0
    this.page = 0
    this.sellPiece = null
  }

  findPieces (filter, page, next) {
    let that = this
    utils.restGet('/private/findSellPieces', {filter: filter, page: page}).then(
      response => {
        if (response) {
          that.count = response.count
          that.sellPieces = response.datas
          if (next) next()
        }
      }
    )
  }

  loadSellPieces = (to, from, next) => {
    this.init()
    this.findPieces(this.filter, null, next)
  }

  moreSellPieces (next) {
    let that = this
    this.findPieces(this.filter, that.page + 1, () => {
      that.page += 1
      if (next) next()
    })
  }

  loadSellPieceForEdit = (to, from, next) => {
    let that = this
    that.init()
    const reg = /[0-9A-Fa-f]{24}/g
    if (to.params.id && to.params.id !== 'new' && reg.test(to.params.id)) {
      that.findPieces({_id: to.params.id}, null, () => {
        if (that.sellPieces.length > 0) {
          that._sellPiece = that.sellPieces[0]
          if (next) next()
        } else {
          utils.event.$emit('SHOW_MESSAGE', {code: 'B001', detail: '対象物件を参照する権限がないか、対象物件が削除されたか、いくつかの原因が考えられます。'}, () => {
            utils.router.push({path: '/'})
          })
        }
      })
    } else if (to.params.id && to.params.id === 'new') {
      that.sellPiece = {}
      if (next) next()
    } else {
      utils.event.$emit('SHOW_MESSAGE', {code: 'B009', detail: '物件IDが間違っています。'}, () => {
        utils.router.push({path: '/'})
      })
    }
  }

  loadSellPieceForDetail = (to, from, next) => {
    let that = this
    that.init()
    const reg = /[0-9A-Fa-f]{24}/g
    if (to.params.id && reg.test(to.params.id)) {
      that.findPieces({_id: to.params.id}, null, () => {
        if (that.sellPieces.length > 0) {
          that._sellPiece = that.sellPieces[0]
          if (next) next()
        } else {
          utils.event.$emit('SHOW_MESSAGE', {code: 'B001', detail: '対象物件を参照する権限がないか、対象物件が削除されたか、いくつかの原因が考えられます。'}, () => {
            utils.router.push({path: '/'})
          })
        }
      })
    } else {
      utils.event.$emit('SHOW_MESSAGE', {code: 'B009', detail: '物件IDが間違っています。'}, () => {
        utils.router.push({path: '/'})
      })
    }
  }

  loadSellPieceForPreview = (to, from, next) => {
    let that = this
    that.init()
    const reg = /[0-9A-Fa-f]{24}/g
    if (to.params.id && reg.test(to.params.id)) {
      that.findPieces({_id: to.params.id}, null, () => {
        if (that.sellPieces.length > 0) {
          that._sellPiece = that.sellPieces[0]
          if (parseInt(to.params.index) >= 0 && parseInt(to.params.index) < that.sellPiece.previews.length) {
            if (next) next()
          } else {
            utils.event.$emit('SHOW_MESSAGE', {code: 'B001'}, () => {
              utils.router.push({path: '/'})
            })
          }
        } else {
          utils.event.$emit('SHOW_MESSAGE', {code: 'B001', detail: '対象物件を参照する権限がないか、対象物件が削除されたか、いくつかの原因が考えられます。'}, () => {
            utils.router.push({path: '/'})
          })
        }
      })
    } else {
      utils.event.$emit('SHOW_MESSAGE', {code: 'B009', detail: '物件IDが間違っています。'}, () => {
        utils.router.push({path: '/'})
      })
    }
  }

  insertSellPiece (piece, next) {
    utils.restPut('/private/insertSellPiece', piece).then(savedPiece => {
      if (savedPiece) {
        if (next) next()
      }
    })
  }

  updateSellPiece (piece, next) {
    utils.restPost('/private/updateSellPiece', piece).then(savedPiece => {
      if (savedPiece) {
        if (next) next()
      }
    })
  }

  publishSellPiece (piece, next) {
    utils.restPost('/private/updateSellPiece', {_id: piece._id, isPublishing: true}).then(savedPiece => {
      if (savedPiece) {
        if (next) next()
      }
    })
  }

  unPublishSellPiece (piece, next) {
    utils.restPost('/private/updateSellPiece', {_id: piece._id, isPublishing: false}).then(savedPiece => {
      if (savedPiece) {
        if (next) next()
      }
    })
  }

  deleteSellPiece (piece, next) {
    let that = this
    utils.restPost('/private/deleteSellPiece', {_id: piece._id}).then(savedPiece => {
      if (savedPiece) {
        for (let i = that.sellPieces.length - 1; i >= 0; i--) {
          if (that.sellPieces[i]._id === piece._id) {
            that.sellPieces.splice(i, 1)
            that._count--
            break
          }
        }
        if (next) next()
        utils.event.$emit('SHOW_MESSAGE', {code: 'I007'})
      }
    })
  }
}

export default new MySellPieceService()



