import utils from '@/tool/utils.js'
// import manager from '@/store/manager.js'
import BuyRequest from '@/store/models/buyRequest.js'

class MyBuyRequestService {
  constructor () {
    this._buyRequests = []
    this._editBuyRequest = null
    this._count = 0
    this.page = 1
  }
  get count () {
    return this._count
  }
  set count (data) {
    this._count = parseInt(data)
  }
  get buyRequests () {
    return this._buyRequests
  }
  get editBuyRequest () {
    return this._editBuyRequest
  }
  set editBuyRequest (request) {
    this._editBuyRequest = new BuyRequest(request)
  }

  init () {
    this._buyRequests.splice(0, this._buyRequests.length)
    this._count = 0
    this.page = 1
  }

  loadMyBuy = (to, from, next) => {
    this.init()
    this.findBuyRequest({}, 1).then(
      () => {
        next()
      }
    )
  }

  async findBuyRequest (params, page) {
    let that = this
    if (page && page >= 1) page--
    await utils.restGet('/private/findBuyRequests', {filter: params, page: page}).then(response => {
      let datas = response.datas
      that.count = response.count
      that._buyRequests.splice(0, that._buyRequests.length)
      if (datas) {
        datas.forEach(one => {
          that._buyRequests.push(new BuyRequest(one))
        })
      }
    })
  }

  loadMyBuyRequest = (to, from, next) => {
    let that = this
    if (to.params.id === 'new') {
      that.editBuyRequest = new BuyRequest({})
      next()
    } else {
      utils.restGet('/private/findBuyRequests', {filter: {'_id': to.params.id}}).then(
        response => {
          if (response) {
            that.editBuyRequest = new BuyRequest(response.datas[0])
            next()
          }
        }
      )
    }
  }

  insertBuyRequest (request, next) {
    utils.restPut('/private/insertBuyRequest', request).then(savedPiece => {
      if (savedPiece) {
        next()
      }
    })
  }

  async publishBuyRequest (request) {
    let that = this
    let isSuccess = true
    await utils.restPost('/private/publishBuyRequest', {_id: request._id, isPublishing: true}).then(savedRequest => {
      if (!savedRequest) {
        isSuccess = false
      }
    })

    if (!isSuccess) {
      return {err: true}
    }
    that.init()
    await that.findBuyRequest({}, 1)
  }

  async unPublishBuyRequest (request) {
    let that = this
    let isSuccess = true
    await utils.restPost('/private/unPublishBuyRequest', {_id: request._id, isPublishing: false}).then(savedRequest => {
      if (!savedRequest) {
        isSuccess = false
      }
    })

    if (!isSuccess) {
      return {err: true}
    }
    that.init()
    await that.findBuyRequest({}, 1)
  }

  updateBuyRequest (request, next) {
    utils.restPost('/private/updateBuyRequest', request).then(savedRequest => {
      if (savedRequest) {
        next()
      }
    })
  }

  deleteBuyRequest (request, next) {
    let that = this
    utils.restPost('/private/deleteBuyRequest', {_id: request._id}).then(savedRequest => {
      if (savedRequest) {
        that.init()
        that.findBuyRequest({}, 1)
        next()
      }
    })
  }
}

export default new MyBuyRequestService()



