<template>
<div>
  <vue-headful title="ブドウさん - プロファイル"/>
  <div class="md-layout md-gutter md-alignment-top">
    <div class="md-layout-item md-size-30 md-medium-size-40 md-small-size-100">
      <div class="row">
        <contactCard :manager="manager" :object="user"/>
      </div>
      <div class="triangle" v-if="user.exam"><md-icon class="md-primary">details</md-icon></div>
      <div class="row" v-if="user.exam">
        <contactCard :manager="manager" :object="user.exam"/>
      </div>
    </div>
    <div class="md-layout-item md-size-70 md-medium-size-60 md-small-size-100 md-layout md-gutter">
      <div class="md-layout-item md-size-50">
        <div class="upload-frame">
          <md-content class="md-primary upload-header">アバター</md-content>
          <uploadThumbnail ref="avatarUploader" :manager="manager" :preview="user.avatar" :subFolder="user._id"/>
        </div>
      </div>
      <div class="md-layout-item md-size-50">
        <div class="upload-frame">
          <md-content class="md-primary upload-header">ロゴ</md-content>
          <uploadThumbnail ref="logoUploader" :manager="manager" :preview="user.logo" type="icon" :subFolder="user._id"/>
        </div>
      </div>
      <div class="md-layout-item md-size-33 md-medium-size-50 md-xsmall-size-100">
        <md-field :class="{'md-invalid': !isNicknameValid}">
          <label>ニックネーム</label>
          <md-input v-model="user.nickname" type="text"></md-input>
          <span class="md-error">10文字以内</span>
        </md-field>
      </div>
      <div class="md-layout-item md-size-33 md-medium-size-50 md-xsmall-size-100">
        <md-field :class="{'md-invalid': !isCompanyValid}">
          <label>会社名</label>
          <md-input v-model="user.company" type="text" :required="manager.user.exam !== null || manager.user.isMediator" :disabled="manager.user.exam !== null || manager.user.isMediator"></md-input>
          <span class="md-helper-text" v-if="manager.user.isMediator">変更は「申込」にて</span>
          <span class="md-error">15文字以内</span>
        </md-field>
      </div>
      <div class="md-layout-item md-size-33 md-medium-size-50 md-xsmall-size-100">
        <md-field :class="{'md-invalid': !isPositionValid}">
          <label>役職</label>
          <md-input v-model="user.position" type="text"></md-input>
          <span class="md-error">10文字以内</span>
        </md-field>
      </div>
      <div class="md-layout-item md-size-33 md-medium-size-50 md-xsmall-size-100">
        <md-field :class="{'md-invalid': !isPhoneValid}">
          <label>電話番号</label>
          <md-input v-model="user.phone" type="text" :required="manager.user.exam !== null || manager.user.isMediator" :disabled="manager.user.exam !== null || manager.user.isMediator"></md-input>
          <span class="md-helper-text" v-if="manager.user.isMediator">変更は「申込」にて</span>
          <span class="md-error">15文字以内</span>
        </md-field>
      </div>
      <div class="md-layout-item md-size-33 md-medium-size-50 md-xsmall-size-100">
        <md-field :class="{'md-invalid': !isFaxValid}">
          <label>FAX</label>
          <md-input v-model="user.fax" type="text" :required="manager.user.exam !== null || manager.user.isMediator" :disabled="manager.user.exam !== null || manager.user.isMediator"></md-input>
          <span class="md-helper-text" v-if="manager.user.isMediator">変更は「申込」にて</span>
          <span class="md-error">15文字以内</span>
        </md-field>
      </div>
      <div class="md-layout-item md-size-33 md-medium-size-50 md-xsmall-size-100">
        <md-field :class="{'md-invalid': !isEmailValid}">
          <label>メールアドレス</label>
          <md-input v-model="user.email" type="text" :required="manager.user.exam !== null || manager.user.isMediator" :disabled="manager.user.exam !== null || manager.user.isMediator"></md-input>
          <span class="md-helper-text" v-if="manager.user.isMediator">変更は「申込」にて</span>
          <span class="md-error">40文字以内</span>
        </md-field>
      </div>
      <div class="md-layout-item md-size-100">
        <md-field :class="{'md-invalid': !isHomepageValid}">
          <label>ホームページ</label>
          <md-input v-model="user.homepage" type="text" :required="manager.user.exam !== null || manager.user.isMediator" :disabled="manager.user.exam !== null || manager.user.isMediator"></md-input>
          <span class="md-helper-text" v-if="manager.user.isMediator">変更は「申込」にて</span>
          <span class="md-error">100文字以内</span>
        </md-field>
      </div>
      <div class="md-layout-item md-size-100">
        <md-field :class="{'md-invalid': !isAddressValid}">
          <label>会社アドレス</label>
          <md-input v-model="user.address" type="text" :required="manager.user.exam !== null || manager.user.isMediator" :disabled="manager.user.exam !== null || manager.user.isMediator"></md-input>
          <span class="md-helper-text" v-if="manager.user.isMediator">変更は「申込」にて</span>
          <span class="md-error">60文字以内</span>
        </md-field>
      </div>
      <div class="md-layout-item md-size-40 md-small-size-100">
        <md-field :class="{'md-invalid': !isLicenseValid}">
          <label>免許番号</label>
          <md-input v-model="user.license" type="text" :required="manager.user.exam !== null || manager.user.isMediator" :disabled="manager.user.exam !== null || manager.user.isMediator"></md-input>
          <span class="md-helper-text" v-if="manager.user.isMediator">変更は「申込」にて</span>
          <span class="md-error">20文字以内</span>
        </md-field>
      </div>
      <div class="md-layout-item md-size-60 md-small-size-100">
        <md-field :class="{'md-invalid': !isCommentValid}">
          <label>コメント</label>
          <md-input v-model="user.comment" type="text"></md-input>
          <span class="md-error">30文字以内</span>
        </md-field>
      </div>
    </div>
  </div>
  <div class="md-layout">
    <div class="md-layout-item md-size-33 md-xsmall-size-100 action-container">
      <md-button class="md-primary md-raised action-button" to="/my/apply">
        <div class="icon-text">
          <md-icon>security</md-icon>
          <p v-if="manager.user.isOrdinary">業者認定申込</p>
          <p v-if="manager.user.isMediator">情報変更申込</p>
        </div>
      </md-button>
    </div>
    <div class="md-layout-item md-size-33 md-xsmall-size-50 action-container">
      <md-button class="md-primary md-raised action-button" @click="changePassword">
        <div class="icon-text">
          <md-icon>lock</md-icon><p>パスワード変更</p>
        </div>
      </md-button>
    </div>
    <div class="md-layout-item md-size-33 md-xsmall-size-50 action-container">
      <md-button class="md-primary md-raised action-button" @click="save" :disabled="!isNicknameValid || !isCompanyValid || !isPositionValid || !isPhoneValid || !isFaxValid || !isEmailValid || !isLicenseValid || !isCommentValid || !isHomepageValid || !isAddressValid">
        <div class="icon-text">
          <md-icon>save</md-icon><p>情報保存</p>
        </div>
      </md-button>
    </div>
  </div>
  <passwordDialog ref="changePassword" :manager="manager"/>
</div>
</template>

<script>
import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import contactCard from '@/components/shared/contactCard'
import uploadThumbnail from '@/components/common/uploadThumbnail'
import passwordDialog from './passwordDialog'
export default {
  props: ['manager'],
  components: {
    contactCard,
    uploadThumbnail,
    passwordDialog
  },
  data: () => ({
    user: null
  }),
  created () {
    this.user = utils.clone(manager.user)
  },
  computed: {
    isNicknameValid () {
      return this.user.nickname.length <= 10
    },
    isCompanyValid () {
      return this.user.company.length <= 15
    },
    isPositionValid () {
      return this.user.position.length <= 10
    },
    isPhoneValid () {
      return this.user.phone.length <= 15
    },
    isFaxValid () {
      return this.user.fax.length <= 15
    },
    isEmailValid () {
      return this.user.email.length <= 40
    },
    isLicenseValid () {
      return this.user.license.length <= 20
    },
    isCommentValid () {
      return this.user.comment.length <= 30
    },
    isHomepageValid () {
      return this.user.homepage.length <= 100
    },
    isAddressValid () {
      return this.user.address.length <= 60
    }
  },
  methods: {
    save () {
      let that = this
      that.$refs.avatarUploader.upload((avatar) => {
        if (avatar) that.user.avatar = avatar.file
        that.$refs.logoUploader.upload((logo) => {
          if (logo) {
            that.user.logo = logo.file
            that.user.icon = logo.thumbnail
          }
          utils.restPost('/private/updateUser', that.user.toJSON()).then(
            response => {
              if (response) {
                manager.user.login(response)
                utils.event.$emit('SHOW_MESSAGE', {code: 'I002'})
              }
            }
          )
        })
      })
    },
    changePassword () {
      this.$refs.changePassword.show()
    }
  }
}
</script>

<style scoped lang="scss">
.row {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin-bottom: 20px;
}
.triangle {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  .md-icon {
    font-size: 60px !important;
  }
}
.upload-frame {
  width: 160px;
  left: 0px;
  right: 0px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
}
.upload-header {
  padding: 5px;
  text-align: center;
}
.md-layout-item {
  margin-top: 10px;
}
.action-container {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  .action-button {
    width: 150px;
  }
}
</style>



