<template>
<div class="topic md-layout md-gutter md-alignment-center">
  <div class="md-layout-item md-size-20 md-small-size-50">
    <md-content class="topic-type md-primary">{{object.type}}</md-content>
  </div>
  <div class="topic-date md-layout-item md-size-20 md-small-size-50">
    <p class="md-caption">{{object.idateText}}</p>
  </div>
  <div class="topic-text md-layout-item md-size-60 md-small-size-100">
    <p class="md-body-1">{{object.text}}</p>
  </div>
</div>
</template>

<script>
// import manager from '@/store/manager.js'
// import utils from '@/tool/utils.js'
export default {
  props: ['manager', 'object']
}
</script>

<style scoped lang="scss">
.topic {
  width: 100%;
  margin: 0;
  border-bottom: solid 1px #e8e8e8;
  padding-bottom: 10px;
  margin-bottom: 10px;
}
.topic-type {
  width: 120px;
  padding: 5px 16px;
  border-radius: 20px;
  text-align: center;
}
.md-caption {
  text-align: right;
}
.md-body-1 {
  padding-left: 20px;
  padding-right: 20px;
}
</style>



