<template>
<carousel :perPage="1">
  <slide v-for="(photo, index) in photos" :key="index">
    <div class="photo-frame">
      <img :src="photo"/>
    </div>
  </slide>
</carousel>
</template>

<script>
// import utils from '@/tool/utils.js'
// import uuid from 'uuid'
import {Carousel, Slide} from 'vue-carousel'
export default {
  props: ['manager', 'photos'],
  components: {
    Carousel,
    Slide
  }
}
</script>

<style scoped lang="scss">
.photo-frame {
  height: 100%;
  width: 100%;
  max-height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>



