import moment from 'moment'
import manager from '@/store/manager.js'
import utils from '@/tool/utils.js'
import Contact from './contact.js'

class RentPiece {
  constructor (data) {
    this._id = data._id
    // 公開済み
    this.isPublishing = data.isPublishing

    // ----------------------------
    // 共通属性
    // ----------------------------

    // 業者
    this.contact = null
    if (data.contact) this.contact = new Contact(data.contact)
    this._contactID = data.contactID || null
    // 写真
    this.photos = data.photos || []
    this.photoThumbnails = data.photoThumbnails || []
    // VRプレビュー
    this.previews = data.previews || []
    this.previewThumbnails = data.previewThumbnails || []
    // 間取り図
    this.floorPlans = data.floorPlans || []
    this.floorPlanThumbnails = data.floorPlanThumbnails || []

    // 物件名
    this.name = data.name || null
    // アドレス
    this.address = data.address || null
    // 建物種別
    this._type = '1'
    if (data.type) this.type = data.type
    // 賃料
    this._rent = null
    if (data.rent != null) this.rent = data.rent
    // 敷金・保証金
    this._deposit = null
    if (data.deposit != null) this.deposit = data.deposit
    // 礼金
    this._keyMoney = null
    if (data.keyMoney != null) this.keyMoney = data.keyMoney
    // 更新料
    this._updateMoney = null
    if (data.updateMoney != null) this.updateMoney = data.updateMoney
    // 保険など
    this.insurance = data.insurance || null
    // 契約期間
    this.contractPeroid = data.contractPeroid || null
    // 線路・駅・徒歩時間
    this.stations = []
    this.setStations(data.stations)
    // 階建て
    this._story = null
    if (data.story != null) this.story = data.story
    // 建物構造
    this._structure = null
    if (data.structure) this.structure = data.structure
    // 築年月
    this._built = null
    if (data.built) this.built = data.built
    // 新築
    this.isNew = data.isNew || null
    // 取引態様
    this._aspect = null
    if (data.aspect) this.aspect = data.aspect
    // 間取り
    this._layout = null
    if (data.layout) this.layout = data.layout
    // 仲介手数料
    this.margin = data.margin || null
    // 現況
    this._available = null
    if (data.available) this.available = data.available
    // 入居可能日
    this._residentable = null
    if (data.residentable) this.residentable = data.residentable
    // 権利形態
    // this._rights = null
    // if (data.rights) this.rights = data.rights
    // 専有面積
    this._exclusiveArea = null
    if (data.exclusiveArea != null) this.exclusiveArea = data.exclusiveArea
    // 備考
    this.comment = data.comment || null
    // メモ
    this.memo = data.memo || null
    // 緯度
    if (data.latitude != null) this.latitude = data.latitude
    this.latitude = data.latitude || null
    // 経度
    if (data.longitude != null) this.longitude = data.longitude
    this.longitude = data.longitude || null
    // 掲載日
    this._cdate = null
    if (data.cdate) this.cdate = data.cdate
    // 更新日
    this._udate = null
    if (data.udate) this.udate = data.udate

    // ----------------------------
    // マンション・アパートの場合
    // ----------------------------

    // 階層
    this._floor = null
    if (data.floor != null) this.floor = data.floor
    // 方位
    this._direction = null
    if (data.direction) this.direction = data.direction
    // 総戸数
    // this._households = null
    // if (data.households != null) this.households = data.households
    // 管理費
    this._manage = null
    if (data.manage != null) this.manage = data.manage
    // 修繕費
    // this._repair = null
    // if (data.repair != null) this.repair = data.repair
    // バルコニー
    this.balcony = data.balcony || null
    // 駐車場
    this.carParking = data.carParking || null
    // 駐輪場
    this.bikeParking = data.bikeParking || null
    // ペット可
    this.pet = data.pet || null
    // 都市ガス
    this.cityGas = data.cityGas || null

    // ----------------------------
    // 一戸建の場合
    // ----------------------------

    // 接道
    // this.connection = data.connection || null
    // 建ぺい率
    // this._coverage = null
    // if (data.coverage != null) this.coverage = data.coverage
    // 容積率
    // this._volume = null
    // if (data.volume != null) this.volume = data.volume
    // 建物面積
    // this._buildingArea = null
    // if (data.buildingArea != null) this.buildingArea = data.buildingArea
    // 土地面積
    // this._landArea = null
    // if (data.landArea != null) this.landArea = data.landArea
  }

  get id () {
    return this._id
  }
  get isPublishing () {
    return !!this._isPublishing
  }
  set isPublishing (data) {
    this._isPublishing = !!data
  }
  get built () {
    return this._built ? this._built.valueOf() : null
  }
  get builtText () {
    let text = ''
    if (this._built) text += this._built.format('YYYY年M月') + '/'
    text += this.age
    return text
  }
  get builtInputText () {
    return this._built ? this._built.format('YYYYMM') : ''
  }
  set built (data) {
    if (data) this._built = moment(data)
    else this._built = null
  }
  get residentable () {
    return this._residentable ? this._residentable.valueOf() : null
  }
  get residentableText () {
    return this._residentable ? this._residentable.format('YYYY年M月D日') : '-'
  }
  set residentable (data) {
    if (data) this._residentable = moment(data)
    else this._residentable = null
  }
  get carParkingText () {
    return this.carParking ? '有' : '無'
  }
  get bikeParkingText () {
    return this.bikeParking ? '有' : '無'
  }
  get balconyText () {
    return this.balcony ? '有' : '無'
  }
  get petText () {
    return this.pet ? '可' : '否'
  }
  get cityGasText () {
    return this.cityGas ? '有' : '無'
  }
  get rent () {
    return this._rent == null ? null : parseInt(this._rent)
  }
  get rentText () {
    return this.rent != null ? utils.formatHugeMoney(this.rent) : '-'
  }
  set rent (data) {
    if (data) this._rent = parseInt(data)
    else this._rent = null
  }
  get deposit () {
    return this._deposit == null ? null : parseFloat(this._deposit)
  }
  get depositText () {
    return this.deposit != null ? this.deposit + 'ヶ月' : '無し'
  }
  set deposit (data) {
    if (data) this._deposit = parseFloat(data)
    else this._deposit = null
  }
  get keyMoney () {
    return this._keyMoney == null ? null : parseFloat(this._keyMoney)
  }
  get keyMoneyText () {
    return this.keyMoney != null ? this.keyMoney + 'ヶ月' : '無し'
  }
  set keyMoney (data) {
    if (data) this._keyMoney = parseFloat(data)
    else this._keyMoney = null
  }
  get updateMoney () {
    return this._updateMoney == null ? null : parseFloat(this._updateMoney)
  }
  get updateMoneyText () {
    return this.updateMoney != null ? this.updateMoney + 'ヶ月' : '無し'
  }
  set updateMoney (data) {
    if (data) this._updateMoney = parseFloat(data)
    else this._updateMoney = null
  }
  get story () {
    return this._story == null ? null : parseInt(this._story)
  }
  get storyText () {
    return this.story != null ? this.story + '階建' : '-'
  }
  set story (data) {
    if (data) this._story = parseInt(data)
    else this._story = null
  }
  get floor () {
    return this._floor == null ? null : parseInt(this._floor)
  }
  get floorText () {
    return this.floor != null ? this.floor + '階目' : '-'
  }
  get floorStoryText () {
    let text = '-'
    if (this.isApartment) {
      if (this.floor > 0) {
        text = this.floorText
      }
      if (this.story > 0) {
        if (text !== '-') {
          text += ' / '
        } else {
          text = ''
        }
        text += this.storyText
      }
    } else if (this.isDetached) {
      if (this.story > 0) {
        text = this.storyText
      }
    }
    return text
  }
  set floor (data) {
    if (data) this._floor = parseInt(data)
    else this._floor = null
  }
  // get households () {
  //   return this._households == null ? null : parseInt(this._households)
  // }
  // get householdsText () {
  //   return this.households != null ? utils.formatNumber(this.households) : '-'
  // }
  // set households (data) {
  //   if (data) this._households = parseInt(data)
  //   else this._households = null
  // }
  get manage () {
    return this._manage == null ? null : parseInt(this._manage)
  }
  get manageText () {
    return this.manage != null ? utils.formatMoney(this.manage) + '円' : '無し'
  }
  set manage (data) {
    if (data) this._manage = parseInt(data)
    else this._manage = null
  }
  get exclusiveArea () {
    return this._exclusiveArea == null ? null : parseFloat(this._exclusiveArea)
  }
  get exclusiveAreaText () {
    return this.exclusiveArea ? utils.formatNumber(this.exclusiveArea) + 'm²' : '-'
  }
  set exclusiveArea (data) {
    if (data) this._exclusiveArea = parseFloat(data)
    else this._exclusiveArea = null
  }
  // get buildingArea () {
  //   return this._buildingArea == null ? null : parseFloat(this._buildingArea)
  // }
  // get buildingAreaText () {
  //   return this.buildingArea ? utils.formatNumber(this.buildingArea) + 'm²' : '-'
  // }
  // set buildingArea (data) {
  //   if (data) this._buildingArea = parseFloat(data)
  //   else this._buildingArea = null
  // }
  // get landArea () {
  //   return this._landArea == null ? null : parseFloat(this._landArea)
  // }
  // get landAreaText () {
  //   return this.landArea ? utils.formatNumber(this.landArea) + 'm²' : '-'
  // }
  // set landArea (data) {
  //   if (data) this._landArea = parseFloat(data)
  //   else this._landArea = null
  // }
  // get repair () {
  //   return this._repair == null ? null : parseInt(this._repair)
  // }
  // get repairText () {
  //   return this.repair != null ? utils.formatHugeMoney(this.repair) : '-'
  // }
  // set repair (data) {
  //   if (data) this._repair = parseInt(data)
  //   else this._repair = null
  // }
  // get coverage () {
  //   return this._coverage == null ? null : parseFloat(this._coverage)
  // }
  // get coverageText () {
  //   return this.coverage != null ? utils.formatPercentage(this.coverage) : '-'
  // }
  // set coverage (data) {
  //   if (data) this._coverage = parseFloat(data)
  //   else this._coverage = null
  // }
  // get volume () {
  //   return this._volume == null ? null : parseFloat(this._volume)
  // }
  // get volumeText () {
  //   return this.volume != null ? utils.formatPercentage(this.volume) : '-'
  // }
  // set volume (data) {
  //   if (data) this._volume = parseFloat(data)
  //   else this._volume = null
  // }
  get latitude () {
    return this._latitude == null ? null : parseFloat(this._latitude)
  }
  set latitude (data) {
    if (data) this._latitude = parseFloat(data)
    else this._latitude = null
  }
  get longitude () {
    return this._longitude == null ? null : parseFloat(this._longitude)
  }
  set longitude (data) {
    if (data) this._longitude = parseFloat(data)
    else this._longitude = null
  }
  get cdate () {
    return this._cdate ? this._cdate.valueOf() : null
  }
  get cdateText () {
    return this._cdate != null ? this._cdate.format('YYYY年M月D日') : '-'
  }
  set cdate (data) {
    if (data) this._cdate = moment(data)
    else this._cdate = null
  }
  get udate () {
    return this._udate ? this._udate.valueOf() : null
  }
  get udateText () {
    return this._udate != null ? this._udate.format('YYYY年M月D日') : '-'
  }
  set udate (data) {
    if (data) this._udate = moment(data)
    else this._udate = null
  }

  get aspect () {
    return this._aspect
  }
  get aspectText () {
    return this.aspect != null ? manager.const.aspect[this.aspect] : '-'
  }
  set aspect (data) {
    this._aspect = data
  }
  get available () {
    return this._available
  }
  get availableText () {
    return this.available != null ? manager.const.availableRent[this.available] : '-'
  }
  set available (data) {
    this._available = data
  }
  get direction () {
    return this._direction
  }
  get directionText () {
    return this.direction != null ? manager.const.direction[this.direction] : '-'
  }
  set direction (data) {
    this._direction = data
  }
  get layout () {
    return this._layout
  }
  get layoutText () {
    return this.layout != null ? manager.const.layout[this.layout] : '-'
  }
  set layout (data) {
    this._layout = data
  }
  // get rights () {
  //   return this._rights
  // }
  // get rightsText () {
  //   return this.rights != null ? manager.const.rights[this.rights] : '-'
  // }
  // set rights (data) {
  //   this._rights = data
  // }
  get structure () {
    return this._structure
  }
  get structureText () {
    return this.structure != null ? manager.const.structure[this.structure] : '-'
  }
  set structure (data) {
    this._structure = data
  }
  get type () {
    return this._type
  }
  get typeText () {
    return this.type != null ? manager.const.typeRent[this.type] : '-'
  }
  get typeTextAb () {
    switch (this._type) {
      case '1':
        return 'マ'
      case '2':
        return '戸'
      case '3':
        return 'ア'
      default:
        return '-'
    }
  }
  set type (data) {
    this._type = data
  }

  // 築年数
  get age () {
    if (this.isNew) {
      return '新築'
    } else if (this._built) {
      if (moment().diff(this._built, 'month') <= 0) {
        return '新築'
      } else {
        const years = moment().diff(this._built, 'year')
        const months = moment().diff(this._built, 'month') - (years * 12)
        let text = ''
        if (years > 0) text += years + '年'
        if (months > 0) text += months + 'ヶ月'
        if (text) text += '前'
        else text = '新築'
        return text
      }
    } else {
      return '-'
    }
  }
  // 交通情報
  setStations (stations) {
    if (stations) {
      this.stations.splice(0, this.stations.length)
      stations.forEach(one => {
        if (one.station && one.walking) {
          this.stations.push({
            station: one.station,
            walking: parseInt(one.walking)
          })
        }
      })
    }
  }
  // 業者
  get contactID () {
    return this.contact ? this.contact._id : this._contactID
  }

  get isApartment () {
    return (this.type === '1' || this.type === '3')
  }
  get isDetached () {
    return (this.type === '2')
  }

  toJSON () {
    return {
      _id: this.id,
      isPublishing: this.isPublishing,
      contactID: this.contactID,
      name: this.name,
      address: this.address,
      rent: this.rent,
      deposit: this.deposit,
      keyMoney: this.keyMoney,
      updateMoney: this.updateMoney,
      insurance: this.insurance,
      contractPeroid: this.contractPeroid,
      aspect: this.aspect,
      layout: this.layout,
      story: this.story,
      floor: this.floor,
      exclusiveArea: this.exclusiveArea,
      // buildingArea: this.buildingArea,
      // landArea: this.landArea,
      type: this.type,
      structure: this.structure,
      built: this.built,
      isNew: this.isNew,
      direction: this.direction,
      // households: this.households,
      manage: this.manage,
      // repair: this.repair,
      margin: this.margin,
      available: this.available,
      residentable: this.residentable,
      // rights: this.rights,
      // coverage: this.coverage,
      // volume: this.volume,
      // connection: this.connection,
      carParking: this.carParking,
      bikeParking: this.bikeParking,
      balcony: this.balcony,
      pet: this.pet,
      cityGas: this.cityGas,
      stations: this.stations,
      comment: this.comment,
      memo: this.memo,
      latitude: this.latitude,
      longitude: this.longitude,
      photos: this.photos,
      photoThumbnails: this.photoThumbnails,
      floorPlans: this.floorPlans,
      floorPlanThumbnails: this.floorPlanThumbnails,
      previews: this.previews,
      previewThumbnails: this.previewThumbnails
    }
  }
}

export default RentPiece



