<script>
import moment from 'moment'
// import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import bdsMdChips from '@/components/common/vue-material/bdsMdChips'
export default {
  props: ['manager'],
  components: {
    bdsMdChips
  },
  data: () => ({
    selections: [],
    stations: [],
    built: null,
    residentable: null
  }),
  created () {
    let that = this
    that.selections.splice(0, that.selections.length)
    for (let line in manager.const.station) {
      manager.const.station[line].forEach(station => {
        that.selections.push({value: line + '-' + station.sn, name: line + ' - ' + station.sn})
      })
    }
    manager.mySellPieceService.sellPiece.stations.forEach(one => {
      that.stations.push({station: [one.station], walking: one.walking})
    })
    this.addStation()
    this.built = manager.mySellPieceService.sellPiece.builtInputText
    this.residentable = manager.mySellPieceService.sellPiece._residentable ? manager.mySellPieceService.sellPiece._residentable.format('YYYY-MM-DD') : null
  },
  computed: {
    isNameValid () {
      if (manager.mySellPieceService.sellPiece.name === '') return false
      if (manager.mySellPieceService.sellPiece.name && manager.mySellPieceService.sellPiece.name.length > 20) return false
      return true
    },
    isAddressValid () {
      if (manager.mySellPieceService.sellPiece.address === '') return false
      if (manager.mySellPieceService.sellPiece.address && manager.mySellPieceService.sellPiece.address.length > 60) return false
      return true
    },
    isPriceValid () {
      if (manager.mySellPieceService.sellPiece.price === null) return true
      if (manager.mySellPieceService.sellPiece.price <= 0 || manager.mySellPieceService.sellPiece.price > 5000000000) return false
      return true
    },
    isStoryValid () {
      if (manager.mySellPieceService.sellPiece.story === null) return true
      if (manager.mySellPieceService.sellPiece.story <= 0 || manager.mySellPieceService.sellPiece.story > 500) return false
      return true
    },
    isFloorValid () {
      if (!manager.mySellPieceService.sellPiece.floor) return true
      if (manager.mySellPieceService.sellPiece.floor <= 0 || manager.mySellPieceService.sellPiece.floor > 500) return false
      return true
    },
    isExclusiveAreaValid () {
      if (!manager.mySellPieceService.sellPiece.exclusiveArea) return true
      if (manager.mySellPieceService.sellPiece.exclusiveArea <= 0 || manager.mySellPieceService.sellPiece.exclusiveArea > 9999) return false
      return true
    },
    isBuildingAreaValid () {
      if (!manager.mySellPieceService.sellPiece.buildingArea) return true
      if (manager.mySellPieceService.sellPiece.buildingArea <= 0 || manager.mySellPieceService.sellPiece.buildingArea > 9999) return false
      return true
    },
    isLandAreaValid () {
      if (!manager.mySellPieceService.sellPiece.landArea) return true
      if (manager.mySellPieceService.sellPiece.landArea <= 0 || manager.mySellPieceService.sellPiece.landArea > 9999) return false
      return true
    },
    isBuiltValid () {
      if (!this.built) return true
      if (this.built.length < 6) return false
      if (!moment(this.built, 'YYYYMM').isValid()) return false
      return true
    },
    isMarginValid () {
      if (!manager.mySellPieceService.sellPiece.margin) return true
      if (manager.mySellPieceService.sellPiece.margin && manager.mySellPieceService.sellPiece.margin.length > 20) return false
      return true
    },
    isConnectionValid () {
      if (!manager.mySellPieceService.sellPiece.connection) return true
      if (manager.mySellPieceService.sellPiece.connection && manager.mySellPieceService.sellPiece.connection.length > 60) return false
      return true
    },
    isHouseholdsValid () {
      if (!manager.mySellPieceService.sellPiece.households) return true
      if (manager.mySellPieceService.sellPiece.households <= 0 || manager.mySellPieceService.sellPiece.households > 9999) return false
      return true
    },
    isManageValid () {
      if (!manager.mySellPieceService.sellPiece.manage) return true
      if (manager.mySellPieceService.sellPiece.manage <= 0 || manager.mySellPieceService.sellPiece.manage > 999999) return false
      return true
    },
    isRepairValid () {
      if (!manager.mySellPieceService.sellPiece.repair) return true
      if (manager.mySellPieceService.sellPiece.repair <= 0 || manager.mySellPieceService.sellPiece.repair > 999999) return false
      return true
    },
    isCoverageValid () {
      if (!manager.mySellPieceService.sellPiece.coverage) return true
      if (manager.mySellPieceService.sellPiece.coverage <= 0 || manager.mySellPieceService.sellPiece.coverage > 9999) return false
      return true
    },
    isVolumeValid () {
      if (!manager.mySellPieceService.sellPiece.volume) return true
      if (manager.mySellPieceService.sellPiece.volume <= 0 || manager.mySellPieceService.sellPiece.volume > 9999) return false
      return true
    },
    isCommentValid () {
      if (!manager.mySellPieceService.sellPiece.comment) return true
      if (manager.mySellPieceService.sellPiece.comment && manager.mySellPieceService.sellPiece.comment.length > 60) return false
      return true
    },
    isMemoValid () {
      if (!manager.mySellPieceService.sellPiece.memo) return true
      if (manager.mySellPieceService.sellPiece.memo && manager.mySellPieceService.sellPiece.memo.length > 60) return false
      return true
    }
  },
  methods: {
    isWalkingValid (walking) {
      if (!walking) return true
      if (parseInt(walking) <= 0) return false
      return true
    },
    addStation () {
      this.stations.push({station: [], walking: null})
    },
    removeStation (index) {
      this.stations.splice(index, 1)
    },
    filterStation (input) {
      if (!input) return
      let selection = null
      this.selections.some(one => {
        const isAdded = this.selections.some(station => {
          if (station === one.value) return true
        })
        if (!isAdded && one.name.indexOf(input) >= 0) {
          selection = one.value
          return true
        }
      })
      return selection
    },
    validate () {
      if (!manager.mySellPieceService.sellPiece.name) {
        return 'タイトルは必須入力項目です。'
      }
      if (!manager.mySellPieceService.sellPiece.address) {
        return 'アドレスは必須入力項目です。'
      }
      if (!manager.mySellPieceService.sellPiece.price) {
        return '価格は必須入力項目です。'
      }
      if (!this.isNameValid || !this.isAddressValid || !this.isPriceValid || !this.isStoryValid || !this.isFloorValid || !this.isExclusiveAreaValid || !this.isBuildingAreaValid || !this.isLandAreaValid || !this.isBuiltValid || !this.isMarginValid || !this.isConnectionValid || !this.isCommentValid || !this.isMemoValid || !this.isHouseholdsValid || !this.isManageValid || !this.isRepairValid || !this.isCoverageValid || !this.isVolumeValid) {
        return '入力不正です。'
      }
      let stations = []
      for (let i = this.stations.length - 1; i >= 0; i--) {
        let one = this.stations[i]
        if (!one.station || one.station.length <= 0 || !one.walking) {
          this.stations.splice(i, 1)
        } else {
          stations.push({
            station: one.station[0],
            walking: parseInt(one.walking)
          })
        }
      }
      manager.mySellPieceService.sellPiece.setStations(stations)
      if (this.built !== null && this.built !== '') manager.mySellPieceService.sellPiece.built = this.built + '01'
      else manager.mySellPieceService.sellPiece.built = null
      manager.mySellPieceService.sellPiece.residentable = this.residentable
      // TODO: md bug
      if (manager.mySellPieceService.sellPiece._residentable && manager.mySellPieceService.sellPiece._residentable.year() === 1970) manager.mySellPieceService.sellPiece.residentable = null
      return null
    }
  }
}
</script>

<style scoped lang="scss">
.input-group {
  display: flex;
}
.input-left {
  width: calc(100% - 100px);
}
.input-right {
  width: 100px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.traffic {
  position: relative;
  padding: 10px;
  margin-bottom: 10px;
}
.remove-button {
  position: absolute;
  top: -5px;
  right: 0px;
}
.add-traffic {
  margin-bottom: 30px;
  display: flex;
  justify-content: center;
}
</style>



