<template>
<div class="navigation">
  <md-content class="footer-bar md-primary">
    <p class="md-title">© {{manager.homepageService.homepage.profile.name}}</p>
    <md-button md-theme="default" class="md-fab md-mini md-plain md-primary" @click="editName = true">
      <md-icon>edit</md-icon>
    </md-button>
  </md-content>
  <div class="links">
    <md-button class="md-accent" @click="manager.homepageService.switchPage('company')">会社概要</md-button>
    <div class="divider"></div>
    <md-button class="md-accent" @click="manager.homepageService.switchPage('contactUs')">お問い合せ</md-button>
  </div>

  <md-dialog-prompt
    md-theme="default"
    :md-active.sync="editName"
    v-model="manager.homepageService.homepage.profile.name"
    md-title="会社名を入力してください。"
    md-input-maxlength="30"
    md-input-placeholder="Type..."
    md-confirm-text="確定"
    md-cancel-text="キャンセル"
  />
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
export default {
  props: ['manager', 'config'],
  data: () => ({
    editName: false
  })
}
</script>

<style scoped lang="scss">
.navigation {
  margin-top: 40px;
}
.footer-bar {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  padding-right: 20px;
  .md-button {
    margin: 12px 8px;
  }
}
.links {
  padding: 20px;
  height: 140px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  .md-button {
    width: 160px
  }
}
.divider {
  height: 40px;
  margin: 0 10px;
  border-right: 1px solid #e8e8e8;
}
</style>



