<template>
<div>
  <p class="md-title">物件写真</p>
  <div class="md-layout md-alignment-left-top pictures" v-if="manager.mySellPieceService.sellPiece.photos && manager.mySellPieceService.sellPiece.photos.length > 0">
    <div class="md-layout-item md-size-10 md-small-size-20 md-xsmall-size-33 one-picture" v-for="(photo, index) in manager.mySellPieceService.sellPiece.photoThumbnails" :key="index">
      <div class="picture-frame">
        <img :class="{'deleted': isPhotoDeleted(index)}" :src="photo">
      </div>
      <div class="delete-action">
        <md-button class="delete-button md-accent" @click="deletePhoto(index)" v-if="!isPhotoDeleted(index)">削除</md-button>
        <md-button class="delete-button md-primary" @click="recoverPhoto(index)" v-else>復活</md-button>
      </div>
    </div>
  </div>
  <div class="md-layout divider">
    <div class="md-layout-item md-size-100">
      <uploadFiles ref="photoUploader" :manager="manager" acceptType="image" :maximum="validPhoto"/>
    </div>
  </div>
  <p class="md-title">間取り図</p>
  <div class="md-layout md-alignment-left-top pictures" v-if="manager.mySellPieceService.sellPiece.floorPlans && manager.mySellPieceService.sellPiece.floorPlans.length > 0">
    <div class="md-layout-item md-size-10 md-small-size-20 md-xsmall-size-33 one-picture" v-for="(floorPlan, index) in manager.mySellPieceService.sellPiece.floorPlanThumbnails" :key="index">
      <div class="picture-frame">
        <img :class="{'deleted': isFloorPlanDeleted(index)}" :src="floorPlan">
      </div>
      <div class="delete-action">
        <md-button class="delete-button md-accent" @click="deleteFloorPlan(index)" v-if="!isFloorPlanDeleted(index)">削除</md-button>
        <md-button class="delete-button md-primary" @click="recoverFloorPlan(index)" v-else>復活</md-button>
      </div>
    </div>
  </div>
  <div class="md-layout divider">
    <div class="md-layout-item md-size-100">
      <uploadFiles ref="floorPlanUploader" :manager="manager" acceptType="image" :maximum="validFloorPlan"/>
    </div>
  </div>
  <p class="md-title">360度プレビュー</p>
  <div class="md-layout md-alignment-left-top pictures" v-if="manager.mySellPieceService.sellPiece.previews && manager.mySellPieceService.sellPiece.previews.length > 0">
    <div class="md-layout-item md-size-10 md-small-size-20 md-xsmall-size-33 one-picture" v-for="(preview, index) in manager.mySellPieceService.sellPiece.previewThumbnails" :key="index">
      <div class="picture-frame">
        <img :class="{'deleted': isPreviewDeleted(index)}" :src="preview">
      </div>
      <div class="delete-action">
        <md-button class="delete-button md-accent" @click="deletePreview(index)" v-if="!isPreviewDeleted(index)">削除</md-button>
        <md-button class="delete-button md-primary" @click="recoverPreview(index)" v-else>復活</md-button>
      </div>
    </div>
  </div>
  <div class="md-layout divider">
    <div class="md-layout-item md-size-100">
      <uploadFiles ref="previewUploader" :manager="manager" acceptType="image" :maximum="validPreview"/>
    </div>
  </div>
</div>
</template>

<script>
import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import uploadFiles from '@/components/common/uploadFiles'
export default {
  props: ['manager'],
  components: {
    uploadFiles
  },
  data: () => ({
    maximum: 9,
    deletedPhotos: [],
    deletedFloorPlans: [],
    deletedPreviews: []
  }),
  computed: {
    validPhoto () {
      return this.maximum - manager.mySellPieceService.sellPiece.photos.length + this.deletedPhotos.length
    },
    validFloorPlan () {
      return this.maximum - manager.mySellPieceService.sellPiece.floorPlans.length + this.deletedFloorPlans.length
    },
    validPreview () {
      return this.maximum - manager.mySellPieceService.sellPiece.previews.length + this.deletedPreviews.length
    }
  },
  methods: {
    deletePhoto (index) {
      this.deletedPhotos.push(index)
    },
    isPhotoDeleted (index) {
      return this.deletedPhotos.some(one => {
        return one === index
      })
    },
    recoverPhoto (index) {
      for (let i = this.deletedPhotos.length - 1; i >= 0; i--) {
        if (this.deletedPhotos[i] === index) {
          if (this.$refs.photoUploader.files && this.$refs.photoUploader.files.length + (manager.mySellPieceService.sellPiece.photos.length - this.deletedPhotos.length) >= this.maximum) {
            utils.event.$emit('SHOW_MESSAGE', {code: 'B007', detail: this.maximum + 'ファイル以内にしてください。'})
          } else {
            this.deletedPhotos.splice(i, 1)
          }
          break
        }
      }
    },
    deleteFloorPlan (index) {
      this.deletedFloorPlans.push(index)
    },
    isFloorPlanDeleted (index) {
      return this.deletedFloorPlans.some(one => {
        return one === index
      })
    },
    recoverFloorPlan (index) {
      for (let i = this.deletedFloorPlans.length - 1; i >= 0; i--) {
        if (this.deletedFloorPlans[i] === index) {
          if (this.$refs.floorPlanUploader.files && this.$refs.floorPlanUploader.files.length + (manager.mySellPieceService.sellPiece.photos.length - this.deletedFloorPlans.length) >= this.maximum) {
            utils.event.$emit('SHOW_MESSAGE', {code: 'B007', detail: this.maximum + 'ファイル以内にしてください。'})
          } else {
            this.deletedFloorPlans.splice(i, 1)
          }
          break
        }
      }
    },
    deletePreview (index) {
      this.deletedPreviews.push(index)
    },
    isPreviewDeleted (index) {
      return this.deletedPreviews.some(one => {
        return one === index
      })
    },
    recoverPreview (index) {
      for (let i = this.deletedPreviews.length - 1; i >= 0; i--) {
        if (this.deletedPreviews[i] === index) {
          if (this.$refs.previewUploader.files && this.$refs.previewUploader.files.length + (manager.mySellPieceService.sellPiece.photos.length - this.deletedPreviews.length) >= this.maximum) {
            utils.event.$emit('SHOW_MESSAGE', {code: 'B007', detail: this.maximum + 'ファイル以内にしてください。'})
          } else {
            this.deletedPreviews.splice(i, 1)
          }
          break
        }
      }
    },
    upload (next) {
      const that = this
      // アップロード（エラーはuploaderが対応してくれる）
      that.$refs.photoUploader.upload((photos) => {
        that.$refs.floorPlanUploader.upload((floorPlans) => {
          that.$refs.previewUploader.upload((previews) => {
            // 削除対象を降順にソート
            that.deletedPhotos.sort((left, right) => {
              return (left < right ? 1 : -1)
            })
            that.deletedFloorPlans.sort((left, right) => {
              return (left < right ? 1 : -1)
            })
            that.deletedPreviews.sort((left, right) => {
              return (left < right ? 1 : -1)
            })

            // 対象画像を削除する（ファイル自体は削除していない）
            for (let i = 0; i < that.deletedPhotos.length; i++) {
              manager.mySellPieceService.sellPiece.photos.splice(that.deletedPhotos[i], 1)
              manager.mySellPieceService.sellPiece.photoThumbnails.splice(that.deletedPhotos[i], 1)
            }
            for (let i = 0; i < that.deletedFloorPlans.length; i++) {
              manager.mySellPieceService.sellPiece.floorPlans.splice(that.deletedFloorPlans[i], 1)
              manager.mySellPieceService.sellPiece.floorPlanThumbnails.splice(that.deletedFloorPlans[i], 1)
            }
            for (let i = 0; i < that.deletedPreviews.length; i++) {
              manager.mySellPieceService.sellPiece.previews.splice(that.deletedPreviews[i], 1)
              manager.mySellPieceService.sellPiece.previewThumbnails.splice(that.deletedPreviews[i], 1)
            }

            // 削除リストをクリア
            that.deletedPhotos.splice(0, that.deletedPhotos.length)
            that.deletedFloorPlans.splice(0, that.deletedFloorPlans.length)
            that.deletedPreviews.splice(0, that.deletedPreviews.length)

            // アップロード結果を保存
            if (photos && photos.length > 0) {
              photos.forEach(file => {
                manager.mySellPieceService.sellPiece.photos.push(file.file)
                if (file.thumbnail) {
                  manager.mySellPieceService.sellPiece.photoThumbnails.push(file.thumbnail)
                } else {
                  manager.mySellPieceService.sellPiece.photoThumbnails.push(file.file)
                }
              })
            }
            if (floorPlans && floorPlans.length > 0) {
              floorPlans.forEach(file => {
                manager.mySellPieceService.sellPiece.floorPlans.push(file.file)
                if (file.thumbnail) {
                  manager.mySellPieceService.sellPiece.floorPlanThumbnails.push(file.thumbnail)
                } else {
                  manager.mySellPieceService.sellPiece.floorPlanThumbnails.push(file.file)
                }
              })
            }
            if (previews && previews.length > 0) {
              previews.forEach(file => {
                manager.mySellPieceService.sellPiece.previews.push(file.file)
                if (file.thumbnail) {
                  manager.mySellPieceService.sellPiece.previewThumbnails.push(file.thumbnail)
                } else {
                  manager.mySellPieceService.sellPiece.previewThumbnails.push(file.file)
                }
              })
            }

            // 終了
            if (next) {
              next()
            }
          })
        })
      })
    }
  }
}
</script>

<style scoped lang="scss">
.one-picture {
  position: relative;
}
.picture-frame {
  margin: 10px 5px 0px 5px;
  height: calc(100% - 10px);
  min-height: 100px;
  background: #e8e8e8;
  .deleted {
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%);
    opacity: .7;
  }
}
.delete-action {
  margin-left: 5px;
  margin-right: 5px;
  width: calc(100% - 10px);
  height: 40px;
  position: absolute;
  bottom: 0px;
  background: rgba(0, 0, 0, .4);
  .delete-button {
    margin: 0;
    width: 100%;
    height: 100%;
    min-width: 40px;
    min-height: 40px;
  }
}
.pictures {
  border: 1px solid #e8e8e8;
  padding-bottom: 10px;
}
.divider {
  margin-top: 30px;
}
</style>



