class User {
  constructor (data) {
    this._id = ''
    this.password = ''
    this.isExam = null
    this.exam = null
    this._avatar = null
    this._logo = null
    this.icon = null
    this.nickname = ''
    this.company = ''
    this.homepage = ''
    this.homepages = []
    this.license = ''
    this.position = ''
    this.phone = ''
    this.fax = ''
    this.email = ''
    this.comment = ''
    this.address = ''
    this.role = '0' // 0: 匿名; 1: 一般人; 2: 仲介屋;
    this.registration = null
    this.card = null
    this.maxPublish = null
    if (data) this.login(data)
  }
  get avatar () {
    return this._avatar || '/static/avatar.png'
  }
  set avatar (data) {
    this._avatar = data || null
  }
  get logo () {
    return this._logo || '/static/noimage.png'
  }
  set logo (data) {
    this._logo = data || null
  }
  get isLogin () {
    return !(this._id === '')
  }
  get isAnonymous () {
    return this._id === '匿名'
  }
  get isOrdinary () {
    return this._id && this.role === '1'
  }
  get isMediator () {
    return this._id && this.role === '2'
  }
  get name () {
    if (this.nickname) return this.nickname
    else return this._id
  }
  login (data) {
    if (data._id) this._id = data._id
    if (data.password) this.password = data.password
    if (data.isExam) this.isExam = data.isExam
    if (data.exam) {
      data.exam.isExam = true
      this.exam = new User(data.exam)
    }
    if (data.avatar) this.avatar = data.avatar
    if (data.logo) this.logo = data.logo
    if (data.icon) this.icon = data.icon
    if (data.nickname) this.nickname = data.nickname
    if (data.company) this.company = data.company
    if (data.homepage) this.homepage = data.homepage
    if (data.homepages) this.homepages = data.homepages
    if (data.license) this.license = data.license
    if (data.position) this.position = data.position
    if (data.phone) this.phone = data.phone
    if (data.fax) this.fax = data.fax
    if (data.email) this.email = data.email
    if (data.comment) this.comment = data.comment
    if (data.address) this.address = data.address
    if (data.role) this.role = data.role
    if (data.registration) this.registration = data.registration
    if (data.card) this.card = data.card
    if (data.maxPublish) this.maxPublish = data.maxPublish
  }
  logout () {
    this._id = '匿名'
    this.password = ''
    this.isExam = null
    this.exam = null
    this.avatar = null
    this.logo = null
    this.icon = null
    this.nickname = ''
    this.company = ''
    this.homepage = ''
    this.homepages = []
    this.license = ''
    this.position = ''
    this.phone = ''
    this.fax = ''
    this.email = ''
    this.comment = ''
    this.address = ''
    this.role = '0'
    this.registration = null
    this.card = null
    this.maxPublish = null
  }
  toJSON () {
    if (this.role === '0') return null
    return {
      _id: this._id,
      password: this.password,
      exam: this.exam ? this.exam.toJSON() : null,
      avatar: this._avatar,
      logo: this._logo,
      icon: this.icon,
      nickname: this.nickname,
      company: this.company,
      homepage: this.homepage,
      homepages: this.homepages,
      license: this.license,
      position: this.position,
      phone: this.phone,
      fax: this.fax,
      email: this.email,
      comment: this.comment,
      address: this.address,
      role: this.role,
      registration: this.registration,
      card: this.card,
      maxPublish: this.maxPublish
    }
  }
}

export default new User()



