<template>
<div class="app-content-auto-height" v-if="manager.estateSellPieceService.sellPiece">
  <vue-headful title="ブドウさん - 物件詳細"/>
  <md-button class="md-icon-button md-fab md-raised md-accent share" @click="share"><md-icon>share</md-icon></md-button>
  <md-button class="md-icon-button md-fab md-raised md-accent contact-info" @click="showContact"><md-icon>account_box</md-icon></md-button>
  <sellPieceDetailInfo :manager="manager" :piece="manager.estateSellPieceService.sellPiece"/>
</div>
</template>

<script>
import manager from '@/store/manager.js'
import utils from '@/tool/utils.js'
import sellPieceDetailInfo from './sellPieceDetailInfo'
export default {
  props: ['manager'],
  components: {
    sellPieceDetailInfo
  },
  mounted () {
    utils.event.$on('OPEN_PIECE_DETAIL', (index) => {
      let url = location.protocol + '//' + location.host + '/sellPiecePreview/' + manager.estateSellPieceService.sellPiece._id + '/' + index
      utils.openInNewTab(url)
    })
  },
  beforeDestroy () {
    utils.event.$off('OPEN_PIECE_DETAIL')
    manager.estateSellPieceService.sellPiece = null
  },
  methods: {
    share () {
      utils.copyToPasteboard(location.protocol + '//' + location.host + '/sellPieceDetail/' + manager.estateSellPieceService.sellPiece._id)
      utils.event.$emit('SHOW_MESSAGE', {code: 'I008'})
    },
    showContact () {
      if (manager.estateSellPieceService.sellPiece.contact) utils.event.$emit('SHOW_DETAIL', 'contactCard', manager.estateSellPieceService.sellPiece.contact)
    }
  }
}
</script>

<style scoped lang="scss">
.share {
  position: absolute;
  top: 146px;
  right: 28px;
}
.contact-info {
  position: absolute;
  top: 210px;
  right: 28px;
}
</style>



