<template>
<div class="component-wrapper">
  <div class="info-title">
    <md-content class="title-bar md-primary"></md-content><p class="md-title">賃貸物件 ({{manager.homepageService.dummyRentPieces.length}} / {{manager.homepageService.dummyRentPieces.length}})</p>
    <md-radio class="bd-small-hide option" v-model="type" value="1">マンション</md-radio>
    <md-radio class="bd-small-hide option" v-model="type" value="3">アパート</md-radio>
    <md-radio class="bd-small-hide option" v-model="type" value="2">一戸建</md-radio>
  </div>
  <div class="bd-small-show">
    <md-radio class="option" v-model="type" value="1">マンション</md-radio>
    <md-radio class="option" v-model="type" value="3">アパート</md-radio>
    <md-radio class="option" v-model="type" value="2">一戸建</md-radio>
  </div>
  <div class="md-layout md-gutter">
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100" v-for="piece in manager.homepageService.dummyRentPieces" :key="piece._id">
      <rentPieceCard :manager="manager" :object="piece" :design="config.design"/>
    </div>
  </div>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
// import manager from '@/store/manager.js'
import rentPieceCard from '../shared/rentPieceCard'
export default {
  props: ['manager', 'config'],
  components: {
    rentPieceCard
  },
  data: () => ({
    type: '1'
  })
}
</script>

<style scoped lang="scss">
.info-title {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  margin-bottom: 10px;
}
.option {
  margin-left: 20px;
}
.title-bar {
  width: 5px;
  height: 30px;
  border-radius: 1px;
  margin-right: 10px;
}
.md-title {
  width: 240px;
  margin: 10px 10px 10px 0px;
}
.md-layout {
  margin-top: 20px;
  min-height: 368px;
}
.md-layout-item {
  margin-bottom: 20px;
}
</style>



