import authority from './authority.json'
import aspect from './aspect.json'
import available from './available.json'
import availableRent from './availableRent.json'
import direction from './direction.json'
import layout from './layout.json'
import message from './message.json'
import rights from './rights.json'
import station from './station.json'
import structure from './structure.json'
import type from './type.json'
import typeRent from './typeRent.json'
import emotion from './emotion.json'
import template from './template.json'
import service from './service.json'

class Const {
  constructor () {
    this.authority = authority
    this.aspect = aspect
    this.available = available
    this.availableRent = availableRent
    this.direction = direction
    this.layout = layout
    this.message = message
    this.rights = rights
    this.station = station
    this.structure = structure
    this.type = type
    this.typeRent = typeRent
    this.emotion = emotion
    this.template = template
    this.service = service
  }
}

export default new Const()



