<template>
<div class="component-wrapper">
  <div class="info-title">
    <md-content class="title-bar md-primary"></md-content><p class="md-title">トピックス</p>
  </div>
  <md-content class="info-center md-scrollbar">
    <infoCard :manager="manager" :object="info" v-for="info in manager.homepageService.dummyInfos" :key="info._id"/>
  </md-content>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
import infoCard from '../shared/infoCard'
export default {
  props: ['manager', 'config'],
  components: {
    infoCard
  }
}
</script>

<style scoped lang="scss">
.info-title {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  margin-bottom: 10px;
}
.title-bar {
  width: 5px;
  height: 30px;
  border-radius: 1px;
  margin-right: 10px;
}
.info-center {
  height: 350px;
  overflow: auto;
  width: 100%;
  padding: 5px;
}
.info-card + .info-card {
  margin-top: 10px;
}
</style>



