let router = new Router({
  mode: 'history',
  routes: [
    {path: '/loading', name: 'loading', component: loading},
    {path: '/login', name: 'login', component: login},
    {path: '/register', name: 'register', component: register},
    {path: '/error', name: 'error', component: error},
    {
      path: '/',
      component: main,
      children: [
        {path: '/', name: 'home', component: home, meta: {requireAuth: true}},
        // {
        //   path: '/estate',
        //   name: 'estate',
        //   component: estate,
        //   children: [
        //     {path: '/estate/sellPiece', name: 'sellPiece', component: sellPiece, beforeEnter: manager.estateSellPieceService.loadSellPieces},
        //     {path: '/estate/buyRequest', name: 'buyRequest', component: buyRequest, beforeEnter: manager.estateBuyRequestService.loadBuyRequest},
        //     {path: '/estate/rentPiece', name: 'rentPiece', component: rentPiece, beforeEnter: manager.estateRentPieceService.loadRentPiece},
        //     {path: '/estate/borrowRequest', name: 'borrowRequest', component: borrowRequest, beforeEnter: manager.estateBorrowRequestService.loadBorrowRequest}
        //   ],
        //   meta: {requireAuth: true}
        // },
        // {path: '/chat', name: 'chat', component: chat, beforeEnter: loadChat, meta: {requireAuth: true}},
        // {path: '/contact', name: 'contact', component: contact, meta: {requireAuth: true}},
        {
          path: '/my',
          component: my,
          // router.push({name: 'my'}) would direct to 'my' component, so it is not good to give a name to 'my' component.
          // name: 'my',
          children: [
            {path: '/my', name: 'account', component: account, meta: {requireAuth: true}},
            {path: '/my/profile', name: 'profile', component: profile, meta: {requireAuth: true}},
            // {path: '/my/resource', name: 'resource', component: resource, meta: {requireAuth: true}},
            {path: '/my/apply', name: 'apply', component: apply, meta: {requireAuth: true}},
            {path: '/my/sell', name: 'mySell', component: mySell, beforeEnter: manager.mySellPieceService.loadSellPieces, meta: {requireAuth: true}},
            // {path: '/my/buy', name: 'myBuy', component: myBuy, beforeEnter: manager.myBuyRequestService.loadMyBuy, meta: {requireAuth: true}},
            {path: '/my/rent', name: 'myRent', component: myRent, beforeEnter: manager.myRentPieceService.loadRentPieces, meta: {requireAuth: true}},
            // {path: '/my/borrow', name: 'myBorrow', component: myBorrow, beforeEnter: manager.myBorrowRequestService.loadMyBorrow, meta: {requireAuth: true}},
            {path: '/my/sellPiece/:id', name: 'mySellPiece', component: mySellPiece, beforeEnter: manager.mySellPieceService.loadSellPieceForEdit, meta: {requireAuth: true}},
            {path: '/my/sellPieceDetail/:id', name: 'mySellPieceDetail', component: mySellPieceDetail, beforeEnter: manager.mySellPieceService.loadSellPieceForDetail, meta: {requireAuth: true}},
            // {path: '/my/buyRequest/:id', name: 'myBuyRequest', component: myBuyRequest, beforeEnter: manager.myBuyRequestService.loadMyBuyRequest, meta: {requireAuth: true}},
            {path: '/my/rentPiece/:id', name: 'myRentPiece', component: myRentPiece, beforeEnter: manager.myRentPieceService.loadRentPieceForEdit, meta: {requireAuth: true}},
            {path: '/my/rentPieceDetail/:id', name: 'myRentPieceDetail', component: myRentPieceDetail, beforeEnter: manager.myRentPieceService.loadRentPieceForDetail, meta: {requireAuth: true}},
            // {path: '/my/borrowRequest/:id', name: 'myBorrowRequest', component: myBorrowRequest, beforeEnter: manager.myBorrowRequestService.loadMyBorrowRequest, meta: {requireAuth: true}}
            {path: '/my/homepage', name: 'homepage', component: homepage, beforeEnter: manager.homepageService.loadHomepages, meta: {requireAuth: true}}
            // {path: '/my/homepageSetting/:id', name: 'homepageSetting', component: homepageSetting, beforeEnter: manager.homepageService.loadHomePageSetting, meta: {requireAuth: true}}
          ]
          // '/my'(path) is 'account'(component) so 'my'(component) need not(can not) check auth.
          // , meta: {requireAuth: true}
        }
      ]
    },
    {
      path: '/sub',
      component: sub,
      children: [
        {path: '/sellPieceDetail/:id', name: 'sellPieceDetail', component: sellPieceDetail, beforeEnter: manager.estateSellPieceService.loadSellPieceForDetail, meta: {requireAuth: true}},
        {path: '/sellPiecePreview/:id/:index', name: 'sellPiecePreview', component: sellPiecePreview, beforeEnter: manager.estateSellPieceService.loadSellPieceForPreview, meta: {requireAuth: true}},
        {path: '/my/sellPiecePreview/:id/:index', name: 'mySellPiecePreview', component: mySellPiecePreview, beforeEnter: manager.mySellPieceService.loadSellPieceForPreview, meta: {requireAuth: true}},
        {path: '/rentPieceDetail/:id', name: 'rentPieceDetail', component: rentPieceDetail, beforeEnter: manager.estateRentPieceService.loadRentPieceForDetail, meta: {requireAuth: true}},
        {path: '/rentPiecePreview/:id/:index', name: 'rentPiecePreview', component: rentPiecePreview, beforeEnter: manager.estateRentPieceService.loadRentPieceForPreview, meta: {requireAuth: true}},
        {path: '/my/rentPiecePreview/:id/:index', name: 'myRentPiecePreview', component: myRentPiecePreview, beforeEnter: manager.myRentPieceService.loadRentPieceForPreview, meta: {requireAuth: true}}
        // {path: '/sharepage/:id', name: 'sharepage', component: sharepage, meta: {requireAuth: true}},
        // {path: '/page/:type/:id', name: 'page', component: page, meta: {requireAuth: true}},
      ],
      meta: {requireAuth: true}
    },
    {path: '*', redirect: '/'}
  ]
})

router.beforeEach((to, from, next) => {
  const requireAuth = to.matched.some(record => record.meta.requireAuth)
  if (requireAuth) {
    if (!manager.checkAuth(to)) {
      return
    }
  }
  next()
})

export default router



