<template>
<div ref="animate" class="component-wrapper">
  <div class="info-title">
    <md-content class="title-bar md-primary"></md-content><p class="md-title">トピックス</p>
    <md-button class="md-accent more" @click="switchPage()">
      more
    </md-button>
  </div>
  <div class="component-wrapper">
    <infoLable :manager="manager" :object="info" v-for="(info, index) in manager.homepageService.dummyInfos" :key="info._id" v-if="index < 3"/>
  </div>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
import infoLable from '../shared/infoLable'
export default {
  props: ['manager', 'config'],
  components: {
    infoLable
  },
  methods: {
    switchPage () {
      this.manager.homepageService.page = {
        topComponents: [
          {
            id: 'cover',
            photo: '/static/resources/c1.jpg',
            vision: false
          },
          {
            id: 'topics'
          }
        ],
        leftComponents: [],
        rightComponents: [],
        bottomComponents: [
          {
            id: 'navigation'
          }
        ]
      }
    }
  }
}
</script>

<style scoped lang="scss">
.info-title {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  margin-bottom: 10px;
}
.title-bar {
  width: 5px;
  height: 30px;
  border-radius: 1px;
  margin-right: 10px;
}
.more {
  position: absolute;
  right: -8px;
  top: 12px;
}
</style>



