<template>
<div class="component-wrapper">
  <div class="info-title">
    <md-content class="title-bar md-primary"></md-content><p class="md-title">サービス</p>
    <md-button md-theme="default" class="md-fab md-mini md-plain add-service md-primary" @click="addService = true">
      <md-icon>add</md-icon>
    </md-button>
  </div>
  <div class="md-layout">
    <div class="md-layout-item md-size-50 md-small-size-100" v-for="(service, index) in manager.homepageService.homepage.services" :key="index">
      <serviceCard :manager="manager" :object="service" :edit="true"/>
    </div>
  </div>

  <md-dialog :md-active.sync="addService">
    <md-content class="service-list md-scrollbar">
      <div class="md-layout md-gutter" v-if="hasNew">
        <div class="md-layout-item md-size-50 md-medium-size-100" v-for="(service, index) in manager.const.service" :key="index" v-if="!isAdded(service)">
          <serviceCard :manager="manager" :object="service" :edit="false"/>
        </div>
      </div>
      <div v-else>追加できるサービスはこれ以上にありません。</div>
    </md-content>
    <md-dialog-actions>
      <md-button md-theme="default" class="md-primary" @click="addService = false">閉じる</md-button>
    </md-dialog-actions>
  </md-dialog>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import serviceCard from '../shared/serviceCard'
export default {
  props: ['manager', 'config'],
  components: {
    serviceCard
  },
  data: () => ({
    addService: false
  }),
  computed: {
    hasNew () {
      if (manager.homepageService.homepage.services) {
        let count = 0
        for (let service in manager.const.service) { // eslint-disable-line
          count++
        }
        if (count === manager.homepageService.homepage.services.length) return false
      }
      return true
    }
  },
  methods: {
    isAdded (service) {
      if (manager.homepageService.homepage.services) {
        return manager.homepageService.homepage.services.some(one => {
          return one.title === service.title
        })
      }
      return false
    }
  }
}
</script>

<style scoped lang="scss">
.info-title {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  margin-bottom: 10px;
}
.title-bar {
  width: 5px;
  height: 30px;
  border-radius: 1px;
  margin-right: 10px;
}
.md-layout-item {
  margin-bottom: 10px;
}
.service-list {
  padding: 20px;
  height: 100%;
  overflow: auto;
}
.add-service {
  position: absolute;
  top: 0px;
  right: 0px;
}
</style>



