<template>
<div>
  <vue-headful title="ブドウさん - ホームページ"/>
  <div class="md-layout md-gutter md-alignment-center-center">
    <div class="md-layout-item md-size-66 md-xsmall-size-100">
      <md-field :class="{'md-invalid': !isDomainValid}">
        <label>ドメイン</label>
        <md-input v-model="manager.homepageService.homepage.domain" type="text" required></md-input>
        <md-button class="md-icon-button inline-action" @click="multiDomain = true"><md-icon>more_vert</md-icon></md-button>
        <span class="md-helper-text">例：www.sample.com(http抜き)</span>
        <span class="md-error">http抜き、50文字以内</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-33 md-xsmall-size-100 action">
      <md-menu md-size="medium" md-align-trigger>
        <md-button md-menu-trigger :md-theme="manager.homepageService.homepage.theme" class="md-primary md-raised color-sample">
          <span class="color-text">主体色</span>
          <md-chip :md-theme="manager.homepageService.homepage.theme" class="md-accent">強調色</md-chip>
          <md-icon>arrow_drop_down</md-icon>
        </md-button>
        <md-menu-content>
          <md-menu-item @click="changeTheme('theme01')">
            <md-content md-theme="theme01" class="md-primary color-sample">
              <strong class="color-text">主体色</strong>
              <md-chip md-theme="theme01" class="md-accent"><strong>強調色</strong></md-chip>
            </md-content>
          </md-menu-item>
          <md-menu-item @click="changeTheme('theme02')">
            <md-content md-theme="theme02" class="md-primary color-sample">
              <strong class="color-text">主体色</strong>
              <md-chip md-theme="theme02" class="md-accent"><strong>強調色</strong></md-chip>
            </md-content>
          </md-menu-item>
          <md-menu-item @click="changeTheme('theme03')">
            <md-content md-theme="theme03" class="md-primary color-sample">
              <strong class="color-text">主体色</strong>
              <md-chip md-theme="theme03" class="md-accent"><strong>強調色</strong></md-chip>
            </md-content>
          </md-menu-item>
        </md-menu-content>
      </md-menu>
    </div>
  </div>
  <templates :manager="manager" v-if="manager.homepageService.showTemplate"/>
  <md-button class="md-fab md-mini md-plain md-accent switch-template" @click="manager.homepageService.showTemplate = true" v-if="!manager.homepageService.showTemplate">
    <md-icon>dashboard</md-icon>
  </md-button>
  <md-button class="md-fab md-mini md-plain md-accent save-homepage" @click="save" v-if="!manager.homepageService.showTemplate">
    <md-icon>save</md-icon>
  </md-button>
  <preview :manager="manager" v-if="!manager.homepageService.showTemplate"/>

  <md-dialog md-theme="default" :md-active.sync="multiDomain">
    <md-content class="domains md-scrollbar">
      <div class="domain-row" v-for="(homepage, index) in manager.homepageService.homepages" :key="'homepage-' + index">
        <md-radio v-model="selected" :value="index" class="md-primary domain-select">{{homepage.domain || '未指定'}}</md-radio>
        <md-button class="md-icon-button md-primary domain-delete" @click="deleteDomain(homepage)" :disabled="index === selected">
          <md-icon>delete_forever</md-icon>
        </md-button>
      </div>
    </md-content>
    <md-dialog-actions>
      <md-button class="md-primary" @click="addHomepage" :disabled="hasNewDomain">追加</md-button>
      <md-button class="md-primary" @click="multiDomain = false">キャンセル</md-button>
    </md-dialog-actions>
  </md-dialog>
</div>
</template>

<script>
import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import templates from './templates'
import preview from './preview'
import Homepage from '@/store/models/homepage.js'
export default {
  props: ['manager'],
  components: {
    templates,
    preview
  },
  data: () => ({
    multiDomain: false,
    selected: 0
  }),
  computed: {
    isDomainValid () {
      if (!manager.homepageService.homepage.domain) return false
      manager.homepageService.homepage.domain = manager.homepageService.homepage.domain.toLowerCase()
      if (manager.homepageService.homepage.domain.indexOf('http') === 0) return false
      return manager.homepageService.homepage.domain.length <= 50
    },
    hasNewDomain () {
      return manager.homepageService.homepages.some(one => {
        return one._id == null
      })
    }
  },
  watch: {
    selected () {
      manager.homepageService.homepage = manager.homepageService.homepages[this.selected]
      if (!manager.homepageService.homepage.pages || manager.homepageService.homepage.pages.length <= 0) {
        manager.homepageService.showTemplate = true
        manager.homepageService.page = null
      } else {
        manager.homepageService.showTemplate = false
        manager.homepageService.page = manager.homepageService.homepage.pages[0]
      }
    }
  },
  methods: {
    addHomepage () {
      let homepage = new Homepage(manager.homepageService.homepages[0] || {})
      homepage._id = null
      homepage.domain = null
      homepage.theme = 'theme01'
      homepage.pages = null
      manager.homepageService.homepages.unshift(homepage)
      if (this.selected !== 0) {
        this.selected = 0
      } else {
        manager.homepageService.homepage = homepage
        manager.homepageService.showTemplate = true
        manager.homepageService.page = null
      }
    },
    deleteDomain (homepage) {
      const that = this
      if (!homepage._id) {
        for (let i = 0; i < manager.homepageService.homepages.length; i++) {
          if (!manager.homepageService.homepages[i]._id) {
            manager.homepageService.homepages.splice(i, 1)
            if (i < that.selected) that.selected--
            break
          }
        }
      } else {
        utils.event.$emit('SHOW_MESSAGE', {code: 'I011', detail: homepage.domain}, () => {
          manager.homepageService.deleteHomepage(homepage, () => {
            for (let i = 0; i < manager.homepageService.homepages.length; i++) {
              if (manager.homepageService.homepages[i]._id === homepage._id) {
                manager.homepageService.homepages.splice(i, 1)
                if (i < that.selected) that.selected--
                break
              }
            }
            utils.event.$emit('SHOW_MESSAGE', {code: 'I007'})
          })
        })
      }
    },
    changeTheme (theme) {
      manager.homepageService.homepage.theme = theme
    },
    save () {
      if (!this.isDomainValid || manager.homepageService.homepage.isDuplicate) {
        utils.event.$emit('SHOW_MESSAGE', {code: 'B011'})
        return
      }
      manager.homepageService.updateHomepage(() => {
        utils.event.$emit('SHOW_MESSAGE', {code: 'I002'})
      })
    }
  }
}
</script>

<style scoped lang="scss">
.md-layout-item {
  margin-bottom: 20px;
}
.inline-action {
  position: absolute;
  top: 8px;
  right: 0px;
}
.action {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.color-sample {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;
  font-weight: 500 !important;
  .color-text {
    padding: 2px 6px 0 5px;
    font-size: 13px;
  }
}
.md-chip {
  cursor: pointer;
}
.switch-template {
  position: absolute;
  top: 158px;
  right: 1px;
}
.save-homepage {
  position: absolute;
  top: 208px;
  right: 1px;
}
.domains {
  width: 100%;
  height: 100%;
  overflow: auto;
}
.domain-row {
  display: flex;
  align-items: center;
  border-bottom: solid 1px #e8e8e8;
  .domain-select {
    padding-left: 16px;
    display: flex;
    align-items: center;
    margin: 0px;
    height: 52px;
    width: calc(100% - 52px);
  }
}
.domain-row:hover {
  background: #e8e8e8;
}
</style>



