<template>
<div>
  <vue-headful title="ブドウさん - アカウント"/>
  <div class="md-layout md-gutter md-alignment-top">
    <div class="md-layout-item md-size-30 md-medium-size-40 md-small-size-100">
      <div class="row">
        <contactCard :manager="manager" :object="manager.user"/>
      </div>
      <div class="row">
        <md-button class="md-primary md-raised action-button" to="/my/profile">
          <div class="icon-text">
            <md-icon>edit</md-icon><p>編集</p>
          </div>
        </md-button>
        <md-button class="md-primary md-raised action-button" @click="logout()">
          <div class="icon-text">
            <md-icon>power_settings_new</md-icon><p>ログアウト</p>
          </div>
        </md-button>
      </div>
    </div>
    <div class="md-layout-item md-size-70 md-medium-size-60 md-small-size-100">
      <p class="md-title">ホームページ作成</p>
      <md-divider></md-divider>
      <p class="md-body-1">まだホームページを作成する為に何十万円も何ヶ月間もかかっていますか？ブドウさんでは、<strong>たったの2ステップ</strong>でホームページを<strong>瞬時に</strong>作成する機能を提供しております。使ってみてはいかがでしょうか？</p>
      <p class="md-body-1">Step1. テンプレート及び配色を<strong>選ぶだけ</strong>でホームページの土台が出来上がります。テンプレート毎の特徴も丁寧に説明しておりますので、お選びの時にぜひ参考にしてください。</p>
      <p class="md-body-1">Step2. ホームページに表示する為の必要な文字情報を入力していただきます。すぐに使えるサンプルも予め用意してありますので、<strong>入力が面倒であれば、サンプルそのままでも使えます。</strong></p>
      <p class="md-body-1">保存ボタンを押したらすぐにでもブドウさんの中でホームページのプレビューをすることができます。<strong>専門知識は一切不要で、最短時間はなんと5秒もかかりません！</strong></p>
      <div class="actions"><md-button class="md-primary md-raised" to="/my/homepage">作成</md-button></div>
      <p class="md-title">ホームページ公開</p>
      <md-divider></md-divider>
      <p class="md-body-1">御社の現状によって柔軟に対応できます。ブドウさんが代理でdomainの申請・設定を行えます。最初の一度だけ、ネット環境を整う為に必要な時間（状況によって数時間〜数営業日）を待っていただきます。</p>
      <p class="md-body-1">パターン1. すでにホームページをお持ちの場合、御社のdomainをブドウさんの提供しているホームページに紐付けをすることができます。もしくは既存ホームページを残しながら、その中の一つのsub domainだけをブドウさんの提供しているホームページに紐付ける策もあります。（例：http://www.〇〇株式会社.com/services）他にもいろいろ対策が考えられますので、お気軽にご相談ください。</p>
      <p class="md-body-1">パターン2. 何もお持ちでない場合、新たにdomainを申請して、ブドウさんの提供しているホームページに紐付けをすることができます。</p>
      <div class="actions"><md-button class="md-primary md-raised">お問合せ</md-button></div>
      <p class="md-title">ホームページ更新</p>
      <md-divider></md-divider>
      <p class="md-body-1">ホームページに書いてある文字を変更したい！ホームページのデザインを刷新したい！とかの要望はどの会社だってきっとあるでしょう。いつも製作会社に工数を見積もってもらって、たくさんのお金を出して、必要な資料を作ってあげて、数営業日を待って、ようやく成果が見えたのに、『えっ！？ここは約束したのと違うでしょう！？』とか、あったりしませんか？</p>
      <p class="md-body-1">ブドウさんをお使いになる場合、ホームページのデザイン変更・文字修正は、ホームページ作成時と同様、いつでもご自身で簡単に行えます。反映はもちろんリアルタイムです。</p>
      <div class="actions"><md-button class="md-primary md-raised">更新</md-button></div>
      <p class="md-title">物件をホームページに掲載</p>
      <md-divider></md-divider>
      <p class="md-body-1">会社の持っている物件の情報をホームページに載せたい？膨大な金額で管理システムでも使ったり、月何万円のサービスを買ったりしていますよね？</p>
      <p class="md-body-1">ブドウさんをお使いになる場合、物件をブドウさんに登録していただき、『HPへ公開』をするだけで、御社のホームページに載せることができます。もちろんリアルタイムです。</p>
      <div class="actions"><md-button class="md-primary md-raised">売買物件管理</md-button><md-button class="md-primary md-raised">賃貸物件管理</md-button></div>
      <p class="md-title">ホームページのカスタマイズ</p>
      <md-divider></md-divider>
      <p class="md-body-1">同じくブドウさんをお使いになっている他社のホームページと差別化したいから、細かいカスタマイズをしたい！という要望もきっとあります。</p>
      <p class="md-body-1">ブドウさんでは可視なホームページカスタマイズツールを提供しております。画面でドラッグ・ドロップしながら、個性的なホームページに生まれ変わります。反映はもちろんリアルタイムです。</p>
      <div class="actions"><md-button class="md-primary md-raised">カスタマイズ</md-button></div>
    </div>
  </div>
</div>
</template>

<script>
import utils from '@/tool/utils.js'
// import manager from '@/store/manager.js'
import contactCard from '@/components/shared/contactCard'
export default {
  props: ['manager'],
  components: {
    contactCard
  },
  methods: {
    logout () {
      utils.restGet('/logout').then(
        response => {
          if (response) {
            location.href = location.protocol + '//' + location.host + '/'
          }
        }
      )
    }
  }
}
</script>

<style scoped lang="scss">
.row {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin-bottom: 20px;
}
.action-button {
  width: 150px;
}
.actions {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}
</style>



