import utils from '@/tool/utils.js'
import RentPiece from '@/store/models/rentPiece.js'

class EstateRentPieceService {
  constructor () {
    this._rentPieces = []
    this._count = 0
    this._page = 0
    this._rentPiece = null
    this.searchCondition = {
      stations: [],
      walking: '',
      layouts: '',
      area: '',
      type: '',
      structure: '',
      age: '',
      min: '',
      max: '',
      yearsBefore: ''
    }
  }
  get rentPieces () {
    return this._rentPieces
  }
  set rentPieces (pieces) {
    if (pieces) {
      pieces.forEach(one => {
        // 重複を削除
        this._rentPieces.filter(rentPiece => {
          return rentPiece._id === one._id
        })
        // リストに追加
        this._rentPieces.push(new RentPiece(one))
      })
    }
  }
  get count () {
    return this._count
  }
  set count (data) {
    this._count = parseInt(data)
  }
  get page () {
    return this._page
  }
  set page (data) {
    this._page = parseInt(data)
  }
  get rentPiece () {
    return this._rentPiece
  }
  set rentPiece (piece) {
    if (piece) {
      this._rentPiece = new RentPiece(piece)
    } else {
      this._rentPiece = null
    }
  }

  init () {
    this._rentPieces.splice(0, this._rentPieces.length)
    this.count = 0
    this.page = 0
    this.rentPiece = null
  }

  findPieces (filter, page, next) {
    let that = this
    utils.restGet('/public/findRentPieces', {filter: filter, page: page}).then(
      response => {
        if (response) {
          that.count = response.count
          that.rentPieces = response.datas
          if (next) next()
        }
      }
    )
  }

  loadRentPieces = (to, from, next) => {
    this.init()
    this.findPieces(null, null, next)
  }

  moreRentPieces (next) {
    let that = this
    this.findPieces(null, that.page + 1, () => {
      that.page += 1
      if (next) next()
    })
  }

  loadRentPieceForEdit = (to, from, next) => {
    let that = this
    const reg = /[0-9A-Fa-f]{24}/g
    if (to.params.id && to.params.id !== 'new' && reg.test(to.params.id)) {
      that.findPieces({_id: to.params.id}, null, () => {
        if (that.rentPieces.length > 0) {
          that._rentPiece = that.rentPieces[0]
          if (next) next()
        } else {
          utils.event.$emit('SHOW_MESSAGE', {code: 'B001', detail: '対象物件を参照する権限がないか、対象物件が削除されたか、いくつかの原因が考えられます。'}, () => {
            utils.router.push({path: '/'})
          })
        }
      })
    } else if (to.params.id && to.params.id === 'new') {
      that.rentPiece = {}
      if (next) next()
    } else {
      utils.event.$emit('SHOW_MESSAGE', {code: 'B009', detail: '物件IDが間違っています。'}, () => {
        utils.router.push({path: '/'})
      })
    }
  }

  loadRentPieceForDetail = (to, from, next) => {
    let that = this
    const reg = /[0-9A-Fa-f]{24}/g
    if (to.params.id && reg.test(to.params.id)) {
      that.findPieces({_id: to.params.id}, null, () => {
        if (that.rentPieces.length > 0) {
          that._rentPiece = that.rentPieces[0]
          if (next) next()
        } else {
          utils.event.$emit('SHOW_MESSAGE', {code: 'B001', detail: '対象物件を参照する権限がないか、対象物件が削除されたか、いくつかの原因が考えられます。'}, () => {
            utils.router.push({path: '/'})
          })
        }
      })
    } else {
      utils.event.$emit('SHOW_MESSAGE', {code: 'B009', detail: '物件IDが間違っています。'}, () => {
        utils.router.push({path: '/'})
      })
    }
  }

  loadRentPieceForPreview = (to, from, next) => {
    let that = this
    const reg = /[0-9A-Fa-f]{24}/g
    if (to.params.id && reg.test(to.params.id)) {
      that.findPieces({_id: to.params.id}, null, () => {
        if (that.rentPieces.length > 0) {
          that._rentPiece = that.rentPieces[0]
          if (parseInt(to.params.index) >= 0 && parseInt(to.params.index) < that.rentPiece.previews.length) {
            if (next) next()
          } else {
            utils.event.$emit('SHOW_MESSAGE', {code: 'B001'}, () => {
              utils.router.push({path: '/'})
            })
          }
        } else {
          utils.event.$emit('SHOW_MESSAGE', {code: 'B001', detail: '対象物件を参照する権限がないか、対象物件が削除されたか、いくつかの原因が考えられます。'}, () => {
            utils.router.push({path: '/'})
          })
        }
      })
    } else {
      utils.event.$emit('SHOW_MESSAGE', {code: 'B009', detail: '物件IDが間違っています。'}, () => {
        utils.router.push({path: '/'})
      })
    }
  }
}

export default new EstateRentPieceService()



