<template>
<div class="component-wrapper">
  <div class="info-title">
    <md-content class="title-bar md-primary"></md-content><p class="md-title">アクセス</p>
  </div>
  <shopCard :manager="manager" :shop="shop"/>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
import shopCard from '../shared/shopCard'
export default {
  components: {
    shopCard
  },
  props: ['manager', 'config'],
  computed: {
    shop () {
      if (!this.manager.homepageService.homepage.shops || !this.manager.homepageService.homepage.shops[0]) return this.manager.homepageService.homepage.profile
      else return this.manager.homepageService.homepage.shops[0]
    }
  }
}
</script>

<style scoped lang="scss">
.info-title {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  margin-bottom: 10px;
}
.title-bar {
  width: 5px;
  height: 30px;
  border-radius: 1px;
  margin-right: 10px;
}
</style>



