<template>
<div>
  <div class="md-layout md-gutter">
    <div class="md-layout-item md-size-50 md-small-size-100">
      <md-field>
        <gmap-autocomplete ref="searchPlace" class="md-input search-place-input" @place_changed="setPlace" :selectFirstOnEnter="true" placeholder="場所で緯度と経度を特定">
        </gmap-autocomplete>
        <!-- <md-button class="md-icon-button search-place" @click="usePlace"><md-icon>search</md-icon></md-button> -->
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-small-size-50">
      <md-field :class="{'md-invalid': !isLatitudeValid}">
        <label for="name">緯度</label>
        <md-input v-model="latitude" type="number" step="0.001"></md-input>
        <span class="md-error">-90〜90の数値を入力してください</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-small-size-50">
      <md-field :class="{'md-invalid': !isLongitudeValid}">
        <label for="name">経度</label>
        <md-input v-model="longitude" type="number" step="0.001"></md-input>
        <span class="md-error">-180〜180の数値を入力してください</span>
      </md-field>
    </div>
  </div>

  <div class="map-container">
    <div v-if="!enabled && prevent" class="overlayer" @touchmove.prevent @scroll.prevent
      @click="validMap"
      @mouseover="showTip"
      @mouseout="hideTip"
      @touchstart="showTip"
      @touchend="hideTip">
      <div v-if="tip" class="text-bg">
        <p class="md-title text-center">地図を操作するには、一度タップしてください</p>
      </div>
    </div>
    <gmap-map class="gmap-inner" :center="center" :zoom="14">
      <gmap-marker
        :key="index"
        v-for="(m, index) in markers"
        :position="m.position"
        :clickable="false"
        :draggable="true"
        @dragend="drag($event)"
      ></gmap-marker>
    </gmap-map>
  </div>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
// import manager from '@/store/manager.js'
export default {
  props: {
    manager: {
      default: 'manager'
    },
    lat: {
      type: Number,
      default: null
    },
    lng: {
      type: Number,
      default: null
    },
    prevent: {
      type: Boolean,
      default: true
    }
  },
  data: () => ({
    enabled: false,
    tip: false,
    init: {lat: 35.7090259, lng: 139.73199249999993},
    center: {lat: null, lng: null},
    markers: [],
    place: null,
    latitude: '',
    longitude: ''
  }),
  created () {
    this.center.lat = this.init.lat
    this.center.lng = this.init.lng
    this.markers.splice(0, this.markers.length)
    this.markers.push({
      position: {
        lat: this.init.lat,
        lng: this.init.lng
      }
    })
    if (this.lat != null && this.lng != null) {
      this.latitude = this.lat
      this.longitude = this.lng
    } else {
      this.latitude = this.init.lat
      this.longitude = this.init.lng
    }
  },
  watch: {
    latitude () {
      this.markers[0].position.lat = parseFloat(this.latitude)
      this.center.lat = parseFloat(this.latitude)
    },
    longitude () {
      this.markers[0].position.lng = parseFloat(this.longitude)
      this.center.lng = parseFloat(this.longitude)
    }
  },
  computed: {
    isLatitudeValid () {
      if (parseFloat(this.latitude) < -90 || parseFloat(this.latitude) > 90) return false
      return true
    },
    isLongitudeValid () {
      if (parseFloat(this.longitude) < -180 || parseFloat(this.longitude) > 180) return false
      return true
    }
  },
  methods: {
    setPlace (place) {
      this.place = place
      this.usePlace()
    },
    usePlace () {
      if (this.place && this.place.geometry && this.place.geometry.location) {
        this.latitude = this.place.geometry.location.lat()
        this.longitude = this.place.geometry.location.lng()
        this.place = null
        this.$refs.searchPlace.$el.value = ''
      }
    },
    drag (event) {
      this.latitude = event.latLng.lat()
      this.longitude = event.latLng.lng()
    },
    validMap () {
      this.enabled = true
    },
    showTip () {
      this.tip = true
    },
    hideTip () {
      this.tip = false
    },
    validate () {
      if (!this.isLatitudeValid || !this.isLongitudeValid) {
        return null
      }
      if (parseFloat(this.latitude) !== this.init.lat || parseFloat(this.longitude) !== this.init.lng) {
        return {
          lat: parseFloat(this.latitude),
          lng: parseFloat(this.longitude)
        }
      }
      return null
    }
  }
}
</script>

<style scoped lang="scss">
// .search-place {
//   position: absolute;
//   top: 10px;
//   right: 0px;
// }
.search-place-input {
  width: 100%;
}
.map-container {
  position: relative;
  width: 100%;
  height: 400px;
}
.gmap-inner {
  width: 100%;
  height: 100%;
}
.overlayer {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
}
.text-bg {
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;

  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.2);
}
.text-center {
  width: 100%;
  padding: 50px 40px 0px 40px;
  text-align: center;
}

$md-input-height: 32px;
$md-transition-stand-timing: cubic-bezier(.25, .8, .25, 1);
$md-transition-stand-duration: .4s;
$md-transition-stand: $md-transition-stand-duration $md-transition-stand-timing;

.search-place-input {
  font-size: 16px !important;
  height: $md-input-height;
  padding: 0;
  display: block;
  flex: 1;
  border: none;
  background: none;
  transition: $md-transition-stand;
  transition-property: font-size, padding-top, color;
  font-family: inherit;
  font-size: 1px;
  line-height: $md-input-height;

  &[disabled] {
    cursor: default;
  }

  &:focus {
    outline: none;
  }

  &::-webkit-input-placeholder {
    font-size: 16px;
    text-shadow: none;
    -webkit-text-fill-color: initial;
    transition: $md-transition-stand;
    transition-property: font-size, color;
  }
}
</style>



