<template>
<div class="service-wrapper">
  <md-card :class="cardClass">
    <md-card-header>
      <md-card-header-text>
        <div class="md-title">{{object.title}}</div>
        <div class="md-subhead">{{object.description}}</div>
      </md-card-header-text>

      <md-card-media>
        <md-icon class="md-accent">{{object.icon}}</md-icon>
      </md-card-media>
    </md-card-header>
  </md-card>

  <md-button md-theme="default" class="md-fab md-mini md-plain md-primary add-service" @click="addService()" v-if="!edit">
    <md-icon>add</md-icon>
  </md-button>
  <md-button md-theme="default" class="md-fab md-mini md-plain md-primary delete-service" @click="deleteService()" v-if="edit">
    <md-icon>close</md-icon>
  </md-button>
  <md-button md-theme="default" class="md-fab md-mini md-plain md-primary edit-service" @click="editService = true" v-if="edit">
    <md-icon>edit</md-icon>
  </md-button>
  <md-dialog-prompt
    md-theme="default"
    :md-active.sync="editService"
    v-model="object.description"
    md-title="サービス内容を入力してください。"
    md-input-maxlength="100"
    md-input-placeholder="Type..."
    md-confirm-text="確定"
    md-cancel-text="キャンセル"
  />
</div>
</template>

<script>
// import manager from '@/store/manager.js'
export default {
  props: {
    manager: {
      default: 'manager'
    },
    object: {
      default: 'object'
    },
    edit: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    cardClass () {
      if (!this.manager.homepageService.theme || this.manager.homepageService.theme === 'default') return 'md-primary'
      return this.manager.homepageService.theme + '-primary-gradient'
    }
  },
  data: () => ({
    editService: false
  }),
  methods: {
    addService () {
      if (!this.manager.homepageService.homepage.services) this.manager.homepageService.homepage.services = []
      this.manager.homepageService.homepage.services.push(this.object)
    },
    deleteService () {
      for (let i = 0; i <= this.manager.homepageService.homepage.services.length; i++) {
        let service = this.manager.homepageService.homepage.services[i]
        if (service.title === this.object.title) {
          this.manager.homepageService.homepage.services.splice(i, 1)
          break
        }
      }
    }
  }
}
</script>

<style scoped lang="scss">
.service-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
}
.md-card {
  height: 100%;
}
.md-card-media {
  .md-icon {
    width: 80px;
    height: 80px;
    font-size: 60px !important;
  }
}
.md-title {
  height: 32px;
}
.delete-service {
  position: absolute;
  top: 0;
  right: 16px;
}
.edit-service, .add-service {
  position: absolute;
  bottom: 0;
  right: 16px;
}
</style>



