<template>
<div class="cover">
  <img class="cover-pic" :src="config.photo">
  <div class="vision md-display-2" v-if="config.vision">
    <span>{{manager.homepageService.homepage.vision.title}}</span>
    <md-button md-theme="default" class="md-fab md-mini md-plain md-primary" @click="editTitle = true">
      <md-icon>edit</md-icon>
    </md-button>
  </div>
  <div class="vision md-headline" v-if="config.vision">
    <span>{{manager.homepageService.homepage.vision.description}}</span>
    <md-button md-theme="default" class="md-fab md-mini md-plain md-primary" @click="editDescription = true">
      <md-icon>edit</md-icon>
    </md-button>
  </div>

  <md-switch md-theme="default" v-model="config.vision" class="md-primary vision-switch">タイトル表示</md-switch>
  <uploadFile class="photo-uploader" ref="photoUploader" :manager="manager" acceptType="image" :subFolder="manager.user._id + '/resources'" :next="uploaded"/>

  <md-dialog-prompt
    md-theme="default"
    :md-active.sync="editTitle"
    v-model="manager.homepageService.homepage.vision.title"
    md-title="タイトルを入力してください。"
    md-input-maxlength="10"
    md-input-placeholder="Type..."
    md-confirm-text="確定"
    md-cancel-text="キャンセル"
  />
  <md-dialog-prompt
    md-theme="default"
    :md-active.sync="editDescription"
    v-model="manager.homepageService.homepage.vision.description"
    md-title="テキストを入力してください。"
    md-input-maxlength="30"
    md-input-placeholder="Type..."
    md-confirm-text="確定"
    md-cancel-text="キャンセル"
  />
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
import uploadFile from '@/components/common/uploadFile'
export default {
  props: ['manager', 'config'],
  components: {
    uploadFile
  },
  data: () => ({
    editTitle: false,
    editDescription: false
  }),
  methods: {
    uploaded (photo) {
      this.config.photo = photo.file
    }
  }
}
</script>

<style scoped lang="scss">
.cover {
  position: relative;
  width: 100%;
  height: 400px;
  display: flex;
  justify-content: center;
  .cover-pic {
    width: 100%;
    height: 400px;
    object-fit: cover;
    filter: brightness(70%);
  }
}
.cover::after {
  position: absolute;
  content: '';
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 0 -15px 15px -5px #fff;
}
.vision {
  position: absolute;
  padding: 0 60px;
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #fff;
  span {
    width: calc(100% - 56px);
  }
}
.md-display-2 {
  top: 70px;
}
.md-headline {
  top: 220px;
}
.vision-switch {
  position: absolute;
  top: 0px;
  left: 16px;
  z-index: 4;
  color: #fff;
}
.photo-uploader {
  position: absolute;
  bottom: 0px;
  right: 24px;
}
</style>



