<template>
<div>
  <p class="md-title">基本情報</p>
  <div class="md-layout md-gutter">
    <div class="md-layout-item md-size-20 md-small-size-30 md-xsmall-size-55">
      <md-radio v-model="manager.myRentPieceService.rentPiece.type" value="1">{{manager.const.typeRent[1]}}</md-radio>
    </div>
    <div class="md-layout-item md-size-20 md-small-size-30 md-xsmall-size-45">
      <md-radio v-model="manager.myRentPieceService.rentPiece.type" value="3">{{manager.const.typeRent[3]}}</md-radio>
    </div>
    <div class="md-layout-item md-size-20 md-small-size-30 md-xsmall-size-50">
      <md-radio v-model="manager.myRentPieceService.rentPiece.type" value="2">{{manager.const.typeRent[2]}}</md-radio>
    </div>
  </div>
  <div class="md-layout md-gutter md-alignment-left">
    <div class="md-layout-item md-size-33 md-small-size-100">
      <md-field md-clearable :class="{'md-invalid': !isNameValid}">
        <label for="name">タイトル</label>
        <md-input v-model="manager.myRentPieceService.rentPiece.name" required></md-input>
        <span class="md-error" v-if="!manager.myRentPieceService.rentPiece.name">入力してください</span>
        <span class="md-error" v-else>20文字以内</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-66 md-small-size-100">
      <md-field md-clearable :class="{'md-invalid': !isAddressValid}">
        <label for="address">アドレス</label>
        <md-input v-model="manager.myRentPieceService.rentPiece.address" required></md-input>
        <span class="md-error" v-if="!manager.myRentPieceService.rentPiece.address">入力してください</span>
        <span class="md-error" v-else>60文字以内</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100">
      <md-field :class="{'md-invalid': !isRentValid}">
        <label>賃料</label>
        <md-input v-model="manager.myRentPieceService.rentPiece.rent" type="number" required></md-input>
        <span class="md-helper-text">{{manager.myRentPieceService.rentPiece.rentText}}</span>
        <span class="md-error">500万以内の正数を入力してください</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100">
      <md-field :class="{'md-invalid': !isDepositValid}">
        <label>敷金</label>
        <md-input v-model="manager.myRentPieceService.rentPiece.deposit" type="number"></md-input>
        <span class="md-helper-text">{{manager.myRentPieceService.rentPiece.depositText}}</span>
        <span class="md-error">99以内の正数を入力してください</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100">
      <md-field :class="{'md-invalid': !isKeyMoneyValid}">
        <label>礼金</label>
        <md-input v-model="manager.myRentPieceService.rentPiece.keyMoney" type="number"></md-input>
        <span class="md-helper-text">{{manager.myRentPieceService.rentPiece.keyMoneyText}}</span>
        <span class="md-error">99以内の正数を入力してください</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100">
      <md-field :class="{'md-invalid': !isUpdateMoneyValid}">
        <label>更新料</label>
        <md-input v-model="manager.myRentPieceService.rentPiece.updateMoney" type="number"></md-input>
        <span class="md-helper-text">{{manager.myRentPieceService.rentPiece.updateMoneyText}}</span>
        <span class="md-error">99以内の正数を入力してください</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100">
      <md-field :class="{'md-invalid': !isInsuranceValid}">
        <label>保険など</label>
        <md-input v-model="manager.myRentPieceService.rentPiece.insurance"></md-input>
        <span class="md-error" v-if="!manager.myRentPieceService.rentPiece.insurance">入力してください</span>
        <span class="md-error">20文字以内</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100">
      <md-field :class="{'md-invalid': !isContractPeroidValid}">
        <label>契約期間</label>
        <md-input v-model="manager.myRentPieceService.rentPiece.contractPeroid"></md-input>
        <span class="md-error" v-if="!manager.myRentPieceService.rentPiece.contractPeroid">入力してください</span>
        <span class="md-error">20文字以内</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-xsmall-size-50" v-if="manager.myRentPieceService.rentPiece.isApartment">
      <md-field :class="{'md-invalid': !isManageValid}">
        <label>管理費</label>
        <md-input v-model="manager.myRentPieceService.rentPiece.manage" type="number"></md-input>
        <span class="md-helper-text">{{manager.myRentPieceService.rentPiece.manageText}}</span>
        <span class="md-error">1~999999の正数を入力してください</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-xsmall-size-50">
      <md-field>
        <label>間取り</label>
        <md-select v-model="manager.myRentPieceService.rentPiece.layout">
          <md-option value="">ー</md-option>
          <md-option :value="key" v-for="(layout, key) in manager.const.layout" :key="key">{{layout}}</md-option>
        </md-select>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-xsmall-size-50">
      <md-field :class="{'md-invalid': !isExclusiveAreaValid}">
        <label>専有面積</label>
        <md-input v-model="manager.myRentPieceService.rentPiece.exclusiveArea" type="number"></md-input>
        <span class="md-helper-text">{{manager.myRentPieceService.rentPiece.exclusiveAreaText}}</span>
        <span class="md-error">1~9999の正数を入力してください</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-xsmall-size-50" v-if="manager.myRentPieceService.rentPiece.isApartment">
      <md-field :class="{'md-invalid': !isFloorValid}">
        <label>所在階</label>
        <md-input v-model="manager.myRentPieceService.rentPiece.floor" type="number"></md-input>
        <span class="md-helper-text">{{manager.myRentPieceService.rentPiece.floorText}}</span>
        <span class="md-error">1~500の正数を入力してください</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-xsmall-size-50">
      <md-field :class="{'md-invalid': !isStoryValid}">
        <label>総階層</label>
        <md-input v-model="manager.myRentPieceService.rentPiece.story" type="number"></md-input>
        <span class="md-helper-text">{{manager.myRentPieceService.rentPiece.storyText}}</span>
        <span class="md-error">1~500の正数を入力してください</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-xsmall-size-50">
      <md-field>
        <label>構造</label>
        <md-select v-model="manager.myRentPieceService.rentPiece.structure">
          <md-option value="">ー</md-option>
          <md-option :value="key" v-for="(structure, key) in manager.const.structure" :key="key">{{structure}}</md-option>
        </md-select>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-xsmall-size-50" v-if="manager.myRentPieceService.rentPiece.isApartment">
      <md-field>
        <label>方位</label>
        <md-select v-model="manager.myRentPieceService.rentPiece.direction">
          <md-option value="">ー</md-option>
          <md-option :value="key" v-for="(direction, key) in manager.const.direction" :key="key">{{direction}}</md-option>
        </md-select>
      </md-field>
    </div>
    <div class="md-layout-item md-size-50 md-xsmall-size-100 input-group">
      <div class="input-left">
        <md-field :class="{'md-invalid': !isBuiltValid}">
          <label>築年月</label>
          <md-input v-model="built" type="text" maxlength="6"></md-input>
          <span class="md-helper-text">例：201802</span>
          <span class="md-error">年月を入力してください</span>
        </md-field>
      </div>
      <div class="input-right">
        <md-checkbox v-model="manager.myRentPieceService.rentPiece.isNew" class="md-primary">新築</md-checkbox>
      </div>
    </div>
    <div class="md-layout-item md-size-25 md-small-size-50 md-xsmall-size-100">
      <md-datepicker v-model="residentable">
        <label>入居可能日</label>
      </md-datepicker>
    </div>
    <div class="md-layout-item md-size-25 md-small-size-50 md-xsmall-size-100">
      <md-field>
        <label>現況</label>
        <md-select v-model="manager.myRentPieceService.rentPiece.available">
          <md-option value="">ー</md-option>
          <md-option :value="key" v-for="(available, key) in manager.const.availableRent" :key="key">{{available}}</md-option>
        </md-select>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-small-size-50 md-xsmall-size-100">
      <md-field>
        <label>取引態様</label>
        <md-select v-model="manager.myRentPieceService.rentPiece.aspect">
          <md-option value="">ー</md-option>
          <md-option :value="key" v-for="(aspect, key) in manager.const.aspect" :key="key">{{aspect}}</md-option>
        </md-select>
      </md-field>
    </div>
    <div class="md-layout-item md-size-25 md-small-size-50 md-xsmall-size-100">
      <md-field :class="{'md-invalid': !isMarginValid}">
        <label>仲介手数料</label>
        <md-input v-model="manager.myRentPieceService.rentPiece.margin"></md-input>
        <span class="md-error" v-if="!manager.myRentPieceService.rentPiece.margin">入力してください</span>
        <span class="md-error">20文字以内</span>
      </md-field>
    </div>
  </div>
  <p class="md-title">交通情報</p>
  <div class="md-layout md-gutter md-elevation-1 traffic" v-for="(station, index) in stations" :key="index">
    <div class="md-layout-item md-size-60 md-xsmall-size-100">
      <bdsMdChips v-model="station.station" :md-format="filterStation" :mdSelections="selections" :isSingleSelect="true" mdPlaceholder="最寄り駅"></bdsMdChips>
    </div>
    <div class="md-layout-item md-size-40 md-xsmall-size-100">
      <md-field :class="{'md-invalid': !isWalkingValid(station.walking)}">
        <label>徒歩時間</label>
        <md-input v-model="station.walking" type="number"></md-input>
        <span class="md-helper-text" v-if="station.walking">{{station.walking}}分</span>
        <span class="md-error">正数を入力してください</span>
      </md-field>
    </div>
    <div class="remove-button">
      <md-button class="md-primary md-icon-button md-raised" @click.stop="removeStation(index)">
        <md-icon>close</md-icon>
      </md-button>
    </div>
  </div>
  <div class="add-traffic">
    <md-button class="md-primary md-icon-button md-raised" @click.stop="addStation"><md-icon>add</md-icon></md-button>
  </div>
  <p class="md-title" v-if="manager.myRentPieceService.rentPiece.isApartment">設備</p>
  <div class="md-layout md-gutter md-alignment-left" v-if="manager.myRentPieceService.rentPiece.isApartment">
    <div class="md-layout-item md-size-20 md-medium-size-33 md-xsmall-size-50">
      <md-checkbox v-model="manager.myRentPieceService.rentPiece.carParking" class="md-primary">駐車場</md-checkbox>
    </div>
    <div class="md-layout-item md-size-20 md-medium-size-33 md-xsmall-size-50">
      <md-checkbox v-model="manager.myRentPieceService.rentPiece.bikeParking" class="md-primary">駐輪場</md-checkbox>
    </div>
    <div class="md-layout-item md-size-20 md-medium-size-33 md-xsmall-size-50">
      <md-checkbox v-model="manager.myRentPieceService.rentPiece.cityGas" class="md-primary">都市ガス</md-checkbox>
    </div>
    <div class="md-layout-item md-size-20 md-medium-size-33 md-xsmall-size-50">
      <md-checkbox v-model="manager.myRentPieceService.rentPiece.pet" class="md-primary">ペット可</md-checkbox>
    </div>
    <div class="md-layout-item md-size-20 md-medium-size-66 md-xsmall-size-100">
      <md-checkbox v-model="manager.myRentPieceService.rentPiece.balcony" class="md-primary">バルコニー</md-checkbox>
    </div>
  </div>
  <p class="md-title">備考</p>
  <div class="md-layout md-gutter">
    <div class="md-layout-item md-size-100">
      <md-field :class="{'md-invalid': !isCommentValid}">
        <md-input v-model="manager.myRentPieceService.rentPiece.comment"></md-input>
        <span class="md-error" v-if="!manager.myRentPieceService.rentPiece.comment">入力してください</span>
        <span class="md-error" v-else>60文字以内</span>
      </md-field>
    </div>
  </div>
  <p class="md-title">メモ</p>
  <div class="md-layout md-gutter">
    <div class="md-layout-item md-size-100">
      <md-field :class="{'md-invalid': !isMemoValid}">
        <md-input v-model="manager.myRentPieceService.rentPiece.memo"></md-input>
        <span class="md-error" v-if="!manager.myRentPieceService.rentPiece.memo">入力してください</span>
        <span class="md-error" v-else>60文字以内</span>
      </md-field>
    </div>
  </div>
</div>
</template>

<script>
import moment from 'moment'
// import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import bdsMdChips from '@/components/common/vue-material/bdsMdChips'
export default {
  props: ['manager'],
  components: {
    bdsMdChips
  },
  data: () => ({
    selections: [],
    stations: [],
    built: null,
    residentable: null
  }),
  created () {
    let that = this
    that.selections.splice(0, that.selections.length)
    for (let line in manager.const.station) {
      manager.const.station[line].forEach(station => {
        that.selections.push({value: line + '-' + station.sn, name: line + ' - ' + station.sn})
      })
    }
    manager.myRentPieceService.rentPiece.stations.forEach(one => {
      that.stations.push({station: [one.station], walking: one.walking})
    })
    this.addStation()
    this.built = manager.myRentPieceService.rentPiece.builtInputText
    this.residentable = manager.myRentPieceService.rentPiece._residentable ? manager.myRentPieceService.rentPiece._residentable.format('YYYY-MM-DD') : null
  },
  computed: {
    isNameValid () {
      if (manager.myRentPieceService.rentPiece.name === '') return false
      if (manager.myRentPieceService.rentPiece.name && manager.myRentPieceService.rentPiece.name.length > 20) return false
      return true
    },
    isAddressValid () {
      if (manager.myRentPieceService.rentPiece.address === '') return false
      if (manager.myRentPieceService.rentPiece.address && manager.myRentPieceService.rentPiece.address.length > 60) return false
      return true
    },
    isRentValid () {
      if (manager.myRentPieceService.rentPiece.rent === null) return true
      if (manager.myRentPieceService.rentPiece.rent <= 0 || manager.myRentPieceService.rentPiece.rent > 5000000) return false
      return true
    },
    isDepositValid () {
      if (manager.myRentPieceService.rentPiece.deposit === null) return true
      if (manager.myRentPieceService.rentPiece.deposit <= 0 || manager.myRentPieceService.rentPiece.deposit > 99) return false
      return true
    },
    isKeyMoneyValid () {
      if (manager.myRentPieceService.rentPiece.keyMoney === null) return true
      if (manager.myRentPieceService.rentPiece.keyMoney <= 0 || manager.myRentPieceService.rentPiece.keyMoney > 99) return false
      return true
    },
    isUpdateMoneyValid () {
      if (manager.myRentPieceService.rentPiece.updateMoney === null) return true
      if (manager.myRentPieceService.rentPiece.updateMoney <= 0 || manager.myRentPieceService.rentPiece.updateMoney > 99) return false
      return true
    },
    isInsuranceValid () {
      if (!manager.myRentPieceService.rentPiece.insurance) return true
      if (manager.myRentPieceService.rentPiece.insurance && manager.myRentPieceService.rentPiece.insurance.length > 20) return false
      return true
    },
    isContractPeroidValid () {
      if (!manager.myRentPieceService.rentPiece.contractPeroid) return true
      if (manager.myRentPieceService.rentPiece.contractPeroid && manager.myRentPieceService.rentPiece.contractPeroid.length > 20) return false
      return true
    },
    isStoryValid () {
      if (manager.myRentPieceService.rentPiece.story === null) return true
      if (manager.myRentPieceService.rentPiece.story <= 0 || manager.myRentPieceService.rentPiece.story > 500) return false
      return true
    },
    isFloorValid () {
      if (!manager.myRentPieceService.rentPiece.floor) return true
      if (manager.myRentPieceService.rentPiece.floor <= 0 || manager.myRentPieceService.rentPiece.floor > 500) return false
      return true
    },
    isExclusiveAreaValid () {
      if (!manager.myRentPieceService.rentPiece.exclusiveArea) return true
      if (manager.myRentPieceService.rentPiece.exclusiveArea <= 0 || manager.myRentPieceService.rentPiece.exclusiveArea > 9999) return false
      return true
    },
    isBuiltValid () {
      if (!this.built) return true
      if (this.built.length < 6) return false
      if (!moment(this.built, 'YYYYMM').isValid()) return false
      return true
    },
    isMarginValid () {
      if (!manager.myRentPieceService.rentPiece.margin) return true
      if (manager.myRentPieceService.rentPiece.margin && manager.myRentPieceService.rentPiece.margin.length > 20) return false
      return true
    },
    isManageValid () {
      if (!manager.myRentPieceService.rentPiece.manage) return true
      if (manager.myRentPieceService.rentPiece.manage <= 0 || manager.myRentPieceService.rentPiece.manage > 999999) return false
      return true
    },
    isCommentValid () {
      if (!manager.myRentPieceService.rentPiece.comment) return true
      if (manager.myRentPieceService.rentPiece.comment && manager.myRentPieceService.rentPiece.comment.length > 60) return false
      return true
    },
    isMemoValid () {
      if (!manager.myRentPieceService.rentPiece.memo) return true
      if (manager.myRentPieceService.rentPiece.memo && manager.myRentPieceService.rentPiece.memo.length > 60) return false
      return true
    }
  },
  methods: {
    isWalkingValid (walking) {
      if (!walking) return true
      if (parseInt(walking) <= 0) return false
      return true
    },
    addStation () {
      this.stations.push({station: [], walking: null})
    },
    removeStation (index) {
      this.stations.splice(index, 1)
    },
    filterStation (input) {
      if (!input) return
      let selection = null
      this.selections.some(one => {
        const isAdded = this.selections.some(station => {
          if (station === one.value) return true
        })
        if (!isAdded && one.name.indexOf(input) >= 0) {
          selection = one.value
          return true
        }
      })
      return selection
    },
    validate () {
      if (!manager.myRentPieceService.rentPiece.name) {
        return 'タイトルは必須入力項目です。'
      }
      if (!manager.myRentPieceService.rentPiece.address) {
        return 'アドレスは必須入力項目です。'
      }
      if (!manager.myRentPieceService.rentPiece.rent) {
        return '賃料は必須入力項目です。'
      }
      if (!this.isNameValid || !this.isAddressValid || !this.isRentValid || !this.isStoryValid || !this.isFloorValid || !this.isExclusiveAreaValid || !this.isBuiltValid || !this.isMarginValid || !this.isCommentValid || !this.isMemoValid || !this.isManageValid || !this.isDepositValid || !this.isKeyMoneyValid || !this.isUpdateMoneyValid || !this.isInsuranceValid || !this.isContractPeroidValid) {
        return '入力不正です。'
      }
      let stations = []
      for (let i = this.stations.length - 1; i >= 0; i--) {
        let one = this.stations[i]
        if (!one.station || one.station.length <= 0 || !one.walking) {
          this.stations.splice(i, 1)
        } else {
          stations.push({
            station: one.station[0],
            walking: parseInt(one.walking)
          })
        }
      }
      manager.myRentPieceService.rentPiece.setStations(stations)
      if (this.built !== null && this.built !== '') manager.myRentPieceService.rentPiece.built = this.built + '01'
      else manager.myRentPieceService.rentPiece.built = null
      manager.myRentPieceService.rentPiece.residentable = this.residentable
      // TODO: md bug
      if (manager.myRentPieceService.rentPiece._residentable && manager.myRentPieceService.rentPiece._residentable.year() === 1970) manager.myRentPieceService.rentPiece.residentable = null
      return null
    }
  }
}
</script>

<style scoped lang="scss">
.input-group {
  display: flex;
}
.input-left {
  width: calc(100% - 100px);
}
.input-right {
  width: 100px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.traffic {
  position: relative;
  padding: 10px;
  margin-bottom: 10px;
}
.remove-button {
  position: absolute;
  top: -5px;
  right: 0px;
}
.add-traffic {
  margin-bottom: 30px;
  display: flex;
  justify-content: center;
}
</style>



