<template>
<md-card md-with-hover>
  <md-card-media class="ribbon-box">
    <div class="ribbon-area" v-if="object.isPublishing">
      <span class="ribbon primary-gradient primary-active">公開中</span>
    </div>
    <img class="piece-thumbnail" :src="object.photoThumbnails[0]" v-if="object.photoThumbnails && object.photoThumbnails.length > 0" @click="viewPiece(object)">
    <img class="piece-thumbnail" src="@/assets/noimage.png" v-else @click="viewPiece(object)">
    <div class="piece-actions">
      <md-menu md-size="big" md-direction="bottom-end">
        <md-button class="md-icon-button md-accent md-raised" md-menu-trigger>
          <md-icon>more_vert</md-icon>
        </md-button>
        <md-menu-content>
          <md-menu-item @click="viewPiece(object)">
            <span>詳細</span>
          </md-menu-item>
          <md-menu-item @click="publishPiece(object)" v-if="!object.isPublishing">
            <span>HPへ公開</span>
          </md-menu-item>
          <md-menu-item @click="unPublishPiece(object)" v-else>
            <span>HPから取下</span>
          </md-menu-item>
          <md-menu-item @click="editPiece(object)">
            <span>編集</span>
          </md-menu-item>
          <md-menu-item @click="deletePiece(object)">
            <span>削除</span>
          </md-menu-item>
        </md-menu-content>
      </md-menu>
    </div>
  </md-card-media>

  <md-card-header>
    <div class="md-title">{{object.name}}</div>
    <div class="md-subhead" v-if="object.stations && object.stations.length > 0">
      {{object.stations[0].station}} 徒歩{{object.stations[0].walking}}分
    </div>
    <div class="md-subhead" v-else></div>
  </md-card-header>

  <md-card-content>
    <md-content class="badge" @click="tooltipActive1 = !tooltipActive1">
      {{object.priceText}}
      <md-tooltip md-direction="top" :md-active.sync="tooltipActive1">{{object.priceText}}</md-tooltip>
    </md-content>
    <md-content class="badge" v-if="object.layout" @click="tooltipActive2 = !tooltipActive2">
      {{object.layoutText}}
      <md-tooltip md-direction="top" :md-active.sync="tooltipActive2">{{object.layoutText}}</md-tooltip>
    </md-content>
    <md-content class="badge muted" v-else @click="tooltipActive2 = !tooltipActive2">
      ー
      <md-tooltip md-direction="top" :md-active.sync="tooltipActive2">間取り</md-tooltip>
    </md-content>
    <md-content class="badge" v-if="object.isApartment && object.exclusiveArea" @click="tooltipActive3 = !tooltipActive3">
      {{object.exclusiveAreaText}}
      <md-tooltip md-direction="top" :md-active.sync="tooltipActive3">専有：{{object.exclusiveAreaText}}</md-tooltip>
    </md-content>
    <md-content class="badge" v-else-if="object.isDetached && (object.buildingArea || object.landArea)" @click="tooltipActive3 = !tooltipActive3">
      <span v-if="object.buildingArea">{{object.buildingAreaText}}</span>
      <span v-else>{{object.landAreaText}}</span>
      <md-tooltip md-direction="top" :md-active.sync="tooltipActive3"><span v-if="object.buildingArea">建築：{{object.buildingAreaText}}</span> <span v-if="object.landArea">土地：{{object.landAreaText}}</span></md-tooltip>
    </md-content>
    <md-content class="badge muted" v-else @click="tooltipActive3 = !tooltipActive3">
      ー
      <md-tooltip md-direction="top" :md-active.sync="tooltipActive3">面積</md-tooltip>
    </md-content>
  </md-card-content>
</md-card>
</template>

<script>
import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
export default {
  props: ['manager', 'object'],
  data: () => ({
    tooltipActive1: false,
    tooltipActive2: false,
    tooltipActive3: false
  }),
  methods: {
    publishPiece (piece) {
      manager.mySellPieceService.publishSellPiece(piece, () => {
        manager.mySellPieceService.loadSellPieces(null, null, () => {
          utils.event.$emit('SCROLL_TO_TOP')
        })
      })
    },
    unPublishPiece (piece) {
      manager.mySellPieceService.unPublishSellPiece(piece, () => {
        manager.mySellPieceService.loadSellPieces(null, null, null)
      })
    },
    viewPiece (piece) {
      if (piece.id) {
        let url = location.protocol + '//' + location.host + '/my/sellPieceDetail/' + piece._id
        utils.openInNewTab(url)
      }
    },
    editPiece (piece) {
      if (piece.id) this.$router.push({path: '/my/sellPiece/' + piece.id})
    },
    deletePiece (piece) {
      let that = this
      if (piece.id) {
        utils.event.$emit('SHOW_MESSAGE', {code: 'I003'}, () => {
          that.manager.mySellPieceService.deleteSellPiece({_id: piece.id})
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
.md-card {
  margin: 0px;
  height: 100%;
}

.ribbon-box {
  position: relative;
  box-sizing: border-box;
}
.ribbon-area {
  position: absolute;
  top: -6px;
  right: -6px;
  width: 89px;
  height: 91px;
  overflow: hidden;
  pointer-events: none;
}
.ribbon {
  display: inline-block;
  position: absolute;
  padding: 7px 0;
  left: -23px;
  top: 22px;
  width: 160px;
  text-align: center;
  font-size: 18px;
  line-height: 16px;
  color: #fff;
  letter-spacing: 0.05em;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}
.ribbon:before, .ribbon:after {
  position: absolute;
  content: "";
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  bottom: -4px;
}
.ribbon:before {
  left: 14px;
}
.ribbon:after {
  right: 18px;
}

.piece-thumbnail {
  cursor: pointer;
  width: 100%;
  height: 200px;
  object-fit: cover;
}
.md-title {
  height: 32px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.md-subhead {
  height: 20px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.md-card-content {
  display: flex;
  justify-content: flex-start;
}
.badge {
  background: #e8e8e8;
  padding: 8px;
  height: 32px;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 3px;
  white-space: nowrap;
  overflow: hidden;
}
.muted {
  color: #aaa !important;
}
.piece-actions {
  position: absolute;
  bottom: 0px;
  right: 0px;
  padding: 5px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
</style>



