<template>
<div class="app-content-auto-height" v-if="manager.estateRentPieceService.rentPiece">
  <vue-headful title="ブドウさん - 物件詳細"/>
  <md-button class="md-icon-button md-fab md-raised md-accent share" @click="share"><md-icon>share</md-icon></md-button>
  <md-button class="md-icon-button md-fab md-raised md-accent contact-info" @click="showContact"><md-icon>account_box</md-icon></md-button>
  <rentPieceDetailInfo :manager="manager" :piece="manager.estateRentPieceService.rentPiece"/>
</div>
</template>

<script>
import manager from '@/store/manager.js'
import utils from '@/tool/utils.js'
import rentPieceDetailInfo from './rentPieceDetailInfo'
export default {
  props: ['manager'],
  components: {
    rentPieceDetailInfo
  },
  mounted () {
    utils.event.$on('OPEN_PIECE_DETAIL', (index) => {
      let url = location.protocol + '//' + location.host + '/rentPiecePreview/' + manager.estateRentPieceService.rentPiece._id + '/' + index
      utils.openInNewTab(url)
    })
  },
  beforeDestroy () {
    utils.event.$off('OPEN_PIECE_DETAIL')
    manager.estateRentPieceService.rentPiece = null
  },
  methods: {
    share () {
      utils.copyToPasteboard(location.protocol + '//' + location.host + '/rentPieceDetail/' + manager.estateRentPieceService.rentPiece._id)
      utils.event.$emit('SHOW_MESSAGE', {code: 'I008'})
    },
    showContact () {
      if (manager.estateRentPieceService.rentPiece.contact) utils.event.$emit('SHOW_DETAIL', 'contactCard', manager.estateRentPieceService.rentPiece.contact)
    }
  }
}
</script>

<style scoped lang="scss">
.share {
  position: absolute;
  top: 146px;
  right: 28px;
}
.contact-info {
  position: absolute;
  top: 210px;
  right: 28px;
}
</style>



