import Contact from './contact.js'
import manager from '@/store/manager.js'
import utils from '@/tool/utils.js'
// import moment from 'moment'

class BorrowRequest {
  constructor (data) {
    this._id = data._id
    this.isPublishing = data.isPublishing

    // リクエスト元
    this.contact = null
    if (data.contact) this.contact = new Contact(data.contact)

    this.contactID = null
    if (data.contactID) this.contactID = new Contact(data.contactID)

    // 建物種別（マンション・アパート・一戸建・その他）
    this._type = '1'
    if (data.type) this.type = data.type
    // 中古・新築
    this.isnew = ''
    if (data.isnew) this.isnew = data.isnew
    // 価格帯
    this._priceFrom = null
    if (data.priceFrom != null) this._priceFrom = data.priceFrom
    this._priceTo = null
    if (data.priceTo != null) this._priceTo = data.priceTo
    // 路線
    this.line = data.line || null
    // 駅
    this.station = data.station || null
    // 駅から所要時間
    this._requiredTime = null
    if (data.requiredTime != null) this._requiredTime = data.requiredTime
    // 入居希望日
    this._hopeDay = null
    if (data._hopeDay) this._hopeDay = data.hopeDay
    // 国籍
    this.nationality = data.nationality || null
    // 年齢代数
    this.generation = ''
    if (data.generation) this.generation = data.generation
    // 職業
    this.profession = data.profession || null
    // 説明
    this.comment = data.comment || null
  }

  get id () {
    return this._id
  }
  get isPublishing () {
    return !!this._isPublishing
  }
  set isPublishing (data) {
    this._isPublishing = !!data
  }
  get type () {
    return this._type
  }
  get typeText () {
    return this._type != null ? manager.const.type[this._type] : '-'
  }
  set type (data) {
    this._type = data
  }
  get isnewText () {
    if (this.isnew === '0') {
      return '特になし'
    } else if (this.isnew === '1') {
      return '新築'
    } else if (this.isnew === '2') {
      return '中古'
    }
  }
  get priceFrom () {
    return this._priceFrom == null ? null : parseInt(this._priceFrom)
  }
  get priceFromText () {
    return this._priceFrom != null ? utils.formatHugeMoney(this._priceFrom) : '-'
  }
  get estatePriceFromText () {
    return this._priceFrom != null ? this.priceFromText() : '下限なし'
  }
  set priceFrom (data) {
    this._priceFrom = parseInt(data)
  }
  get priceTo () {
    return this._priceTo == null ? null : parseInt(this._priceTo)
  }
  get priceToText () {
    return this._priceTo != null ? utils.formatHugeMoney(this._priceTo) : '-'
  }
  get estatePriceToText () {
    return this._priceFrom != null ? this.priceToText() : '上限なし'
  }
  set priceTo (data) {
    this._priceTo = parseInt(data)
  }
  // get requiredTimeFrom () {
  //   return this._requiredTimeFrom == null ? null : parseInt(this._requiredTimeFrom)
  // }
  // set requiredTimeFrom (data) {
  //   this._requiredTimeFrom = parseInt(data)
  // }
  // get requiredTimeTo () {
  //   return this._requiredTimeTo == null ? null : parseInt(this._requiredTimeTo)
  // }
  // set requiredTimeTo (data) {
  //   this._requiredTimeTo = parseInt(data)
  // }
  // get hopeDay () {
  //   return this._hopeDay ? this._hopeDay.valueOf() : null
  // }
  get requiredTime () {
    return this._requiredTime == null ? null : parseInt(this._requiredTime)
  }
  set requiredTime (data) {
    this._requiredTime = parseInt(data)
  }

  toJSON () {
    return {
      _id: this.id,
      contactID: this.contactID,
      type: this._type,
      isnew: this.isnew,
      priceFrom: this.priceFrom,
      priceTo: this.priceTo,
      line: this.line,
      stations: this.stations,
      requiredTime: this.requiredTime,
      nationality: this.nationality,
      generation: this.generation,
      profession: this.profession,
      comment: this.comment,
      isPublishing: this._isPublishing
    }
  }
}

export default BorrowRequest



